import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import RemarkComponent from '../../../RemarkContainer/RemarkComponent';
import { Popper } from 'components';
import { Remark as RemarkIcon } from 'components/Icons';
import { __DEV__ } from '../../../../common/constants';

const iconNodeGap = {
  marginLeft: '6px',
  display: 'inline-block',
};

const stopEvent = event => event.stopPropagation();
export default class RemarkItem extends PureComponent {
  state = {
    open: false,
    edit: false,
  };

  containerRef = React.createRef();

  componentDidMount() {
    this.containerRef.current.addEventListener('mouseleave', this.handleMouseLeave);
    this.containerRef.current.addEventListener('mouseenter', this.handleMouseEnter);
  }

  componentWillUnmount() {
    this.containerRef.current.removeEventListener('mouseleave', this.handleMouseLeave);
    this.containerRef.current.removeEventListener('mouseenter', this.handleMouseEnter);
  }

  handleBlur = () => {
    this.setHidden();
  };

  saveRemark = idAndRemark => {
    const { remark } = this.props;
    // 如果从 "" => "value", forceUpdate
    // 如果是 "value" => "", forceUpdate
    if (remark === idAndRemark.value) {
      __DEV__ && console.log('ignore save remark');
      return;
    }
    mindmapEditor.saveRemark(idAndRemark);
  };

  setHidden = () => {
    this.shouldShow = false;
    this.toggleShow();
  };

  handleMouseEnter = event => {
    this.shouldShow = true;
    if (mindmapEditor.__uistatus__.focus) return;
    if (this.state.open || this.state.edit) return;
    if (this.leaveTimeout) return;
    this.enterTimeout = setTimeout(() => this.toggleShow(event), 500);
  };

  handleMouseLeave = () => {
    this.shouldShow = false;
    if (mindmapEditor.__uistatus__.focus) return;
    if (this.state.edit) return;
    if (this.enterTimeout) return;
    this.leaveTimeout = setTimeout(() => this.toggleShow(), 500);
  };

  resetStatus = () => {
    this.setState({
      open: false,
      edit: false,
    });
  };

  /**
   * 如果不传 event 表示隐藏弹窗
   */
  toggleShow = event => {
    clearTimeout(this.leaveTimeout);
    clearTimeout(this.enterTimeout);
    this.leaveTimeout = null;
    this.enterTimeout = null;

    if (event && this.shouldShow) {
      this.setState({
        open: true,
      });
      this.triggerRender(event.target);
      return;
    }
    this.resetStatus();
    this.triggerUnmount();
  };

  triggerRender = anchorEl => {
    const { remark, id, readOnly } = this.props;
    if (!remark) return;
    mindmapEditor.emit('controller:render:component', {
      component: (
        <Popper open={true} anchorEl={anchorEl} placement="bottom" style={{ zIndex: 1000 }}>
          <div
            onMouseEnter={this.remarkMouseEnter}
            onMouseLeave={this.remarkMouseLeave}
            style={{ marginTop: -20, paddingTop: 20 }}
          >
            <RemarkComponent
              saveRemark={this.saveRemark}
              id={id}
              value={remark}
              noTitle={true}
              onDoubleClick={stopEvent}
              readOnly={readOnly}
              onClick={this.handleTextClick}
              onBlur={this.handleBlur}
            />
          </div>
        </Popper>
      ),
      triggerElement: anchorEl,
    });
  };

  triggerUnmount = () => {
    mindmapEditor.emit('controller:unmount:component');
  };

  handleTextClick = () => {
    if (this.props.readOnly) return;
    mindmapEditor.__uistatus__.focus = 'remark';
    this.setState({
      edit: true,
    });
  };

  remarkMouseEnter = () => {
    this.shouldShow = true;
    mindmapEditor.__uistatus__.focus = 'remark';
  };

  remarkMouseLeave = () => {
    if (this.state.edit) return;
    this.setHidden();
  };

  render() {
    return (
      <div
        ref={this.containerRef}
        onDoubleClick={stopEvent}
        style={{ ...this.props.style, ...iconNodeGap }}
      >
        <WithEnterIcon />
      </div>
    );
  }
}
RemarkItem.propTypes = {
  remark: PropTypes.string,
  id: PropTypes.string,
  readOnly: PropTypes.bool,
  style: PropTypes.object,
};

const WithEnterIcon = () => <EnterLeave render={({ hover }) => <RemarkIcon hover={hover} />} />;

class EnterLeave extends PureComponent {
  state = {
    hover: false,
  };
  onMouseEnter = event => {
    this.setState({
      hover: true,
    });
    event.stopPropagation();
  };

  onMouseLeave = event => {
    this.setState({
      hover: false,
    });
    event.stopPropagation();
  };

  render() {
    const defaultEvents = {
      onMouseEnter: this.onMouseEnter,
      onMouseLeave: this.onMouseLeave,
    };
    return (
      <div style={{ height: '100%' }} {...defaultEvents}>
        {this.props.render(this.state)}
      </div>
    );
  }
}
EnterLeave.propTypes = {
  render: PropTypes.func,
};
