import PropTypes from 'prop-types';
import React from 'react';
import { findDOMNode, createPortal } from 'react-dom';

function getContainer(container, defaultContainer) {
  container = typeof container === 'function' ? container() : container;

  return findDOMNode(container) || defaultContainer;
}

function ownerDocument(node) {
  return (node && node.ownerDocument) || document;
}

function getOwnerDocument(element) {
  return ownerDocument(findDOMNode(element));
}

export default class Portal extends React.Component {
  componentDidMount() {
    this.setMountNode(this.props.container);

    if (!this.props.disabledProtal) {
      this.forceUpdate(this.props.onRendered);
    }
  }

  componentDidUpdate(prevProps) {
    if (
      prevProps.container !== this.props.container ||
      prevProps.disabledProtal !== this.props.disabledProtal
    ) {
      this.setMountNode(this.props.container);

      if (!this.props.disabledProtal) {
        this.forceUpdate(this.props.onRendered);
      }
    }
  }

  setMountNode(container) {
    if (this.props.disabledProtal) {
      this.mountNode = findDOMNode(this).parentElement;
      return;
    }

    this.mountNode = getContainer(container, getOwnerDocument(this).body);
  }

  getMountNode = () => {
    return this.mountNode;
  };

  componentWillUnmount() {
    this.mountNode = null;
  }

  render() {
    const { children, disabledProtal } = this.props;
    if (disabledProtal) {
      return children;
    }

    return this.mountNode ? createPortal(children, this.mountNode) : null;
  }
}

Portal.propTypes = {
  children: PropTypes.node.isRequired,
  container: PropTypes.oneOfType([PropTypes.object, PropTypes.func]),
  disabledProtal: PropTypes.bool,
  onRendered: PropTypes.func,
};

Portal.defaultProps = {
  disabledProtal: false,
};
