import { YMArea, RuleArea, ChildrenArea } from './YMArea';
import _ from 'lodash';

class YMNode {
  constructor(source, parent = null, children = []) {
    const self = this;
    self.parent = parent;
    self.children = children;
    self.source = source;
    self.id = source.id;
    self.order = source.order;
    self.index = self.parent ? self.parent.index + 1 : 0;
    self.selfArea = new YMArea({ container: source.id });
    self.ruleArea = new RuleArea({ container: source.id });
    self.childrenArea = new ChildrenArea();

    self._style =
      source.style && !_.isEmpty(source.style)
        ? {
            left: parseInt(source.style.left, 10) || 0,
            top: parseInt(source.style.top, 10) || 0,
            visibility: source.style.visibility,
          }
        : {
            left: 0,
            top: 0,
          };

    self.position = {};

    Object.defineProperties(self.position, {
      start: {
        get() {
          return {
            x: self._style.left,
            y: self._style.top,
          };
        },
      },
      middleTop: {
        get() {
          return {
            x: self._style.left + self.selfArea.width / 2,
            y: self._style.top,
          };
        },
      },
      rightTop: {
        get() {
          return {
            x: self._style.left + self.selfArea.width,
            y: self._style.top,
          };
        },
      },
      middleRight: {
        get() {
          return {
            x: self._style.left + self.selfArea.width,
            y: self._style.top + self.selfArea.height / 2,
          };
        },
      },
      end: {
        get() {
          return {
            x: self._style.left + self.selfArea.width,
            y: self._style.top + self.selfArea.height,
          };
        },
      },
      middleBottom: {
        get() {
          return {
            x: self._style.left + self.selfArea.width / 2,
            y: self._style.top + self.selfArea.height,
          };
        },
      },
      leftBottom: {
        get() {
          return {
            x: self._style.left,
            y: self._style.top + self.selfArea.height,
          };
        },
      },
      middleLeft: {
        get() {
          return {
            x: self._style.left,
            y: self._style.top + self.selfArea.height / 2,
          };
        },
      },
      center: {
        get() {
          return {
            x: self._style.left + self.selfArea.width / 2,
            y: self._style.top + self.selfArea.height / 2,
          };
        },
      },
    });

    self.expanded = source.expanded == null ? true : source.expanded;

    self._expanderStyle =
      source.expanderStyle && !_.isEmpty(source.expanderStyle)
        ? Object.assign({}, source.expanderStyle)
        : {
            left: 0,
            top: 0,
            bottom: 0,
            right: 0,
            visibility: 'hidden',
          };
  }

  doLayout() {
    const updateSelfAreaWithRule = () => {
      this.ruleArea.width = this.selfArea.width;
      this.ruleArea.height = this.selfArea.height;
      this.selfArea.start.x = 0;
      this.selfArea.start.y = 0;
    };
    if (!this.children.length) {
      updateSelfAreaWithRule();
      return;
    }

    if (this.expanded) {
      // 布局childrenArea，即计算child的ruleArea的起点
      this.doStrategy(this.childrenArea);

      // 计算childrenArea宽高
      this.getChildrenAreaWH();

      // 布局ruleArea，即计算childrenArea的起点
      this.doStrategy(this.ruleArea);

      // 计算ruleArea宽高
      this.getRuleAreaWH();

      // 计算selfArea的起点
      this.getSelfAreaStart();
    } else {
      updateSelfAreaWithRule();
    }
  }

  isRoot() {
    return this instanceof YMNode && this.parent === null && this.id === 'root';
  }

  isFirst() {
    return this.parent ? this.parent.children.indexOf(this) === 0 : null;
  }

  isLast() {
    return this.parent
      ? this.parent.children.indexOf(this) === this.parent.children.length - 1
      : null;
  }

  isFirstOrLast() {
    return this.isFirst() || this.isLast();
  }

  getDescendantLength() {
    let count = 0;
    const getChildLength = children => {
      if (children) {
        count += children.length;
        children.forEach(item => {
          getChildLength(item.children);
        });
      }
    };
    getChildLength(this.children);
    return count;
  }

  isHidden() {
    return (
      this.style.visibility === 'hidden' ||
      this.source.visibility === 'hidden' ||
      (this.parent && this.parent.expanded === false)
    );
  }

  hasChild() {
    return this.children.length > 0;
  }

  doStrategy(area) {
    area.strategy && area.strategy(this);
  }

  getRuleAreaWH() {
    this.ruleArea.width = Math.max(
      this.childrenArea.end.x - this.childrenArea.start.x,
      this.childrenArea.end.x - 0,
      this.selfArea.width - this.childrenArea.start.x,
      this.selfArea.width - 0,
    );
    this.ruleArea.height = Math.max(
      this.childrenArea.end.y - this.childrenArea.start.y,
      this.childrenArea.end.y - 0,
      this.selfArea.height - this.childrenArea.start.y,
      this.selfArea.height - 0,
    );
  }

  getChildrenAreaWH() {
    let { children } = this;
    let minX = 0;
    let maxX = 0;
    let minY = 0;
    let maxY = 0;
    for (let i = 0, l = children.length; i < l; i++) {
      let child = children[i];
      minX = Math.min(minX, child.ruleArea.start.x);
      maxX = Math.max(maxX, child.ruleArea.end.x);
      minY = Math.min(minY, child.ruleArea.start.y);
      maxY = Math.max(maxY, child.ruleArea.end.y);
    }
    this.childrenArea.width = maxX - minX;
    this.childrenArea.height = maxY - minY;
  }

  getSelfAreaStart() {
    let { x, y } = this.childrenArea.start;
    this.selfArea.start.x = 0 - Math.min(0, x);
    this.selfArea.start.y = 0 - Math.min(0, y);
  }

  get style() {
    const style = {};
    _.isNumber(this._style.left) && (style.left = `${this._style.left}px`);
    _.isNumber(this._style.top) && (style.top = `${this._style.top}px`);
    this._style.visibility && (style.visibility = `${this._style.visibility}`);
    this._style.display && (style.display = `${this._style.display}`);
    return style;
  }

  set style({ left, top, visibility, display }) {
    _.isNumber(left) && (this._style.left = left);
    _.isNumber(top) && (this._style.top = top);
    visibility && (this._style.visibility = visibility);
    display && (this._style.display = display);
  }

  get expanderStyle() {
    const expanderStyle = {};
    _.isNumber(this._expanderStyle.left) && (expanderStyle.left = `${this._expanderStyle.left}px`);
    _.isNumber(this._expanderStyle.top) && (expanderStyle.top = `${this._expanderStyle.top}px`);
    _.isNumber(this._expanderStyle.bottom) &&
      (expanderStyle.bottom = `${this._expanderStyle.bottom}px`);
    _.isNumber(this._expanderStyle.right) &&
      (expanderStyle.right = `${this._expanderStyle.right}px`);
    this._expanderStyle.visibility &&
      (expanderStyle.visibility = `${this._expanderStyle.visibility}`);
    return expanderStyle;
  }

  set expanderStyle({ visibility, left, top, bottom, right }) {
    visibility && (this._expanderStyle.visibility = visibility);
    _.isNumber(left) && (this._expanderStyle.left = left);
    _.isNumber(top) && (this._expanderStyle.top = top);
    _.isNumber(right) && (this._expanderStyle.right = right);
    _.isNumber(bottom) && (this._expanderStyle.bottom = bottom);
  }
}

export default YMNode;
