import React from 'react';
import PropTypes from 'prop-types';
import { getStroke } from '../../../theme/getTheme';
import { makeStyles } from '@material-ui/styles';
import _ from 'lodash';

const SvgLine = function SvgLine(props) {
  return (
    <svg width="100%" height="100%" id="mind-canvas">
      {props.lines.map(line => line && line.index && <Line key={line.d} line={line} />)}
    </svg>
  );
};

SvgLine.propTypes = {
  lines: PropTypes.array,
};

const useStyles = makeStyles(theme => {
  return {
    rootLine: getStroke(1, theme),
    mainLine: getStroke(2, theme),
  };
});

const areEqual = (prevProps, nextProps) => {
  return _.isEqual(prevProps.line, nextProps.line);
};
const Line = React.memo(props => {
  // eslint-disable-next-line no-unused-vars
  const { d, index, color, ...rest } = props.line;
  const classes = useStyles(props);
  const className = index === 1 ? classes.rootLine : classes.mainLine;
  return (
    <path fill="none" strokeLinecap="square" d={d} className={className} stroke={color} {...rest} />
  );
}, areEqual);
Line.displayName = 'Line';
Line.propTypes = {
  line: PropTypes.object,
};

export default SvgLine;
