import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { getUnhandleProps } from '../common/util';
import _ from 'lodash';

export default class TextArea extends PureComponent {
  static propTypes = {
    rows: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    autoHeight: PropTypes.bool,
    style: PropTypes.object,
    value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    onChange: PropTypes.func,
    autoFocus: PropTypes.bool,
  };

  static defaultProps = {
    rows: 3,
    autoFocus: false,
  };

  componentDidMount() {
    this.updateHeight();
  }

  componentDidUpdate(prevProps) {
    if (!this.props.autoHeight && prevProps.autoHeight) {
      this.removeAutoHeightStyles();
    }
    if ((this.props.autoHeight && !prevProps.autoHeight) || prevProps.value !== this.props.value) {
      this.updateHeight();
    }
  }

  updateHeight = () => {
    const { autoHeight } = this.props;
    if (!this.elementRef || !autoHeight) return;
    const { minHeight, borderBottomWidth, borderTopWidth } = window.getComputedStyle(
      this.elementRef,
    );
    const borderHeight = _.sum([borderBottomWidth, borderTopWidth].map(x => parseFloat(x)));

    // 确保高度能自适应
    this.elementRef.style.height = 'auto';
    this.elementRef.style.overflowY = 'hidden';
    this.elementRef.style.height = `${Math.max(
      parseFloat(minHeight),
      Math.ceil(this.elementRef.scrollHeight + borderHeight),
    )}px`;
    this.elementRef.style.overflowY = '';
  };

  handleChange = e => {
    const value = e.target.value;
    _.invoke(this.props, 'onChange', e, { ...this.props, value });
  };

  handleInput = e => {
    const value = e.target.value;
    _.invoke(this.props, 'OnInput', e, { ...this.props, value });
    this.updateHeight();
  };

  handleRef = ref => (this.elementRef = ref);

  removeAutoHeightStyles = () => {
    this.elementRef.style = {
      height: null,
      resize: null,
    };
  };

  render() {
    const { autoHeight, rows, style, value, autoFocus } = this.props;
    const resize = autoHeight ? 'none' : '';
    const rest = getUnhandleProps(TextArea, this.props, { autoHeight });
    return (
      <textarea
        {...rest}
        onChange={this.handleChange}
        onInput={this.handleInput}
        ref={this.handleRef}
        rows={rows}
        style={{ resize, ...style }}
        value={value}
        autoFocus={autoFocus}
      />
    );
  }
}
