import defaultTheme from '../../theme/default';
import simple from '../../theme/simple';

export const getCustomStyleWithTheme = (index, theme) => {
  switch (index) {
    case 0:
      return {
        ...theme.root,
      };
    case 1:
      return {
        ...theme.main,
      };
    case 2:
      return {
        ...theme.sub,
      };
    default:
      return {
        ...theme.third,
      };
  }
};

export const getThemeStyle = (index, configTheme = 'default') => {
  let theme;
  switch (configTheme) {
    case 'simple': {
      theme = simple;
      break;
    }
    default:
      theme = defaultTheme;
      break;
  }
  return getCustomStyleWithTheme(index, theme);
};

const DeepBlue = '#4D94FF';
const TiffanyBlue = '#1BD5D2';
const GrassGreen = '#1DDA70';
const Purple = '#9B83F8';
const Red = '#FE695D';
const Orange = '#FF974E';

const COLOR_LIST = [DeepBlue, TiffanyBlue, GrassGreen, Red, Purple, Orange];

export const getColorWithLineType = (node, lineType) => {
  if (lineType === 'default') return COLOR_LIST[node.order % COLOR_LIST.length];
  if (lineType === 'simple') return '#434B54';
};
