import { Component } from 'react';
import { redoStack } from 'common/redoStack';
import { undoStack } from 'common/undoStack';
import setType from 'es6-set-proptypes';
import { pcBridge } from '../../../bridges';
import PropTypes from 'prop-types';
import client from 'common/client';
import { KEYCDEO_ZERO, KEYCODE_ADD, KEYCODE_CUT, KEYCODE_BACK } from 'common/constants';
import { shortKeyHandle, eqSet } from 'common/util';
import { KEYCODE_COPY } from '../../../common/constants';

const noop = () => {};

export default class ShortCutKey extends Component {
  static propTypes = {
    selectedNodes: setType,
    nodes: PropTypes.array,
  };
  componentDidMount() {
    this.registerShortCutKey();
    // 全局的快捷键（未选中节点的时候）
    document.addEventListener('keydown', this.handleKeyDown);
  }

  shouldComponentUpdate(nextProps) {
    return !eqSet(nextProps.selectedNodes, this.props.selectedNodes);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown);
  }

  // globalListenerUndo = event => {
  //   // if (this.props.selectedNodes.size !== 0) {
  //   //   return;
  //   // }
  //   // if (event.shiftKey) {
  //   //   mindmapEditor.handleRedo();
  //   //   return;
  //   // }
  //   // mindmapEditor.handleUndo();
  // };

  globalListenerHandleCopy = () => {
    const { selectedNodes, nodes } = this.props;
    if (selectedNodes.size > 0) {
      const selectedNodesTopicData = nodes
        .map(item => {
          if (selectedNodes.has(item.id)) {
            return item.topic;
          }
          return '';
        })
        .join('\n');
      mindmapEditor.pasteData = selectedNodesTopicData;
    }
  };

  handleKeyDown = event => {
    if (event.metaKey || event.ctrlKey) {
      switch (event.keyCode) {
        case KEYCODE_ADD:
          return shortKeyHandle(mindmapEditor.handleZoomIn)(event);
        case KEYCODE_CUT:
          return shortKeyHandle(mindmapEditor.handleZoomOut)(event);
        case KEYCDEO_ZERO:
          return shortKeyHandle(mindmapEditor.scrollToCenter)(event);
        // case KEYCODE_UNDO:
        //   return this.globalListenerUndo(event);
        case KEYCODE_COPY:
          return this.globalListenerHandleCopy(event);
        default:
          break;
      }
    } else {
      switch (event.keyCode) {
        case KEYCODE_BACK: {
          if (
            !mindmapEditor.isEditing &&
            document.activeElement.tagName.toLocaleLowerCase() === 'body' &&
            this.props.selectedNodes.size > 1
          ) {
            return mindmapEditor.deleteNode();
          }
          break;
        }
        default:
          break;
      }
    }
  };

  registerShortCutKey = () => {
    const insertBrotherNode = () => mindmapEditor.insertBrotherNode();
    const insertChildNode = () => mindmapEditor.insertChildNode();
    const deleteNode = () => mindmapEditor.deleteNode();
    const arrowLeft = () => mindmapEditor.arrowLeft();
    const arrowRight = () => mindmapEditor.arrowRight();
    const arrowDown = () => mindmapEditor.arrowDown();
    const arrowUp = () => mindmapEditor.arrowUp();
    const handleUndo = () => mindmapEditor.handleUndo();
    const handleRedo = () => mindmapEditor.handleRedo();
    const handleClientSearch = () => pcBridge.OnFindText();
    handleClientSearch.__status__ = () => client.isPC();
    handleUndo.__status__ = () => undoStack.canUndo() && !mindmapEditor.isEditing;
    handleRedo.__status__ = () => redoStack.canRedo() && !mindmapEditor.isEditing;

    const toggleBold = () => mindmapEditor.toggleBold();
    const toggleItalic = () => mindmapEditor.toggleItalic();
    const toggleUnderline = () => mindmapEditor.toggleUnderline();
    const toggleDeleteline = () => mindmapEditor.toggleDeleteline();
    const handlePaste = event => mindmapEditor.handlePaste(event);
    const _canToggle = () => !mindmapEditor.isEditing && this.props.selectedNodes.size !== 0;
    // prettier-ignore
    const notEditAndSize1 = () => !mindmapEditor.isEditing && this.props.selectedNodes.size === 1;
    const _isEditing = () => !mindmapEditor.isEditing;
    deleteNode.__status__ = _isEditing;
    [handlePaste, toggleItalic, toggleBold, toggleUnderline, toggleDeleteline].forEach(
      // eslint-disable-next-line no-return-assign
      item => (item.__status__ = _canToggle),
    );
    [insertBrotherNode, insertChildNode, arrowLeft, arrowRight, arrowUp, arrowDown].forEach(
      // eslint-disable-next-line no-return-assign
      item => (item.__status__ = notEditAndSize1),
    );
    mindmapEditor.shortCutKeyMap = {
      'ctrl+b': toggleBold, // 加粗
      'ctrl+i': toggleItalic, // 斜体
      'ctrl+e': toggleDeleteline, // 下划线
      'ctrl+u': toggleUnderline, // 删除线
      'ctrl+a': noop, // 全选
      'ctrl+0': noop, // 回到中点
      'ctrl+=': noop, // 放大
      'ctrl+-': noop, // 缩小
      'ctrl+z': handleUndo, // 撤销
      'ctrl+shift+z': handleRedo, // 重做
      'ctrl+f': handleClientSearch, // pc 客户端搜索
      Tab: insertChildNode, // 插入子节点
      Enter: insertBrotherNode, // 插入同级节点
      Insert: insertChildNode, // 插入子节点
      Delete: deleteNode, // 删除节点
      Backspace: deleteNode,
      Left: arrowLeft,
      Right: arrowRight,
      Up: arrowUp,
      Down: arrowDown,
    };
  };

  render() {
    return null;
  }
}
