import React from 'react';
import PropTypes from 'prop-types';
import DocumentEvents from 'react-document-events';
import { MOVE_NODE } from 'common/constants';
import { undoStack } from 'common/undoStack';
import { KEYCODE_SPACE } from '../../../common/constants';
import { withStyles } from '@material-ui/styles';
import { getTheme } from '../../../theme/getTheme';

class DragNode extends React.Component {
  static propTypes = {
    nodes: PropTypes.array,
    onDrop: PropTypes.func,
    classes: PropTypes.object,
  };

  handleKeyDown = event => {
    // 如果当前处于编辑态，即 editor 节点接受事件，不禁止默认事件
    if (
      event.target.id === 'editor' ||
      ['textarea', 'input'].includes(event.target.tagName.toLowerCase())
    ) {
      return;
    }
    if (event.keyCode === KEYCODE_SPACE) {
      this.disable = true;
      event.preventDefault();
    }
  };

  handleKeyUp = () => {
    this.disable = false;
  };

  handleDragStart = event => {
    if (this.disable || (event.target.dataset && event.target.dataset.helpId === 'root')) {
      event.preventDefault();
      return;
    }
    this.ghostNode = event.target.cloneNode(true);
    const svgIcon = this.ghostNode.querySelector('svg');
    if (svgIcon) svgIcon.remove();
    const { width, height } = event.target.getBoundingClientRect();
    this.ghostNode.id = 'dragnodes';
    Object.assign(this.ghostNode.style, {
      width: `${width}px`,
      height: `${height}px`,
      position: 'absolute',
      top: `${-999}px`,
    });
    document.body.appendChild(this.ghostNode);
    event.dataTransfer.setDragImage(this.ghostNode, 0, 0);
    if (mindmapEditor.isEditing) {
      event.stopPropagation();
      event.preventDefault();
    }
    const target = event.target;
    const targetId = target.id || target.firstChild.id;
    const { nodes } = this.props;
    const dragNode = nodes.find(item => item.id === targetId);
    if (dragNode) {
      this.dragNodeId = dragNode.id;
    }
  };

  handleDragEnter = event => {
    this._clearDragoverClass();
    const targetId = event.target.dataset.helpId;
    if (!targetId || !this.dragNodeId) return;
    if (targetId === this.dragNodeId) return;
    const canDrop = mindmapEditor.manager.canMoveNodes(this.dragNodeId, targetId);
    this.canDrop = canDrop;
    if (!canDrop) return;
    this.targetId = targetId;
    const dragOverCls =
      this.targetId === 'root' ? this.props.classes.root : this.props.classes.default;

    document.getElementById(targetId).firstChild.classList.add(dragOverCls);
  };

  _clearDragoverClass = () => {
    if (this.targetId) {
      const dragOverCls =
        this.targetId === 'root' ? this.props.classes.root : this.props.classes.default;

      document.getElementById(this.targetId).firstChild.classList.remove(dragOverCls);
    }
  };

  removeGhostNode = () => {
    if (this.ghostNode) {
      this.ghostNode.remove();
      this.ghostNode = null;
    }
  };

  handleDragEnd = () => {
    this._clearDragoverClass();
    this.lastTarget = null;
    this.removeGhostNode();
  };

  handleDragOver = event => {
    // 拖到外部时检测
    if (!event.target.dataset.helpId) {
      this.canDrop = false;
    }
    if (this.canDrop) {
      event.preventDefault();
    }
  };

  handleDrop = () => {
    const { nodes } = this.props;
    undoStack.pushToUndoStack(MOVE_NODE, nodes);
    if (!this.targetId || !this.dragNodeId) return;
    this.props.onDrop(this.dragNodeId, this.targetId);
    const dragOverCls =
      this.targetId === 'root' ? this.props.classes.root : this.props.classes.default;
    document.getElementById(this.targetId).firstChild.classList.remove(dragOverCls);
    this.targetId = null;
    this.dragNodeId = null;
    this.removeGhostNode();
  };

  render() {
    return (
      <DocumentEvents
        onDragStart={this.handleDragStart}
        onDrop={this.handleDrop}
        onDragOver={this.handleDragOver}
        onDragEnd={this.handleDragEnd}
        onDragEnter={this.handleDragEnter}
        onKeyDown={this.handleKeyDown}
        onKeyUp={this.handleKeyUp}
      />
    );
  }
}

const styles = theme => ({
  root: {
    ...getTheme(theme).dragOver.root,
  },
  default: {
    ...getTheme(theme).dragOver.default,
  },
});

export default withStyles(styles)(DragNode);
