let KEYMAP = {
  Backspace: 8,
  Tab: 9,
  Enter: 13,
  Delete: 46,
  Shift: 16,
  Control: 17,
  Alt: 18,
  CapsLock: 20,

  Esc: 27,

  Spacebar: 32,

  PageUp: 33,
  PageDown: 34,
  End: 35,
  Home: 36,

  Insert: 45,

  Left: 37,
  Up: 38,
  Right: 39,
  Down: 40,
  Cmd: 91,
  CmdFF: 224,
  F1: 112,
  F2: 113,
  F3: 114,
  F4: 115,
  F5: 116,
  F6: 117,
  F7: 118,
  F8: 119,
  F9: 120,
  F10: 121,
  F11: 122,
  F12: 123,

  '`': 192,
  '=': 187,
  '-': 189,

  '/': 191,
  '.': 190,
};

// 小写适配
for (let key in KEYMAP) {
  if (KEYMAP.hasOwnProperty(key)) {
    KEYMAP[key.toLowerCase()] = KEYMAP[key];
  }
}
let aKeyCode = 65;
let aCharCode = 'a'.charCodeAt(0);

// letters
'abcdefghijklmnopqrstuvwxyz'.split('').forEach(function(letter) {
  KEYMAP[letter] = aKeyCode + (letter.charCodeAt(0) - aCharCode);
});

// numbers
let n = 9;
do {
  KEYMAP[n.toString()] = n + 48;
} while (--n);

export default KEYMAP;
