import React, { Component } from 'react';
import PropTypes from 'prop-types';
import exact from 'prop-types-exact';
import _ from 'lodash';
import { __DEV__, KEYCODE_SPACE, FORCE_UPDATE_NODES } from '../../../common/constants';
import client from '../../../common/client';
import { getTheme } from '../../../theme/getTheme';
import { pcBridge } from '../../../bridges/pc';
import { withStyles } from '@material-ui/styles';

class MoveableCanvas extends Component {
  mindMapContainer = React.createRef();
  constructor(props) {
    super(props);
    this._spaceKeyDown = false;
    this.handleScroll = _.debounce(this.handleScroll, 300);
  }

  componentDidMount() {
    document.addEventListener('keydown', this.syncHandleKeyDown);
    document.addEventListener('keyup', this.syncHandleKeyUp);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.syncHandleKeyDown);
    document.removeEventListener('keyup', this.syncHandleKeyUp);
  }

  handleScroll = () => {
    __DEV__ && console.log('handleScroll');
    if (!mindmapEditor.isEditing) {
      mindmapEditor.emit(FORCE_UPDATE_NODES);
    }
  };

  syncHandleKeyUp = () => {
    this._spaceKeyDown = false;
  };

  syncHandleKeyDown = e => {
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    if (
      e.keyCode === KEYCODE_SPACE &&
      selectedNodes.size === 0 &&
      !mindmapEditor.isEditing &&
      !mindmapEditor.__uistatus__.focus
    ) {
      this._spaceKeyDown = true;
      e.preventDefault();
    }
  };

  _canMoveCanvas = event => {
    // 在非编辑态的情况下
    // 1. 点击根节点
    // 2. 非节点的区域+空格键 允许拖拽
    const ifClickRoot =
      event.target.id === 'root' ||
      (event.target.parentNode && event.target.parentNode.id === 'root');
    return (
      !mindmapEditor.isEditing &&
      (ifClickRoot || (!ifClickRoot && this._spaceKeyDown)) &&
      !mindmapEditor.__uistatus__.focus
    );
  };

  checkNodeClick = event => {
    const { nodeContainer, toolbarContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;

    if (!event.target.contains(document.getElementById('mind-canvas'))) return;
    // 加 setTimeout 是为了先让节点 blur 时取到 node 的值
    if (selectedNodes.size) {
      setTimeout(() => {
        nodeContainer.clearSelect();
        toolbarContainer.setDefaultTextStyle();
      });
    }
  };

  handleContainerMouseDown = e => {
    this.checkNodeClick(e);
    if (!this._canMoveCanvas(e)) return;
    const container = this.mindMapContainer.current;
    if (!container) return;
    const startX = e.clientX;
    const startY = e.clientY;
    const _prevScrollLeft = container.scrollLeft;
    const _prevScrollTop = container.scrollTop;
    function onMove(ev) {
      if (Math.max(Math.abs(ev.clientX - startX), Math.abs(ev.clientY - startY)) < 2) return;
      container.classList.add('dragging');
      // document.getElementById('nodes').stylev.pointerEvents = 'none';
      container.scrollLeft = _prevScrollLeft - (ev.clientX - startX);
      container.scrollTop = _prevScrollTop - (ev.clientY - startY);
    }

    const onUp = () => {
      document.removeEventListener('mousemove', onMove);
      document.removeEventListener('mouseup', onUp);
      if (container) {
        container.classList.remove('dragging');
      }
      this._spaceKeyDown = false;
    };
    document.addEventListener('mousemove', onMove);
    document.addEventListener('mouseup', onUp);
  };

  disableContextMenu = event => {
    event.preventDefault();
  };

  render() {
    const {
      classes,
      toolbarContainer: {
        state: { loading },
      },
      children,
    } = this.props;
    let className;
    const classList = [`${classes.mindContainer}`, `${classes.nodeTheme}`];
    if (loading) classList.push('loading');
    className = classList.join(' ');
    return (
      <div
        className={className}
        id="mindmap-container"
        ref={this.mindMapContainer}
        onScroll={this.handleScroll}
        onMouseDown={this.handleContainerMouseDown}
        onContextMenu={this.disableContextMenu}
      >
        {children}
      </div>
    );
  }
}

const styles = theme => ({
  nodeTheme: {
    background: getTheme(theme).background,
  },
  mindContainer: {
    position: 'relative',
    cursor: 'inherit',
    overflow: 'auto',
    width: '100vw',
    height: '100vh',
    '&.loading': {
      opacity: '0 !important',
    },
  },
});

export default withStyles(styles)(MoveableCanvas);

MoveableCanvas.propTypes = exact({
  classes: PropTypes.object,
  nodeContainer: PropTypes.object,
  toolbarContainer: PropTypes.object,
  children: PropTypes.object,
});
