import React from 'react';
import PropTypes from 'prop-types';
import exact from 'prop-types-exact';
import { makeStyles } from '@material-ui/styles';
// import ImageItem from './ImageItem';
import LinkItem from './LinkItem';
import RemarkItem from './RemarkItem';
import _ from 'lodash';
import { getThemeStyle, getThemeFontFamily } from '../../../theme/getTheme';
import { Close as CloseIcon } from 'components/Icons';

const stopEevent = e => {
  e.preventDefault();
  e.stopPropagation();
};

const iconOuter = {
  width: 14,
  height: 14,
};

const minNodeStyle = {
  minWidth: '12px',
  minHeight: '12px',
};

// 注意，不推荐通过 props 强行注入，会给 每个 props 依赖的 classname 添加 scope 去做单独的判断
const useStyles = makeStyles(theme => {
  return {
    baseNode: {
      border: '2px solid transparent',
      maxWidth: '380px',
      textAlign: 'left',
      wordBreak: 'break-all',
      fontFamily: getThemeFontFamily(theme),
      ...minNodeStyle,
    },
    rootTheme: getThemeStyle(Math.min(0), theme),
    mainTheme: getThemeStyle(1, theme),
    subTheme: getThemeStyle(2, theme),
    thirdTheme: getThemeStyle(3, theme),
    nodeItem: {
      position: 'absolute',
      paddingRight: '20px',
      paddingLeft: '20px',
      marginLeft: '-20px',
    },
    isExpander: {
      '&:hover $closeExpander': {
        visibility: 'visible !important',
      },
    },
    closeExpander: {
      visibility: 'visible !important',
    },
    openExpander: {
      position: 'absolute',
      borderRadius: '50%',
      border: '1px solid rgba(66, 75, 84, 1)',
      fontSize: '8px',
      width: '13px',
      height: '13px',
      lineHeight: '13px',
      display: 'flex',
      justifyContent: 'center',
      alignContent: 'center',
      cursor: 'pointer',
      background: '#ebebeb',
      color: '#434b54',
      fontFamily: '微软雅黑',
    },
    dot: {
      letterSpacing: '1px',
    },
    noDot: {
      letterSpacing: '-1px',
    },
    expander: {
      visibility: 'hidden !important',
      position: 'absolute',
      display: 'block',
      cursor: 'pointer',
      color: '#EBEBEB',
      '&:hover': {
        color: '#C2C2C2',
      },
    },
    node: {
      cursor: 'inherit',
      boxSizing: 'border-box',
      color: '#333333',
      fontFamily: 'Verdana, Arial, Helvetica, sans-serif',
      display: 'flex',
    },
    wrapper: {
      position: 'relative',
    },
  };
});

/**
 * nodeItem  html 结构
 * 最外层 div 用于事件处理以及提供 padding 来实现展开图标的状态显示
 * --次层用于定位坐标位置
 * ----拖拽
 * ------次层相对定位（赋予ID）
 * --------次层用于渲染文本/备注等图标
 * --------展开图标相对定位
 * 由于每次都是新生成的数组，我们的 shadowEqual 会失效
 * 需要用 lodash 的 equal 来做判断
 * eg:
 * var object = { 'a': 1 };
 * var other = { 'a': 1 };
 * _.isEqual(object, other); // true
 */
function NodeItem(props) {
  const classes = useStyles();
  const { node, readOnly, onClick, onContextMenu, onDoubleClick } = props;
  const handleToggle = id => () => {
    if (document.activeElement && document.activeElement.id === 'editor') {
      document.activeElement.blur();
    }
    mindmapEditor.toggleNode(id);
  };
  const renderExpandIcon = () => {
    if (node.isroot) return null;
    // eslint-disable-next-line no-unused-vars
    const { visibility, ...rest } = node.expanderStyle || {};
    const YMNode = mindmapEditor.manager.YMNodes[node.id];
    return node.expanded ? (
      <CloseIcon
        className={`${classes.expander} ${classes.closeExpander}`}
        style={rest}
        onClick={handleToggle(node.id)}
        onDoubleClick={stopEevent}
        onMouseDown={stopEevent}
      />
    ) : (
      YMNode && YMNode.children.length > 0 && (
        <div
          style={rest}
          className={`${classes.openExpander} ${
            YMNode.getDescendantLength() >= 100 ? classes.dot : classes.noDot
          }`}
          onClick={handleToggle(node.id)}
          onMouseDown={stopEevent}
        >
          {YMNode.getDescendantLength() >= 100 ? '‧‧‧' : YMNode.getDescendantLength()}
        </div>
      )
    );
  };
  const { left, top, visibility } = node.style || { left: 0, top: 0 };
  const containerStyle = {
    left,
    top,
    visibility,
  };
  const containerClass = [`${classes.nodeItem}`];
  if (
    node.expanded &&
    (!node.expanderStyle || (node.expanderStyle && node.expanderStyle.visibility !== 'hidden'))
  ) {
    containerClass.push(`${classes.isExpander}`);
  }
  const containerClassName = containerClass.join(' ');
  let nodeStyle = node.customStyle || {};
  const contentClassArr = [classes.baseNode];
  const getContentClass = () => {
    switch (node.index) {
      case 0:
        return contentClassArr.push(`${classes.rootTheme}`);
      case 1:
        return contentClassArr.push(`${classes.mainTheme}`);
      case 2:
        return contentClassArr.push(`${classes.subTheme}`);
      default:
        return contentClassArr.push(`${classes.thirdTheme}`);
    }
  };
  getContentClass();
  const contentClass = contentClassArr.join(' ');
  return (
    <div
      style={containerStyle}
      className={containerClassName}
      onDoubleClick={onDoubleClick}
      onClick={onClick(node)}
      onContextMenu={onContextMenu(node.id)}
    >
      <div className={`${classes.node}`} draggable="true" data-help-id={node.id}>
        <div className={`${classes.wrapper} move-selection`} id={node.id}>
          <div className={contentClass} style={nodeStyle} data-help-id={node.id}>
            {node.topic}
            {node.link && Boolean(node.link.id) && (
              <LinkItem link={node.link} readOnly={readOnly} style={iconOuter} />
            )}
            {node.remark && (
              <RemarkItem id={node.id} remark={node.remark} readOnly={readOnly} style={iconOuter} />
            )}
          </div>
          {renderExpandIcon(node)}
        </div>
      </div>
    </div>
  );
}

const areEqual = (prevProps, nextProps) => {
  const isNodeDeepEqual = _.isEqual(prevProps.node, nextProps.node);
  // 检测子节点的长度是否相同
  const checkChildEqual = () => {
    mindmapEditor.manager.YMNodes[prevProps.node.id].children.length ===
      mindmapEditor.manager.YMNodes[nextProps.node.id].children.length;
  };
  return isNodeDeepEqual && checkChildEqual();
};
export default React.memo(NodeItem, areEqual);

NodeItem.propTypes = exact({
  node: PropTypes.object.isRequired,
  readOnly: PropTypes.bool,
  onClick: PropTypes.func,
  onContextMenu: PropTypes.func,
  onDoubleClick: PropTypes.func,
});
