import { Bezier, Straight, StraightWithArc, LineType } from './Line';
import { arcRadius, LineTypeMap } from '../../constant';

const LineMap = {
  Bezier,
  Straight,
  StraightWithArc,
};

const processDirection = (direction, node) => {
  return node.index === 1 && direction === 'bottom_catalog' ? 'bottom' : direction;
};

const processLineType = (lineType, direction, node) => {
  const c1 = direction === 'bottom';
  const c2 = direction === 'bottom_catalog' && node.index === 1;
  const c3 = direction === 'top';
  const c4 = direction === 'top_catalog' && node.index === 1;
  const c = c1 || c2 || c3 || c4;
  if (c && LineTypeMap[lineType].line === LineType.StraightWithArc && !node.isFirstOrLast()) {
    return LineType.Straight;
  }
  return LineTypeMap[lineType].line;
};

export default function getLinesArray(nodesArray, lineType, direction) {
  let linesArray = [];
  nodesArray.forEach(node => {
    const directionProcessed = processDirection(direction, node);
    const typeProcessed = processLineType(lineType, direction, node);
    const line = new LineMap[typeProcessed]({
      node,
      direction: directionProcessed,
      radius: arcRadius,
      lineType,
    });

    const pushToLinesArray = linePath => {
      if (linePath && linesArray.indexOf(linePath) === -1) {
        linesArray.push(linePath);
      }
    };
    // 获取收起后的小折线
    if (!node.expanded && !node.isHidden()) {
      pushToLinesArray(line.getExpand());
    }

    pushToLinesArray(line.draw());
  });
  return linesArray;
}
