import React, { PureComponent } from 'react';
import { Selection } from 'components';
import PropTypes from 'prop-types';
import { FORCE_UPDATE_NODES } from 'common/constants';
import { KEYCODE_SPACE } from '../../../common/constants';

export default class DragSelection extends PureComponent {
  static propTypes = {
    zoom: PropTypes.number,
    handleSelection: PropTypes.func,
    children: PropTypes.array,
    disabled: PropTypes.bool,
  };

  constructor() {
    super();
    this.state = {
      canDrag: true,
      eleRefs: [],
    };
  }

  componentDidMount() {
    document.addEventListener('keydown', this.handlekeydown);
    document.addEventListener('keyup', this.handleKeyUp);
    mindmapEditor.on(FORCE_UPDATE_NODES, this.updateForce);
    this.targetContainer = document.getElementById('mindmap-inner');
    this.setElements();
  }

  static getDerivedStateFromProps() {
    const mapNodes = document.getElementById('map-nodes');
    if (mapNodes) {
      return {
        eleRefs: Array.prototype.slice.call(mapNodes.querySelectorAll('.move-selection')),
      };
    }
    return null;
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handlekeydown);
    document.removeEventListener('keyup', this.handleKeyUp);
    mindmapEditor.removeListener(FORCE_UPDATE_NODES, this.updateForce);
  }

  updateForce = () => {
    this.forceUpdate();
  };

  handleKeyUp = () => {
    this.enableDrag();
  };

  handleMouseDown = event => {
    // 选中到节点时会和节点拖拽冲突，因此禁止滑动选择节点
    if ((event.target && event.target.dataset.helpId) || mindmapEditor.isEditing) {
      this.disableDrag();
    } else {
      this.enableDrag();
      // pc 端会滑动到底部，需要禁掉默认事件
      event.preventDefault();
    }
  };

  disableDrag = () => {
    this.setState({
      canDrag: false,
    });
  };
  enableDrag = () => {
    this.setState({
      canDrag: true,
    });
  };

  setElements = () => {
    const mapNodes = document.getElementById('map-nodes');
    if (mapNodes) {
      // 接收的是一个数组
      // eslint-disable-next-line react/no-did-mount-set-state
      this.setState({
        eleRefs: Array.prototype.slice.call(mapNodes.querySelectorAll('.move-selection')),
      });
    }
  };

  handlekeydown = event => {
    if (event.keyCode === KEYCODE_SPACE) {
      this.disableDrag();
    } else {
      this.enableDrag();
    }
  };

  render() {
    const { canDrag, eleRefs } = this.state;
    const { zoom, handleSelection, disabled } = this.props;
    if (!this.targetContainer || !eleRefs) {
      return null;
    }
    // 当鼠标按在节点中的时候禁止多选，而是拖拽
    return (
      <React.Fragment>
        <div id="drag-selectron-zone" onMouseDown={this.handleMouseDown}>
          {this.props.children}
        </div>
        <Selection
          target={this.targetContainer}
          elements={eleRefs}
          onSelectionChange={handleSelection}
          disabled={!canDrag || disabled}
          scale={zoom}
        />
      </React.Fragment>
    );
  }
}
