import {
  hs_lr_base,
  vs_tb_base,
  arcRadius,
  hs_tb,
  LineTypeMap,
  baseExpandGap,
} from '../../constant';
import { getColorWithLineType } from '../../utils';

const removeBlank = s => {
  return s.replace(/\s+/g, ' ').trim();
};

class Line {
  constructor(props) {
    const { node, direction, lineType } = props;
    this.node = node;
    this.lineType = lineType || 'default';
    this.direction = direction;
    this.expandStart = {};
    this.getStart();
    this.end = this.getEnd();
    this.H = this.getH(); // 线条是否穿过节点
  }

  getStart() {
    if (this.node.isRoot()) return;

    const { parent } = this.node;
    const { index: parentIdx, position: parentPos } = parent;
    let start;
    switch (this.direction) {
      case 'right': {
        if (this.lineType === 'simple') {
          start = parentPos.middleRight;
        } else if (this.lineType === 'default') {
          start = parentIdx > 1 ? parentPos.end : parentPos.middleRight;
        }
        break;
      }
      case 'left':
        if (this.lineType === 'simple') {
          start = parentPos.middleLeft;
        } else {
          start = parentIdx > 1 ? parentPos.leftBottom : parentPos.middleLeft;
        }
        break;
      case 'bottom':
        start = parentPos.middleBottom;
        break;
      case 'top':
        start = parentPos.middleTop;
        break;
      case 'bottom_catalog':
        start = parentPos.leftBottom;
        start.x += hs_tb;
        break;
      default:
        break;
    }

    start.x = this.processNum(start.x);
    start.y = this.processNum(start.y);

    // if (this.lineType === 'simple') {
    if (this.direction === 'right') {
      this.expandStart.x = start.x + baseExpandGap;
      this.expandStart.y = start.y;
    }
    if (this.direction === 'left') {
      this.expandStart.x = start.x - baseExpandGap;
      this.expandStart.y = start.y;
    }
    // }

    this.start = start;
  }

  getEnd() {
    if (this.node.isRoot()) return;

    const { index, position } = this.node;

    let end;
    switch (this.direction) {
      case 'right':
        if (this.lineType === 'simple') {
          end = position.middleLeft;
        } else if (this.lineType === 'default') {
          end = index > 1 ? position.leftBottom : position.middleLeft;
        }
        break;
      case 'left':
        if (this.lineType === 'simple') {
          end = position.middleRight;
        } else {
          end = index > 1 ? position.end : position.middleRight;
        }
        break;
      case 'bottom':
        end = position.middleTop;
        break;
      case 'top':
        end = position.middleBottom;
        break;
      case 'bottom_catalog':
        end = position.leftBottom;
        break;
      default:
        break;
    }

    end.x = this.processNum(end.x);
    end.y = this.processNum(end.y);
    return end;
  }

  getStrokeWidth() {
    return this.node.index > 1 ? 1 : 2;
  }

  getSVGData(d) {
    if (this.H !== undefined) {
      d += ` H ${this.H}`;
    }
    d = removeBlank(d);
    return {
      d,
      index: this.node.index,
      color: getColorWithLineType(this.node, this.lineType),
    };
  }

  getH() {
    let H;
    const { position, index } = this.node;

    if (this.lineType === 'simple') {
      return;
    }

    // 如果是默认样式，并且是1级节点，不绘制节点穿过的线条
    if (this.lineType === 'default' && index === 1) {
      return;
    }

    switch (this.direction) {
      case 'right':
      case 'bottom_catalog':
        H = position.end.x;
        break;
      case 'left':
        H = position.leftBottom.x;
        break;
      default:
        break;
    }
    return H;
  }

  getExpand() {
    let d;
    switch (this.direction) {
      case 'right': {
        if (this.lineType === 'simple' || (this.lineType === 'default' && this.node.index === 1)) {
          const { x, y } = this.node.position.middleRight;
          d = `
          M ${x} ${y}
          H ${x + baseExpandGap}
          `;
        } else if (this.lineType === 'default') {
          const { x, y } = this.node.position.end;
          d = `
         M ${x} ${y}
         H ${x + baseExpandGap}
         `;
        }
        break;
      }
      case 'left': {
        if (this.lineType === 'simple' || (this.lineType === 'default' && this.node.index === 1)) {
          const { x: x2, y: y2 } = this.node.position.middleLeft;
          d = `
            M ${x2} ${y2}
            H ${x2 - baseExpandGap}
            `;
        } else if (this.lineType === 'default') {
          const { x, y } = this.node.position.leftBottom;
          d = `
         M ${x} ${y}
         H ${x - baseExpandGap}
         `;
        }
        break;
      }
      default:
        break;
    }
    return this.getSVGData(d);
  }

  processNum(n) {
    if (n == null) return;
    if (n % 1 !== 0) {
      n = Math.floor(n);
    }
    if (this.strokeWidth % 2 === 1) {
      n += 0.5;
    }
    return n;
  }
}

export class Bezier extends Line {
  draw() {
    if (this.node.isRoot() || this.node.isHidden()) {
      return;
    }

    const { x: x1, y: y1 } = this.start;
    const { x: x2, y: y2 } = this.end;
    const { x: expandStartX, y: expandStartY } = this.expandStart;
    const x = this.processNum(expandStartX + ((x2 - expandStartX) * 2) / 8);

    let d = `
      M ${x1} ${y1}
      L ${expandStartX} ${expandStartY}
      C ${x} ${y1} ${expandStartX} ${y2} ${x2} ${y2}
    `;

    return this.getSVGData(d);
  }
}

export class Straight extends Line {
  constructor(props) {
    super(props);
    this.diverge1 = this.getDiverge1();
    this.diverge2 = this.getDiverge2();
  }

  draw() {
    if (this.node.isRoot() || this.node.isHidden()) return;
    const { x: x1, y: y1 } = this.start;
    const { x: xd1, y: yd1 } = this.diverge1;
    let xd2;
    let yd2;
    if (this.diverge2) {
      xd2 = this.diverge2.x;
      yd2 = this.diverge2.y;
    }
    const { x: x2, y: y2 } = this.end;

    let d = `M ${x1} ${y1}`;
    xd1 && yd1 && (d += ` L ${xd1} ${yd1}`);
    xd2 && yd2 && (d += ` L ${xd2} ${yd2}`);
    d += `L ${x2} ${y2}`;

    return this.getSVGData(d);
  }

  getDiverge1() {
    if (this.node.isRoot()) return;

    let x;
    let y;
    switch (this.direction) {
      case 'left':
        x = this.start.x - hs_lr_base;
        y = this.start.y;
        break;
      case 'right':
        x = this.start.x + hs_lr_base;
        y = this.start.y;
        break;
      case 'bottom':
        x = this.start.x;
        y = this.node.index > 1 ? this.start.y + vs_tb_base : this.start.y + vs_tb_base * 1.618;
        break;
      case 'top':
        x = this.start.x;
        y = this.node.index > 1 ? this.start.y - vs_tb_base : this.start.y - vs_tb_base * 1.618;
        break;
      case 'bottom_catalog':
        x = this.start.x;
        y = this.end.y;
        break;
      default:
        break;
    }

    x = this.processNum(x);
    y = this.processNum(y);
    return { x, y };
  }

  getDiverge2() {
    if (this.node.isRoot()) return;
    switch (this.direction) {
      case 'left':
      case 'right':
        return { x: this.diverge1.x, y: this.end.y };
      case 'bottom':
      case 'top':
        return { x: this.end.x, y: this.diverge1.y };
      default:
        break;
    }
  }
}

export class StraightWithArc extends Straight {
  constructor(props) {
    super(props);
    this.radius = props.radius || 8;
    this.diverge1 = this.getDiverge1();
  }

  draw() {
    if (this.node.isRoot()) {
      return;
    }

    // 当该节点处于收起状态时，新增一条线
    if (this.node.isHidden() && !this.node.parent.isHidden()) {
      // todo: 新增一条线条 看设计师需求针对收起节点
      return;
    }

    if (this.node.isHidden()) {
      return;
    }

    const r = this.radius;
    const { x: x1, y: y1 } = this.start;
    const { x: xd1, y: yd1 } = this.diverge1;
    const { x: x2, y: y2 } = this.end;
    const { xArcStart, yArcStart, xArcEnd, yArcEnd, sweepFlag, shouldAddL } = this.getArcParam(r);

    let d;
    if (this.isStraight()) {
      d = `
        M ${x1} ${y1}
        L ${x2} ${y2}
      `;
    } else {
      this.direction === 'bottom_catalog'
        ? (d = `
      M ${x1} ${y1}
      L ${xArcStart} ${yArcStart}
      A ${r} ${r} 0 0 ${sweepFlag} ${xArcEnd} ${yArcEnd}
    `)
        : (d = `
      M ${x1} ${y1}
      L ${xd1} ${yd1}
      L ${xArcStart} ${yArcStart}
      A ${r} ${r} 0 0 ${sweepFlag} ${xArcEnd} ${yArcEnd}
    `);
    }

    if (shouldAddL) {
      d += ` L ${x2} ${y2}`;
    }

    return this.getSVGData(d);
  }

  getDiverge1() {
    if (this.node.isRoot()) return;

    let x;
    let y;
    switch (this.direction) {
      case 'left':
        x = this.start.x - hs_lr_base;
        y = this.start.y;
        break;
      case 'right':
        x = this.start.x + hs_lr_base;
        y = this.start.y;
        break;
      case 'bottom':
        x = this.start.x;
        y = this.node.index > 1 ? this.start.y + vs_tb_base : this.start.y + vs_tb_base * 1.618;
        break;
      case 'top':
        x = this.start.x;
        y = this.node.index > 1 ? this.start.y - vs_tb_base : this.start.y - vs_tb_base * 1.618;
        break;
      case 'bottom_catalog':
        x = this.start.x;
        y = this.end.y;
        break;
      default:
        break;
    }

    x = this.processNum(x);
    y = this.processNum(y);
    return { x, y };
  }

  isStraight() {
    if (this.direction === 'left' || this.direction === 'right') {
      return this.start.y === this.end.y;
    }
    if (this.direction === 'top' || this.direction === 'bottom') {
      return this.start.x === this.end.x;
    }
  }

  getArcStart(y1, y2, r) {
    let yc;
    const delta = y1 - y2;
    if (delta > 2) {
      yc = y2 + r;
    } else if (delta < -2) {
      yc = y2 - r;
    } else {
      yc = y1;
    }
    return yc;
  }

  getArcParam(r) {
    const { x: x1, y: y1 } = this.start;
    const { x: x2, y: y2 } = this.end;
    const { x: xd1, y: yd1 } = this.diverge1;

    let xArcStart;
    let yArcStart;
    let xArcEnd;
    let yArcEnd;
    let sweepFlag;
    let shouldAddL;

    if (this.direction === 'left') {
      xArcStart = xd1;
      yArcStart = this.getArcStart(y1, y2, r);
      xArcEnd = xd1 - r;
      yArcEnd = y2;
      sweepFlag = y1 > y2 ? 0 : 1;
      shouldAddL = x2 < xArcEnd;
    }

    if (this.direction === 'right') {
      xArcStart = xd1;
      yArcStart = this.getArcStart(y1, y2, r);
      xArcEnd = xd1 + r;
      yArcEnd = y2;
      sweepFlag = y1 > y2 ? 1 : 0;
      shouldAddL = x2 > xArcEnd;
    }

    if (this.direction === 'top') {
      xArcStart = this.getArcStart(x1, x2, r);
      yArcStart = yd1;
      xArcEnd = x2;
      yArcEnd = yd1 - r;
      sweepFlag = x1 > x2 ? 1 : 0;
      shouldAddL = y2 < yArcEnd;
    }

    if (this.direction === 'bottom') {
      xArcStart = this.getArcStart(x1, x2, r);
      yArcStart = yd1;
      xArcEnd = x2;
      yArcEnd = yd1 + r;
      sweepFlag = x1 > x2 ? 0 : 1;
      shouldAddL = y2 > yArcEnd;
    }

    if (this.direction === 'bottom_catalog') {
      xArcStart = xd1;
      yArcStart = yd1 - r;
      xArcEnd = x2;
      yArcEnd = yd1;
      sweepFlag = 0;
      shouldAddL = y2 > yArcEnd;
    }

    return {
      xArcStart,
      yArcStart,
      xArcEnd,
      yArcEnd,
      sweepFlag,
      shouldAddL,
    };
  }
}

export const LineType = {
  Bezier: 'Bezier',
  Straight: 'Straight',
  StraightWithArc: 'StraightWithArc',
};
