class YMArea {
  constructor({ width = 0, height = 0, container, start = { x: 0, y: 0 } } = {}) {
    this.start = start;
    this.width = width;
    this.height = height;
    this.container = container;
  }

  set width(_width) {
    this._width = _width;
  }
  get width() {
    return (
      this._width ||
      (document.getElementById(this.container) &&
        document.getElementById(this.container).offsetWidth)
    );
  }

  set height(_height) {
    this._height = _height;
  }

  get height() {
    return (
      this._height ||
      (document.getElementById(this.container) &&
        document.getElementById(this.container).offsetHeight)
    );
  }

  get end() {
    return {
      x: this.start.x + this.width,
      y: this.start.y + this.height,
    };
  }
}

const DEFAULT_STRATEGY = undefined;
class RuleArea extends YMArea {
  constructor({
    width = 0,
    height = 0,
    container,
    start = { x: 0, y: 0 },
    strategy = DEFAULT_STRATEGY,
  } = {}) {
    super(width, height, start, container);
    this.strategy = strategy;
    this.container = container;
  }
}

class ChildrenArea extends YMArea {
  constructor({ width = 0, height = 0, start = { x: 0, y: 0 }, strategy = DEFAULT_STRATEGY } = {}) {
    super(width, height, start);
    this.strategy = strategy;
    this.partArea = {
      top: {},
      buttom: {},
      left: {},
      right: {},
    };
  }
}

export { YMArea, RuleArea, ChildrenArea };
