import { pcController } from 'controllers';
/**
 * PC调用编辑器的 API
 */
const editorApi = {
  insertNoteLink(id, title) {
    pcController.emit('controller:insert:notelink', id, title);
  },

  // x,y 是坐标轴
  setContent(content, readOnly = false, left, top) {
    pcController.emit('controller:set:content', content, readOnly, left, top);
  },

  getContent() {
    return window.mindmapEditor.getValue();
  },

  /**
   * return string `${left} ${top}`
   */
  getScrollPercent() {
    // return pcController.currentLocation;
  },

  focusEditor() {
    pcController.emit('controller:focus:editor');
  },

  setZoomPercent(data1, data2) {
    pcController.emit('controller:set:zoom', data1, data2);
  },

  addImageByUrl(src) {
    pcController.emit('controller:set:image', src);
  },

  downloadImage() {
    window.mindmapEditor.export2image();
  },

  export2xmind() {
    return JSON.stringify(mindmapEditor.export2xmind());
  },

  getSize() {
    const size = mindmapEditor.getSize();
    if (!size) return '';
    return `${size.width};${size.height}`;
  },

  getPureHtml() {
    return mindmapEditor.getPureHtml();
  },
};

class PCBridge {
  constructor() {
    if (!PCBridge.instance) {
      PCBridge.instance = this;
      this._nativeApiObj = window.YNote;
      if (this._nativeApiObj) {
        this._nativeApiObj.Ready((...args) => this.callEditorApi(...args));
      }
    }
    return PCBridge.instance;
  }

  OpenNote(fileId) {
    return this.callNativeApi('OpenNote', fileId);
  }

  OpenLink(link) {
    return this.callNativeApi('OpenLink', link);
  }

  PasteNoteLink() {
    return this.callNativeApi('PasteNoteLink');
  }

  ClipboardAvailable() {
    return this.callNativeApi('ClipboardAvailable');
  }

  IsNoteLinkInClipboard() {
    return this.callNativeApi('IsNoteLinkInClipboard');
  }

  callNativeApi(name, ...args) {
    console.info(name, ...args);
    if (mindmapEditor.readOnly) return;
    return this._nativeApiObj[name](...args);
  }

  callEditorApi(name, args) {
    console.info(name, args);
    return editorApi[name](...args);
  }

  OnAreaClick() {
    return this.callNativeApi('OnAreaClick');
  }

  OnContentChange() {
    return this.callNativeApi('OnContentChange');
  }

  OnFindText() {
    return this.callNativeApi('OnFindText');
  }

  OnPaste() {
    return this.callNativeApi('OnPaste');
  }

  OnImageFileUpload() {
    return this.callNativeApi('OnImageFileUpload');
  }

  GetLocalPathByResID(src) {
    const result = this.callNativeApi('GetLocalPathByResID')(src);
    pcController.emit('controller:get:localpath', result);
  }

  SetContentDone() {
    return this.callNativeApi('SetContentDone');
  }

  SetImageBase64(base64) {
    return this.callNativeApi('SetImageBase64', base64);
  }

  IsVip() {
    return this.callNativeApi('IsVip');
  }
}

const pcBridge = new PCBridge();
export { pcBridge };
