import {
  // 左右
  setXLeft,
  setXRight,
  setYCenter,
  setRootXForCenterLR,
  setRootYForCenterLR,
  // 上下
  setXCenter,
  setXHS,
  setYVS,
  setYBottom,
  setYTop,
} from './setPosition';

// ruleArea的布局策略
// 即计算childrenArea的起点

function rightCenter(node) {
  setXRight(node);
  setYCenter(node);
}

function leftCenter(node) {
  setXLeft(node);
  setYCenter(node);
}

function center_lr(root) {
  setRootXForCenterLR(root);
  setRootYForCenterLR(root);
}

function bottomCenter(node) {
  setXCenter(node);
  setYBottom(node);
}

function topCenter(node) {
  setXCenter(node);
  setYTop(node);
}

function fallDiagonal(node) {
  setXHS(node);
  setYVS(node);
}

const rs = {
  center_lr,
  rightCenter,
  leftCenter,
  bottomCenter,
  topCenter,
  fallDiagonal,
};

export default rs;
