import _ from 'lodash';
import { compareVersion } from './util';
import { __DEV__ } from './constants';

const enablePC =
  window.hasOwnProperty('YNote') || (window.parent && window.parent.hasOwnProperty('YNote'));
const enableMac =
  window.hasOwnProperty('ynote') || (window.parent && window.parent.hasOwnProperty('ynote'));

const PLATFORM_FUNCTION = {
  OnContentChange: 'OnContentChange',
  OnImageFileUpload: 'OnImageFileUpload',
  GetLocalPathByResID: 'GetLocalPathByResID',
  OnPaste: 'OnPaste',
  SetContentDone: 'SetContentDone',
  OnAreaClick: 'OnAreaClick',
  OnFindText: 'OnFindText',
};

class clientProxy {
  constructor() {
    this.registerCallback = {};
    this.platform = enablePC ? 'PC' : enableMac ? 'Mac' : 'Web';
  }

  init(store) {
    this.store = store;
  }

  isNative() {
    return enablePC || enableMac;
  }

  isPC(minVersionCode) {
    let ua = navigator.userAgent,
      versionCode = ua.match(/YNoteCEF\/(\d+\.\d+\.\d+\.\d+)/i);
    if (!minVersionCode) {
      return enablePC;
    }
    if (!versionCode) {
      return false;
    }
    versionCode = versionCode[1];
    return compareVersion(versionCode, minVersionCode);
  }

  isMac() {
    return enableMac || navigator.platform.includes('Mac');
  }

  // 来自分享页
  fromShare() {
    return window.location.href.includes('share');
  }
}

const client = new clientProxy();

export default client;
