import YMNodeManager from './Core/YMNodeManager';

/**
 * 包装 Manager，负责对接内部的数据操作
 */
class YMDelegate {
  constructor(options) {
    if (!YMDelegate.instance) {
      YMDelegate.instance = this;
      this.manager = new YMNodeManager(options);
    }
    return YMDelegate.instance;
  }

  parseData(wrappedData) {
    this.manager.parseData(wrappedData);
  }

  _handleLayoutStrategy(strategy, lineType) {
    this.manager.handleLayoutWithStratgy(strategy, lineType);
  }

  /**
   *
   * @param  {...any} args id
   * 返回 节点和线条
   */
  _deleteNode(...args) {
    this.manager.deleteNode(...args);
    return this.manager.getNodes();
  }

  _moveNodes(id, targetNodeId) {
    this.manager.moveNodes(id, targetNodeId);
    return this.manager.getNodes();
  }

  _insertNoteLink({ link, id }) {
    this.manager.insertNoteLink({ link, id });
    return this.manager.getNodes();
  }

  _insertLink(link) {
    this.manager.insertLink(link);
    return this.manager.getNodes();
  }

  _deleteLink(link) {
    this.manager.deleteLink(link);
    return this.manager.getNodes();
  }

  _insertChildNode(id) {
    this.manager.insertChildNode(id);
    return this.manager.getNodes();
  }

  _insertParentNode(id) {
    this.manager.insertParentNode(id);
    return this.manager.getNodes();
  }

  _insertBrotherNode(id) {
    this.manager.insertBrotherNode(id);
    return this.manager.getNodes();
  }

  _toggleNode(id) {
    const node = this.manager.toggleNode(id);
    this.manager.handleToggleLayout(node);
    return [this.manager.nodesRenderArray, this.manager.linesArray];
  }

  _pasteMultNodes(id, text) {
    this.manager.pasteMultNodes(id, text);
    return this.manager.getNodes();
  }

  _toggleCustomStyle(selectedIds, data, type) {
    selectedIds.forEach(id => {
      this.manager.toggleCustomStyle(id, data, type);
    });
    return this.manager.getNodes();
  }

  _saveNodeTopic(id, topic) {
    this.manager.saveNodeTopic(id, topic);
    return this.manager.getNodes();
  }

  _updateRemark(id, remark) {
    this.manager.updateRemark(id, remark);
    return this.manager.getNodes();
  }

  _deleteRemark(id) {
    this.manager.deleteRemark(id);
    return this.manager.getNodes();
  }

  // 用于处理之前版本的转换
  _addRemarkMap(remarkMap) {
    Object.keys(remarkMap).forEach(id => {
      this.manager.updateRemark(id, remarkMap[id]);
    });
  }

  injectStrategy(strategy, lineType) {
    this.manager.injectStrategy(strategy, lineType);
  }

  getNextBrotherNode(id) {
    const currentNode = this.manager.YMNodes[id];
    if (!currentNode) return null;
    function findLastChild() {
      const children = currentNode.children;
      if (!children || !children.length) return;
      return children[children.length - 1];
    }
    if (id === 'root') {
      // 如果选中的是root 节点，直接跳转到下一级最后一个节点
      const children = currentNode.children;
      if (!children || !children.length) return;
      return findLastChild();
    }
    const brothers = currentNode.parent.children;

    if (!brothers || !brothers.length) {
      // 如果没有兄弟节点
      return findLastChild();
    }

    const currentIndexInBrothers = brothers.findIndex(node => node.id === id);
    if (currentIndexInBrothers + 1 === brothers.length) {
      // 如果是最后一个节点
      return findLastChild();
    }
    return brothers[currentIndexInBrothers + 1];
  }

  getPrevBrotherNode(id) {
    const currentNode = this.manager.YMNodes[id];
    if (!currentNode) return null;
    if (id === 'root') {
      // 如果选中的是根节点，则直接跳转到子级第一个节点
      const children = currentNode.children;
      if (!children || !children.length) return;
      return children[0];
    }

    const brothers = currentNode.parent.children;
    if (!brothers || !brothers.length) {
      // 如果没有同级节点，直接返回父节点
      return currentNode.parent;
    }

    const currentIndexInBrothers = brothers.findIndex(node => node.id === id);
    if (currentIndexInBrothers === 0) {
      return currentNode.parent;
    }
    return brothers[currentIndexInBrothers - 1];
  }

  getFirstChildNode(id) {
    const children = this.manager.YMNodes[id].children;
    if (!children || !children.length) return;
    return children[0];
  }

  getMiddleNode(id) {
    const children = this.manager.YMNodes[id].children;
    const middle = Math.max(Math.ceil(children.length / 2), 0);
    return children[middle];
  }

  getParentNode(id) {
    return this.manager.YMNodes[id].parent;
  }

  getSelectedNode() {
    return this.manager.selectedNode;
  }

  export2xmind() {
    return this.manager.export2xmind();
  }
}

export default YMDelegate;
