import { Component } from 'react';
import setType from 'es6-set-proptypes';
import { eqSet } from 'common/util';
import exact from 'prop-types-exact';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/styles';
import { getTheme } from '../../../theme/getTheme';
import { __DEV__ } from '../../../common/constants';
// 匹配 a set 中 b set 没有的项
const difference = (a, b) => new Set([...a].filter(x => !b.has(x)));

// let cacheData = {};
// let temp = {};
const checkScrollIntoView = id => {
  const elem = document.getElementById(id);
  if (!elem) return;
  const GAP = 80;
  const { left, top, width, height, right, bottom } = elem.getBoundingClientRect();
  const container = document.getElementById('mindmap-container');
  // cacheData = {};
  // temp = {
  //   left: 0,
  //   top: 0,
  // };
  // let shouldScroll = false;
  // 避免节点较大时选中来回滚动
  if (width > window.innerWidth / 3) {
    return;
  }
  if (left > window.innerWidth - width - GAP) {
    container.scrollLeft += Math.max(left - (window.innerWidth - width - GAP), width + GAP);
    // cacheData.left = Math.max(left - (window.innerWidth - width - GAP), width + GAP);
    // shouldScroll = true;
  }
  if (top > window.innerHeight - height - GAP) {
    // shouldScroll = true;
    // cacheData.top = Math.max(top - (window.innerHeight - height - GAP), width + GAP);
    container.scrollTop += Math.max(top - (window.innerHeight - height - GAP), width + GAP);
  } else if (right < width + GAP) {
    // shouldScroll = true;
    // cacheData.left = Math.min(-(width + GAP), right - (width + GAP));
    container.scrollLeft += Math.min(-(width + GAP), right - (width + GAP));
  } else if (bottom < height + GAP) {
    // shouldScroll = true;
    // cacheData.top = Math.min(-(height + GAP), bottom - (height + GAP));
    container.scrollTop += Math.min(-(height + GAP), bottom - (height + GAP));
  }
  // if (!shouldScroll) return;
  // requestAnimFrame(smoothScroll);
};

class NodeSelect extends Component {
  static propTypes = exact({
    selectedNodes: setType,
    classes: PropTypes.object,
    nodes: PropTypes.array,
    updateNodeToolbar: PropTypes.func,
  });

  componentDidMount() {
    this.addSelectedNodeStyle();
  }

  shouldComponentUpdate(prevProps) {
    // 这种动态添加 class 的方法，导致节点更新后 在 nodeitem 中的 class 被重新修改，而动态添加的 class 将失效
    // 所以当数据更新时强制刷新再让其动态添加 class
    if (mindmapEditor.shouldDispatchSave) {
      return true;
    }
    return Boolean(!eqSet(prevProps.selectedNodes, this.props.selectedNodes));
  }

  componentDidUpdate(prevProps) {
    const shouldRemoveSets = difference(prevProps.selectedNodes, this.props.selectedNodes);
    // 有些删除的节点就不会再去掉 class 了
    if (shouldRemoveSets.size) {
      __DEV__ && console.log('shouldRemoveSets', shouldRemoveSets);
      shouldRemoveSets.forEach(item => this.handleItem(item, 'remove'));
    }
    this.addSelectedNodeStyle();
    // 加定时器是用来避免新增后的节点此时出现在其他位置以及数据不一致的现象
    if (!eqSet(prevProps.selectedNodes, this.props.selectedNodes)) {
      // 更新 toolbar
      const selectedId = [...this.props.selectedNodes][0];
      if (!selectedId) return;
      const selectNode = this.props.nodes.find(node => node.id === selectedId);
      if (!selectNode) return;
      mindmapEditor.emit('controller:edit:toggle');
      this.props.updateNodeToolbar(selectNode);
      setTimeout(() => {
        this.checkShouldScrollIntoView();
      });
    }
  }

  handleItem = (item, type) => {
    if (document.getElementById(item) && document.getElementById(item).firstChild) {
      const selectedNode = this.props.nodes.find(node => node.id === item);
      let selected;
      if (selectedNode.index === 0) {
        selected = this.props.classes.root;
      } else {
        selected = this.props.classes.default;
      }
      document.getElementById(item).firstChild.classList[type](selected);
    }
  };

  addSelectedNodeStyle = () => {
    if (this.props.selectedNodes.size) {
      __DEV__ && console.log('add addSelectedNodeStyle', this.props.selectedNodes);
      this.props.selectedNodes.forEach(item => this.handleItem(item, 'add'));
    }
  };

  enableSelectCheck = (curr, prev) => {
    if (curr.selectedNodes.size !== 1 || eqSet(prev.selectedNodes, curr.selectedNodes)) {
      return false;
    }
    return true;
  };

  checkShouldScrollIntoView = () => {
    if (this.props.selectedNodes.size !== 1) return;
    checkScrollIntoView([...this.props.selectedNodes][0]);
  };

  render() {
    return null;
  }
}
const styles = theme => ({
  root: {
    ...getTheme(theme).selected.root,
  },
  default: {
    ...getTheme(theme).selected.default,
  },
});
export default withStyles(styles)(NodeSelect);

