import _ from 'lodash';

// todo: The current Stack has no length limit.
class RedoStack {
  constructor() {
    this.reset();
  }

  canRedo() {
    return this.redoStack.length > 0;
  }

  pushToRedoStack(type, state) {
    if (_.isEqual(state, this.redoStack[0])) return;
    this.redoStack.unshift([type, state]);
  }

  redo() {
    return this.redoStack.shift();
  }

  reset() {
    this.redoStack = [];
  }
}

export const redoStack = new RedoStack();
window._redoStack = redoStack;
