import { CanvasPadding, __DEV__ } from '../../common/constants';

const TOOLBAR_HEIGHT = 50;
// 首先将画布移动到滚动条居中，然后再将中心节点尽量往中心节点移动
export function handleScrollToCenter(mindMapContainer, rootElement) {
  if (!mindMapContainer) return;
  const SCROLLWIDTH = 8;
  const viewPortWidth = window.innerWidth - SCROLLWIDTH;
  const viewPortHeight = window.innerHeight - TOOLBAR_HEIGHT - SCROLLWIDTH;

  /**
   * 根据目标节点当前的left 和 top 与移动到当前可视区宽高的一半为目标，调整滚动条
   * @param {element} source 容器
   * @param {element} target 想要定位到容器中点的目标元素
   */
  function scrollTocenter(source, target) {
    const { left, top, width, height } = target.getBoundingClientRect();
    const marginLeft = (viewPortWidth - width) / 2;
    const marginTop = (viewPortHeight - height) / 2;
    source.scrollLeft += Math.round(left - marginLeft);
    source.scrollTop += Math.round(top - marginTop);
  }

  scrollTocenter(mindMapContainer, rootElement);
}

let iframeHelper = null;
export function getHtml() {
  try {
    if (iframeHelper) {
      iframeHelper.remove();
      iframeHelper = null;
    }
    const mindmap = document.getElementById('mindmap-container').cloneNode(true);
    mindmap.querySelector('#editor').remove();
    mindmap.querySelector('#mindmap-inner').style.transform = null;
    const previewBodyHTML = mindmap.innerHTML;
    iframeHelper = document.createElement('iframe');
    iframeHelper.style.display = 'none';
    document.body.appendChild(iframeHelper);
    const contentDocument = iframeHelper.contentDocument;
    const iframeBody = contentDocument.body;
    const iframeHead = contentDocument.head;
    // 识别中文
    const meta = document.createElement('meta');
    meta.setAttribute('charset', 'utf-8');
    iframeHead.appendChild(meta);
    const styleTags = document.getElementsByTagName('style');
    [].forEach.call(styleTags, style => {
      const deepCopyStyle = style.cloneNode(true);
      iframeHead.appendChild(deepCopyStyle);
    });
    iframeBody.innerHTML = previewBodyHTML;
    // 特殊处理 markdown 的图标，防止导出异常
    // iframeBody.querySelectorAll('svg').forEach((item) => item.removeAttribute('height'));
    const result = contentDocument.documentElement.outerHTML;
    return result;
  } catch (err) {
    console.log(err);
    return '';
  }
}

export function getSize() {
  const canvas = document.querySelector('.mindmap-design');
  if (canvas) {
    return {
      width: canvas.offsetWidth,
      height: canvas.offsetHeight,
    };
  }
  return null;
}

export function placeCaretAtEnd(el) {
  if (!el) return;
  el.focus({ preventScroll: true });
  if (typeof window.getSelection !== 'undefined' && typeof document.createRange !== 'undefined') {
    const range = document.createRange();
    range.selectNodeContents(el);
    range.collapse(false);
    const sel = window.getSelection();
    sel.removeAllRanges();
    sel.addRange(range);
  } else if (typeof document.body.createTextRange !== 'undefined') {
    const textRange = document.body.createTextRange();
    textRange.moveToElementText(el);
    textRange.collapse(false);
    textRange.select();
  }
}

export function selectionAll(el) {
  if (!el) return;
  el.focus({ preventScroll: true });
  let sel;
  let range;
  if (window.getSelection && document.createRange) {
    // Browser compatibility
    sel = window.getSelection();
    if (sel.toString() === '') {
      // no text selection
      range = document.createRange(); // range object
      range.selectNodeContents(el); // sets Range
      sel.removeAllRanges(); // remove all ranges from selection
      sel.addRange(range); // add Range to a Selection.
    }
  } else if (document.selection) {
    // older ie
    sel = document.selection.createRange();
    if (sel.text === '') {
      // no text selection
      range = document.body.createTextRange(); // Creates TextRange object
      range.moveToElementText(el); // sets Range
      range.select(); // make selection.
    }
  }
}

export function checkInputValid(keyCode) {
  const valid =
    (keyCode > 47 && keyCode < 58) || // number keys
    keyCode === 32 ||
    keyCode === 229 || // 中文输入法
    keyCode === 13 || // spacebar & return key(s) (if you want to allow carriage returns)
    (keyCode > 64 && keyCode < 91) || // letter keys
    (keyCode > 95 && keyCode < 112) || // numpad keys
    (keyCode > 185 && keyCode < 193) || // ;=,-./` (in order)
    (keyCode > 218 && keyCode < 223); // [\]' (in order)
  return valid;
}

let lastRuleAreaSize = {};
export function updateCanvasSize(ruleArea) {
  __DEV__ && console.log('ruleArea', ruleArea);
  if (
    !__DEV__ &&
    (lastRuleAreaSize &&
      lastRuleAreaSize.width === parseInt(ruleArea.width, 10) &&
      lastRuleAreaSize.height === parseInt(ruleArea.height, 10))
  ) {
    return;
  }
  const innerWidth = window.innerWidth;
  const innerHeight = window.innerHeight;
  const mindDesign = document.querySelector('.mindmap-design');
  const svg = document.getElementById('mind-canvas');
  if (!svg) return;
  // if (ruleArea.width + basePadding < windowW) {
  const gapWidth = innerWidth - ruleArea.width;
  const gapHeight = innerHeight - ruleArea.height;
  let paddingTop = 100;
  let paddingLeft = 100;
  if (gapWidth > 0) {
    paddingLeft = Math.max(parseInt(gapWidth / 2, 10), paddingLeft);
  }
  if (gapHeight > 0) {
    paddingTop = Math.max(parseInt(gapHeight / 2, 10), paddingTop);
  }
  const lastPadding = `${paddingTop}px 100px 100px ${paddingLeft}px`;
  svg.style.width = `${ruleArea.width + CanvasPadding * 2}px`;
  svg.style.height = `${ruleArea.height + CanvasPadding * 2}px`;
  mindDesign.style.padding = lastPadding;
  mindDesign.style.width = `${Math.max(ruleArea.width + paddingLeft, innerWidth)}px`;

  // 设定最小宽度避免初始化时元素被挤小，导致布局错乱
  mindDesign.style.minWidth = '300px';
  // svg.style.height = `${Math.max(ruleArea.height + svgPadding, innerHeight)}px`;
  // 缓存
  lastRuleAreaSize = {
    width: parseInt(ruleArea.width, 10),
    height: parseInt(ruleArea.height, 10),
    padding: lastPadding,
  };
}

export function updateScalePadding() {
  if (!lastRuleAreaSize || !lastRuleAreaSize.padding) return;
  const mindDesign = document.querySelector('.mindmap-design');
  mindDesign.style.padding = lastRuleAreaSize.padding;
  const svgCanvas = document.getElementById('mind-canvas');
  try {
    svgCanvas.scrollIntoView();
    // eslint-disable-next-line no-empty
  } catch (e) {}
}
