import { pcBridge } from '../bridges';
import emitter from 'common/events';
import { __DEV__ } from '../common/constants';
import client from 'common/client';

class PC {
  constructor() {
    if (!PC.instance) {
      PC.instance = this;
    }
    this._symbol = `__YNOTEPC__`;
    this._currentLocation = null;
    return PC.instance;
  }

  openLink(link, isInnerLink) {
    if (isInnerLink) {
      // callnative api  OpenNote
      return pcBridge.OpenNote(link);
    }
    return pcBridge.OpenLink(link);
  }

  pasteNoteLink() {
    __DEV__ && console.log(`[controller]: pasteNoteLink`);
    pcBridge.PasteNoteLink();
  }

  emit(type, ...args) {
    __DEV__ && console.log(`[emit]`, type, ...args);
    emitter.emit(`${this._symbol}${type}`, ...args);
  }

  on(type, ...args) {
    __DEV__ && console.log(`[on]`, type, ...args);
    emitter.on(`${this._symbol}${type}`, ...args);
  }

  removeListener(type, ...args) {
    emitter.removeListener(`${this._symbol}${type}`, ...args);
  }

  set currentLocation({ left, top }) {
    this._currentLocation = `${left} ${top}`;
  }

  get currentLocation() {
    return this._currentLocation;
  }
}

const pc = new PC();
if (client.isPC()) {
  pc.on('check-paste-status', callback => {
    callback(pcBridge.ClipboardAvailable(), pcBridge.IsNoteLinkInClipboard());
  });
}

export default pc;
