import React from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';
import exact from 'prop-types-exact';
import storage from '../../common/storage';
import emitter from 'common/events';
import {
  __DEV__,
  INSERT_NODE,
  RESET_TOOLBAR,
  DELETE_NODE,
  SET_LINK,
  REDO_UNDO_LIST,
  TOGGLE_BOLD,
  TOGGLE_ITALIC,
  TOGGLE_UNDERLINE,
  TOGGLE_DELETELINE,
  EDIT_NODE,
  MOVE_NODE,
  FORCE_UPDATE_NODES,
  SET_FONT_FAMILY,
  SET_FONT_SIZE,
  SET_FONT_COLOR,
  PASTE_NODE,
  SET_REMARK,
  TOGGLE_NODE,
} from '../../common/constants';
import { wrapMindMapData, getPureMapData, removeStylePosition } from 'common/util';
import { undoStack } from 'common/undoStack';
import { redoStack } from 'common/redoStack';
import client from '../../common/client';
import { pcBridge } from '../../bridges/pc';
import { ErrorHandler } from '../../components';
import { handleScrollToCenter } from './dom';
import { getInitNodes } from '../../model/NodeContainer';
import MindmapEditor from '../../controllers/mindmapEditor';

const BASE_SCALE = 0.25;
const MAX_SCALE = 3;
const MIN_SCALE = 0.5;
const EMPTY_STRING = '';

export default class WrapperIndex extends React.Component {
  constructor(props) {
    super(props);
    this._initMindMapEditor();
    this.state = {
      error: false,
    };
    // -1 不标记； 0 初始状态
    this.pengdingUpdate = 0;
    this.shouldParse = true;
    this.isDidMount = false;
    this._updateListener = () => {
      this._updateMindMapLine();
    };
  }

  componentDidMount() {
    // 本地开发时自己触发 update
    // 线上的版本通过 setValue 触发
    if (__DEV__) {
      this._updateMindMapLine();
    }
    this.isDidMount = true;
    this.initMindmapData = true;
    mindmapEditor.on(FORCE_UPDATE_NODES, this._updateListener);
  }

  componentDidUpdate() {
    if (__DEV__) {
      storage.set('data', removeStylePosition(mindmapEditor.manager.nodesRenderArray));
    }
    this._afterInitMindmapData();
  }

  componentWillUnmount() {
    mindmapEditor.removeListener(FORCE_UPDATE_NODES, this._updateListener);
  }

  static getDerivedStateFromError(error) {
    return { error };
  }

  /**
   * ==== toolbar start =====
   */
  insertBrotherNode = () => {
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    if (selectedNodes.size !== 1) return;
    if (selectedNodes.has('root')) {
      return this.insertChildNode();
    }
    __DEV__ && console.log('[insertBrotherNode]');
    undoStack.pushToUndoStack(INSERT_NODE, nodeContainer.state.nodes);
    nodeContainer.insertBrotherNode(selectedNodes);
  };

  insertChildNode = () => {
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    if (!selectedNodes.size || selectedNodes.size > 1) return;
    const firstNode = [...selectedNodes][0];
    if (!firstNode) return;
    undoStack.pushToUndoStack(INSERT_NODE, nodeContainer.state.nodes);
    nodeContainer.insertChildNode(selectedNodes);
  };

  insertParentNode = () => {
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    if (selectedNodes.has('root') || selectedNodes.size > 1) return;
    undoStack.pushToUndoStack(INSERT_NODE, nodeContainer.state.nodes);
    nodeContainer.insertParentNode(selectedNodes);
  };

  toggleNode = id => {
    const { nodeContainer } = this.props;
    undoStack.pushToUndoStack(TOGGLE_NODE, nodeContainer.state.nodes);
    this.props.nodeContainer.toggleNode(id);
  };

  insertLink = link => {
    const { nodeContainer } = this.props;
    if (!link || !link.id) return;
    undoStack.pushToUndoStack(SET_LINK, nodeContainer.state.nodes);
    nodeContainer.insertLink({ link });
  };

  insertNoteLink = (link, name) => {
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    undoStack.pushToUndoStack(SET_LINK, nodeContainer.state.nodes);
    const id = [...selectedNodes][0];
    const newlink = {
      id,
      value: link,
      name,
      isInnerLink: true,
    };
    nodeContainer.insertNoteLink({ link: newlink });
  };

  removeLink = link => {
    const { nodeContainer } = this.props;
    undoStack.pushToUndoStack(SET_LINK, nodeContainer.state.nodes);
    nodeContainer.deleteLink({ link });
  };

  saveRemark = ({ id, value }) => {
    const { nodeContainer } = this.props;
    undoStack.pushToUndoStack(SET_REMARK, nodeContainer.state.nodes);
    this.props.nodeContainer.updateRemark(id, value);
  };

  deleteNode = () => {
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    if (selectedNodes.has('root')) return;
    undoStack.pushToUndoStack(DELETE_NODE, nodeContainer.state.nodes);
    nodeContainer.deleteNodes(selectedNodes);
  };

  moveNodes = (dragId, targetId) => {
    const { nodeContainer } = this.props;
    undoStack.pushToUndoStack(MOVE_NODE, nodeContainer.state.nodes);
    nodeContainer.moveNodes(dragId, targetId);
  };

  handleZoomIn = () => {
    const { toolbarContainer } = this.props;
    let zoom = toolbarContainer.state.toolbar.zoom;
    const newZoom = _.round(zoom + BASE_SCALE, 2);
    if (newZoom > MAX_SCALE) {
      return;
    }
    toolbarContainer.updateToolbarItem({ zoom: newZoom });
  };

  handleZoomOut = () => {
    const { toolbarContainer } = this.props;
    let zoom = toolbarContainer.state.toolbar.zoom;
    const newZoom = _.round(zoom - BASE_SCALE, 2);
    if (newZoom < MIN_SCALE) return;
    toolbarContainer.updateToolbarItem({ zoom: newZoom });
  };

  saveNodeTopic = (topic, node) => {
    const { nodeContainer } = this.props;
    undoStack.pushToUndoStack(EDIT_NODE, nodeContainer.state.nodes);
    nodeContainer.saveNodeTopic({ id: node.id, topic });
  };

  // 由于 redo undo 需要同步更新 YDManager 中的循环引用的模型，所以会有一定的损耗
  handleRedo = () => {
    const { nodeContainer } = this.props;
    if (!redoStack.canRedo() || this.isEditing) return;
    const [type, state] = redoStack.redo();
    if (REDO_UNDO_LIST.includes(type)) {
      undoStack.pushToUndoStack(type, nodeContainer.state.nodes);
      nodeContainer.updateNodes(state);
      nodeContainer.clearSelect();
      this.shouldParse = true;
    }
  };

  handleUndo = () => {
    const { nodeContainer } = this.props;
    if (!undoStack.canUndo() || this.isEditing) return;
    const [type, state] = undoStack.undo();
    if (REDO_UNDO_LIST.includes(type)) {
      redoStack.pushToRedoStack(type, nodeContainer.state.nodes);
      nodeContainer.updateNodes(state);
      nodeContainer.clearSelect();
      this.shouldParse = true;
    }
  };

  scrollToCenter = () => {
    const root = document.getElementById('root');
    const mindContainer = document.getElementById('mindmap-container');
    handleScrollToCenter(mindContainer, root);
  };

  handleLayoutStrategy = (strategy, lineType) => {
    mindmapEditor.shouldScrollToCenter = true;
    this.props.nodeContainer.handleLayoutStrategy(strategy, lineType);
    this.props.toolbarContainer.handleLayoutStrategy(strategy, lineType);
  };

  toggleItalic = () => {
    if (!this._canToggle()) return;
    const { nodeContainer, toolbarContainer } = this.props;
    let fontStyle = toolbarContainer.state.toolbar.fontStyle;
    fontStyle = fontStyle === 'italic' ? 'normal' : 'italic';
    toolbarContainer.updateToolbarItem({ fontStyle });
    const selectedNodes = nodeContainer.state.selectedNodes;
    if (!selectedNodes.size) return;
    undoStack.pushToUndoStack(TOGGLE_ITALIC, nodeContainer.state.nodes);
    nodeContainer.toggleItalic({ selectedNodes, fontStyle });
  };

  toggleDeleteline = () => {
    if (!this._canToggle()) return;
    const { nodeContainer, toolbarContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    if (!selectedNodes.size) return;
    const textDecoration = toolbarContainer.state.toolbar.textDecoration;
    toolbarContainer.toggleDeleteline();
    undoStack.pushToUndoStack(TOGGLE_DELETELINE, nodeContainer.state.nodes);
    nodeContainer.toggleDeleteline({ selectedNodes, textDecoration });
  };

  handleFontFamily = fontFamily => {
    const { nodeContainer, toolbarContainer } = this.props;
    if (toolbarContainer.state.toolbar.fontFamily === fontFamily) return;
    const { selectedNodes, nodes } = nodeContainer.state;
    if (!selectedNodes.size) return;
    undoStack.pushToUndoStack(SET_FONT_FAMILY, nodes);
    nodeContainer.setFontFamily({ fontFamily, selectedNodes });
    toolbarContainer.updateToolbarItem({ fontFamily });
  };

  handleColor = (selectedNodes, color) => {
    const { nodeContainer, toolbarContainer } = this.props;
    const { nodes } = nodeContainer.state;
    // 当鼠标点击外部时会触发清空选择节点，需要用户选择在颜色之后拿到上一次的选中节点
    undoStack.pushToUndoStack(SET_FONT_COLOR, nodes);
    nodeContainer.setFontColor({ color, selectedNodes });
    toolbarContainer.updateToolbarItem({ color });
  };

  handleFontSize = fontSize => {
    const { nodeContainer, toolbarContainer } = this.props;
    if (toolbarContainer.state.toolbar.fontSize === fontSize) return;
    const { selectedNodes, nodes } = nodeContainer.state;
    if (!selectedNodes.size) return;

    undoStack.pushToUndoStack(SET_FONT_SIZE, nodes);
    nodeContainer.setFontSize({ fontSize: `${fontSize}px`, selectedNodes });
    toolbarContainer.updateToolbarItem({ fontSize });
  };

  toggleUnderline = () => {
    if (!this._canToggle()) return;
    const { nodeContainer, toolbarContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    const textDecoration = toolbarContainer.state.toolbar.textDecoration;
    toolbarContainer.toggleUnderline();
    if (!selectedNodes.size) return;
    undoStack.pushToUndoStack(TOGGLE_UNDERLINE, nodeContainer.state.nodes);
    nodeContainer.toggleUnderline({ selectedNodes, textDecoration });
  };

  toggleBold = () => {
    if (!this._canToggle()) return;

    const { nodeContainer, toolbarContainer } = this.props;
    let fontWeight = toolbarContainer.state.toolbar.fontWeight;
    fontWeight = fontWeight === 'bold' ? 'normal' : 'bold';

    const selectedNodes = nodeContainer.state.selectedNodes;
    toolbarContainer.updateToolbarItem({ fontWeight });
    if (!selectedNodes.size) return;
    undoStack.pushToUndoStack(TOGGLE_BOLD, nodeContainer.state.nodes);
    nodeContainer.toggleBold({ selectedNodes, fontWeight });
  };

  /**
   * ==== toolbar end ====
   */

  /**
   * ==== arrow key start ===
   */

  arrowUp = () => {
    if (!this._arrowCheck()) return;
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    const nextNode = mindmapEditor.getPrevBrotherNode([...selectedNodes][0]);
    if (!nextNode) return;
    this.moveByShortCutKey = true;
    nodeContainer.selectNodes(nextNode.id);
  };

  arrowDown = () => {
    if (!this._arrowCheck()) return;
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    const nextNode = mindmapEditor.getNextBrotherNode([...selectedNodes][0]);
    if (!nextNode) return;
    nodeContainer.selectNodes(nextNode.id);
  };

  arrowRight = () => {
    if (!this._arrowCheck()) return;
    const { nodeContainer, toolbarContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    const firstNode = [...selectedNodes][0];
    let nextNode;
    if (
      mindmapEditor.manager.YMNodes[firstNode].direction === 2 &&
      ['logic_left', 'logic_left_right'].includes(toolbarContainer.state.toolbar.strategy)
    ) {
      nextNode = mindmapEditor.getParentNode([...selectedNodes][0]);
    } else {
      nextNode = mindmapEditor.getFirstChildNode(firstNode);
    }
    if (!nextNode) return;
    nodeContainer.selectNodes(nextNode.id);
  };

  arrowLeft = () => {
    if (!this._arrowCheck()) return;
    const { nodeContainer, toolbarContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    const firstNode = [...selectedNodes][0];
    let nextNode;

    // 如果是中心主题并且处于平衡布局
    if (firstNode === 'root' && toolbarContainer.state.toolbar.strategy === 'logic_left_right') {
      nextNode = mindmapEditor.getMiddleNode(firstNode);
    } else if (
      mindmapEditor.manager.YMNodes[firstNode].direction === 2 &&
      ['logic_left', 'logic_left_right'].includes(toolbarContainer.state.toolbar.strategy) &&
      firstNode.id !== 'root'
    ) {
      nextNode = mindmapEditor.getFirstChildNode([...selectedNodes][0]);
    } else {
      nextNode = mindmapEditor.getParentNode(firstNode);
    }
    if (!nextNode) return;
    nodeContainer.selectNodes(nextNode.id);
  };

  _arrowCheck = () => {
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    if (selectedNodes.size === 1) return true;
    return false;
  };

  /**
   * ==== arrow key end ===
   */
  pasteMultNodes = (selectedNodes, text) => {
    this.props.nodeContainer.pasteMultNodes(selectedNodes, text);
  };

  setValue = (data, readOnly = false) => {
    __DEV__ && console.log('call setValue');
    const containerEl = document.getElementById('mindmap-container');
    if (client.isPC() && containerEl) {
      containerEl.scrollLeft = 0;
      containerEl.scrollTop = 0;
    }
    if (this.state.error) {
      window.location.reload();
      return;
    }
    this.shouldParse = true;
    this.initMindmapData = true;
    this.pengdingUpdate = 0;
    mindmapEditor.reset();
    __DEV__ && console.log('start set content from client', data, readOnly);
    let renderNodes;
    let remarksData;
    let toolbar = { zoom: 1, lineType: 'default', strategy: 'logic_right' };
    let tempReadOnly;
    if (_.isEmpty(data)) {
      renderNodes = getInitNodes(true);
    } else {
      try {
        let mapData = JSON.parse(data);
        remarksData = mapData.remarks; // 只是为了让以前的老版本兼容
        renderNodes = mapData.nodes;
        toolbar = { ...toolbar, ...mapData.toolbar };
        tempReadOnly = mapData.readOnly;
      } catch (e) {
        this.setState({
          error: true,
        });
        console.error(e);
        return;
      }
    }

    mindmapEditor.readOnly = readOnly;

    // 1. 先 parse为 YMNode 节点
    mindmapEditor.parseData(wrapMindMapData(renderNodes));

    // 2. 设置布局策略以及主题
    mindmapEditor.injectStrategy(toolbar.strategy, toolbar.lineType);

    // v2 后数据全在 node 中，需要对 v2 之前的备注做转换
    if (!_.isEmpty(remarksData)) mindmapEditor._addRemarkMap(remarksData);

    const nodes = mindmapEditor.manager.getNodes();

    this.props.nodeContainer.updateNodes(nodes);
    this.props.nodeContainer.selectNodes('root');
    this._resetToolbar(toolbar, readOnly || tempReadOnly);

    // 重置错误状态
    if (this.state.error) {
      this.setState({
        error: false,
      });
    }
  };

  handlePaste = event => {
    if (client.isPC()) {
      pcBridge.OnPaste();
    }
    __DEV__ && console.log('handlePaste');
    const { nodeContainer } = this.props;
    const selectedNodes = nodeContainer.state.selectedNodes;
    if (mindmapEditor.isEditing || selectedNodes.size === 0) return;
    const { types, files } = event.clipboardData || event.originalEvent.clipboardData;
    const preventDefault = () => {
      event.preventDefault();
      event.stopPropagation();
    };
    // 粘贴的是如果资源文件统一放在 fileuploadcomponent 中
    if (files.length > 0) {
      preventDefault();
      return;
    }
    // 笔记内的图片附件粘贴 目前不支持
    if (types.join('').includes('yne')) {
      const image = event.clipboardData.getData('text/yne-image.json');
      // const resource = event.clipboardData.getData('text/yne-json');
      // 图片资源不进行 paste 目前不支持 禁止默认事件
      if (image && image.length) {
        preventDefault();
        return;
      }
    }

    // fix: TIM QQ
    if (types.includes('Files')) {
      preventDefault(event);
      return;
    }
    if (types.includes('text/plain')) {
      let text = event.clipboardData.getData('text/plain');
      if (mindmapEditor.pasteData) {
        text = mindmapEditor.pasteData;
      }
      undoStack.pushToUndoStack(PASTE_NODE, nodeContainer.state.nodes);
      mindmapEditor.pasteMultNodes(selectedNodes, text);

      // event.stopPropagation();
      // event.preventDefault();
      // 具体的字符串处理规则
      mindmapEditor.pasteData = null;
    }
  };

  getValue = () => {
    __DEV__ && console.log('call getValue');
    // 当数据异常时返回 null，外层拒绝保存本次数据
    const { nodeContainer, toolbarContainer } = this.props;
    const mapData = getPureMapData({
      nodes: nodeContainer.state.nodes,
      status: toolbarContainer.state,
    });
    if (!mapData) return EMPTY_STRING;
    if (client.isPC()) {
      const resourceList = mindmapEditor.getResources();
      mapData.resourceList = resourceList;
    }
    let finalData = EMPTY_STRING;
    try {
      finalData = JSON.stringify(mapData);
      // eslint-disable-next-line no-empty
    } catch (e) {}
    if (mindmapEditor.shouldDispatchSave) {
      setTimeout(() => {
        mindmapEditor.shouldDispatchSave = false;
      });
    }
    return finalData;
  };

  _resetToolbar = (toolbar, readOnly) => {
    const { toolbarContainer } = this.props;
    toolbarContainer.handleReadOnly(Boolean(readOnly));
    let payload = {
      ...toolbar,
      zoom: readOnly ? 1 : toolbar.zoom,
      loading: true,
    };
    toolbarContainer.updateToolbarItem(payload);
    emitter.emit(RESET_TOOLBAR);
  };

  _canToggle = () => {
    const selectedNodes = this.props.nodeContainer.state.selectedNodes;
    return !mindmapEditor.isEditing && selectedNodes.size !== 0;
  };

  _initMindMapEditor = () => {
    const mindmapEditor = new MindmapEditor(this.props.options);
    window.mindmapEditor = mindmapEditor;
    // 将 React 中可以引发视图变更的方法注入
    mindmapEditor.registerAPI(this);
  };

  _updateMindMapLine = () => {
    const { nodeContainer } = this.props;
    const wrapperData = wrapMindMapData(nodeContainer.state.nodes);
    if (this.shouldParse) {
      mindmapEditor.parseData(wrapperData);
      this.shouldParse = false;
    }
    nodeContainer.handleLayoutStrategy();
    if (this.pengdingUpdate === -1) {
      return;
    }
    this.pengdingUpdate++;
    if (this.pengdingUpdate >= 2) {
      this.props.toolbarContainer.setLoading(false);
      this.pengdingUpdate = -1;
      mindmapEditor.emit('controller:focus:editor');
    }
  };

  // 脑图首次初始化完成之后（即数据和坐标都计算正确）
  _afterInitMindmapData = () => {
    if (!this.initMindmapData) return;
    const { toolbarContainer } = this.props;
    // 调用客户端内容初始化完成的方法
    if (client.isPC() && !toolbarContainer.state.readOnly && !mindmapEditor.readOnly) {
      pcBridge.SetContentDone();
    }
    this.initMindmapData = false;
    this._updateMindMapLine();
  };

  render() {
    if (this.state.error) {
      return <ErrorHandler />;
    }
    return this.props.children;
  }
}

WrapperIndex.propTypes = exact({
  children: PropTypes.object,
  nodeContainer: PropTypes.object,
  toolbarContainer: PropTypes.object,
  options: PropTypes.object,
});
