import cs from '../Layout/childLayoutStrategy';
import rs from '../Layout/ruleLayoutStrategy/index';

const deepTraversal = (data, action) => {
  action(data);
  if (!data.children || !data.children.length) return;
  data.children.forEach(child => {
    deepTraversal(child, action);
  });
};

function logic_right(root) {
  const setStrategy = node => {
    if (!node.hasChild()) {
      node.direction = 1;
      return;
    }
    node.ruleArea.strategy = rs.rightCenter;
    node.childrenArea.strategy = cs.columnLeft;
    node.direction = 1;
  };
  deepTraversal(root, setStrategy);
}

function logic_left(root) {
  const setStrategy = node => {
    if (!node.hasChild()) {
      node.direction = 2;
      return;
    }
    node.ruleArea.strategy = rs.leftCenter;
    node.childrenArea.strategy = cs.columnRight;
    node.direction = 2;
  };
  deepTraversal(root, setStrategy);
}

function logic_left_right(root) {
  const setStrategy = node => {
    if (!node.hasChild()) {
      if (node.parent && node.parent.id === 'root') {
        const len = node.parent.children.length;
        const midIdx = Math.ceil(len / 2); // 前一半放左侧、后一半放右侧
        if (node.parent.children.indexOf(node) >= midIdx) {
          node.direction = 2;
        } else {
          node.direction = 1;
        }
      } else {
        node.direction = node.isRoot() ? 1 : node.parent.direction;
      }
      return;
    }
    if (node.isRoot()) {
      node.ruleArea.strategy = rs.center_lr;
      node.childrenArea.strategy = cs.both_sides_h;
    } else if (node.index === 1) {
      const len = node.parent.children.length;
      const midIdx = Math.ceil(len / 2); // 前一半放左侧、后一半放右侧
      if (node.parent.children.indexOf(node) >= midIdx) {
        node.ruleArea.strategy = rs.leftCenter;
        node.childrenArea.strategy = cs.columnRight;
        node.direction = 2;
      } else {
        node.ruleArea.strategy = rs.rightCenter;
        node.childrenArea.strategy = cs.columnLeft;
        node.direction = 1;
      }
    } else {
      node.direction = node.parent.direction;
      node.ruleArea.strategy = node.parent.ruleArea.strategy;
      node.childrenArea.strategy = node.parent.childrenArea.strategy;
    }
  };
  deepTraversal(root, setStrategy);
}

function logic_bottom(root) {
  const setStrategy = node => {
    node.ruleArea.strategy = rs.bottomCenter;
    node.childrenArea.strategy = cs.rowTop;
  };
  deepTraversal(root, setStrategy);
}

function logic_bottom_catalog(root) {
  const setStrategy = node => {
    if (node.hasChild()) {
      if (node.isRoot()) {
        node.ruleArea.strategy = rs.bottomCenter;
        node.childrenArea.strategy = cs.rowTop;
      } else {
        node.ruleArea.strategy = rs.fallDiagonal;
        node.childrenArea.strategy = cs.columnLeft;
      }
    }
  };
  deepTraversal(root, setStrategy);
}

function logic_top(root) {
  const setStrategy = node => {
    if (node.hasChild()) {
      node.ruleArea.strategy = rs.topCenter;
      node.childrenArea.strategy = cs.rowBottom;
    }
  };
  deepTraversal(root, setStrategy);
}

function logic_top_bottom(root) {
  const setStrategy = node => {
    if (node.hasChild()) {
      if (node.isRoot()) {
        node.ruleArea.strategy = rs.center_tb;
        node.childrenArea.strategy = cs.both_sides_v;
      } else if (node.index === 1) {
        const len = node.parent.children.length,
          midIdx = Math.ceil(len / 2);
        if (node.parent.children.indexOf(node) >= midIdx) {
          node.ruleArea.strategy = rs.topCenter;
          node.childrenArea.strategy = cs.rowBottom;
        } else {
          node.ruleArea.strategy = rs.bottomCenter;
          node.childrenArea.strategy = cs.rowTop;
        }
      } else {
        node.ruleArea.strategy = node.parent.ruleArea.strategy;
        node.childrenArea.strategy = node.parent.childrenArea.strategy;
      }
    }
  };
  deepTraversal(root, setStrategy);
}

function fish_bone_right(root) {
  const setStrategy = node => {
    if (node.hasChild()) {
      if (node.isRoot()) {
        node.ruleArea.strategy = rs.rightFishBone;
        node.childrenArea.strategy = cs.fishbone_right;
      } else if (node.index === 1) {
        const midIdx = Math.ceil(node.parent.children.length / 2);
        node.ruleArea.strategy =
          node.parent.children.indexOf(node) >= midIdx ? rs.fallDiagonal : rs.riseDiagonal;
        node.childrenArea.strategy = cs.fishbone_right;
      } else if (node.index === 2 || node.index === 4) {
        node.ruleArea.strategy =
          node.parent.ruleArea.strategy === rs.riseDiagonal ? rs.fallDiagonal : rs.riseDiagonal;
        node.childrenArea.strategy = cs.columnLeft;
      } else {
        node.ruleArea.strategy = node.parent.ruleArea.strategy;
        node.childrenArea.strategy = node.parent.childrenArea.strategy;
      }
    }
  };
  deepTraversal(root, setStrategy);
}

const layoutStrategyMap = {
  logic_right,
  logic_left,
  logic_bottom,
  logic_top,
  logic_left_right,
  logic_bottom_catalog,
  logic_top_bottom,
  // fish_bone_right,
};

export default layoutStrategyMap;
