import getLinesArray from './getLinesArray';
// prettier-ignore

const logicRight = manager => {
  return getLinesArray(manager.YMNodesArray, manager.lineType, 'right');
};

const logicLeft = manager => getLinesArray(manager.YMNodesArray, manager.lineType, 'left');

const logicLeftRight = manager => {
  const rootLeft = manager._getRootPartChildren('left');
  const rootRight = manager._getRootPartChildren('right');

  const linesArrayLeft = getLinesArray(rootLeft, manager.lineType, 'left');

  const linesArrayRight = getLinesArray(rootRight, manager.lineType, 'right');
  linesArrayLeft.push(...linesArrayRight);
  return linesArrayLeft;
};

const logicBottom = manager => getLinesArray(manager.YMNodesArray, manager.lineType, 'bottom');

const logicBottomCatalog = manager =>
  getLinesArray(manager.YMNodesArray, manager.lineType, 'bottom_catalog');

const logicTop = manager => getLinesArray(manager.YMNodesArray, manager.lineType, 'top');

const lineStrategy = {
  logic_right: logicRight,
  logic_left: logicLeft,
  logic_left_right: logicLeftRight,
  logic_bottom: logicBottom,
  logic_bottom_catalog: logicBottomCatalog,
  logic_top: logicTop,
  // logic_top_bottom,
  // fish_bone_right,
};

export default lineStrategy;
