import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { TextArea, ClickAwayListener } from '../../components';
import { withStyles } from '@material-ui/styles';
import { __DEV__, KEYCODE_TAB } from '../../common/constants';

class RemarkComponent extends PureComponent {
  static propTypes = {
    saveRemark: PropTypes.func.isRequired,
    id: PropTypes.string,
    value: PropTypes.string,
    noTitle: PropTypes.bool,
    readOnly: PropTypes.bool,
    autoFocus: PropTypes.bool,
    onBlur: PropTypes.func,
    classes: PropTypes.object,
  };
  static defaultProps = {
    noTitle: false,
    readOnly: false,
    autoFocus: false,
  };
  constructor(props) {
    super(props);
    this.state = {
      value: props.value,
      initValue: props.value || '',
    };
    this._cache = {};
  }

  saveRemark = () => {
    // blur 的时候可能下一个节点的数据已经进来了，或者拿不到上一次的数据。所以要保留上一次选中的状态
    // let forceUpdate = false;
    // if (
    //   (!this.state.initValue && this._cache.value) ||
    //   (this.state.initValue && !this._cache.value)
    // ) {
    //   forceUpdate = true;
    // }
    if (!this._cache.value && !this.state.initValue) return;
    if (this._cache.value === this.state.initValue) {
      __DEV__ && console.log('ignore save remark');
      return;
    }
    this.props.saveRemark(this._cache);
    this._cache = {};
  };

  handleChange = (e, { value }) => {
    const { id } = this.props;
    this.setState({
      value,
    });
    this._cache = {
      value,
      id,
    };
  };

  handleClick = () => {
    mindmapEditor.__uistatus__.focus = 'remark';
  };

  handleBlur = () => {
    this.saveRemark();
    const { onBlur } = this.props;
    // eslint-disable-next-line no-unused-expressions
    onBlur && onBlur();
  };

  handleKeyDown = event => {
    if (event.keyCode === KEYCODE_TAB) {
      event.preventDefault();
      event.target.blur();
    }
  };
  render() {
    const { value } = this.state;
    /* eslint-disable no-unused-vars */
    const { noTitle, saveRemark, classes, id, readOnly, autoFocus, ...rest } = this.props;
    return (
      <ClickAwayListener onClickAway={this.handleBlur}>
        <div className={classes.root} {...rest}>
          {!noTitle && <span className={classes.title}>备注</span>}
          {!noTitle && <div className={classes.hr} />}
          <TextArea
            rows={5}
            placeholder="在此输入备注"
            onChange={this.handleChange}
            onBlur={this.handleBlur}
            value={value}
            disabled={readOnly}
            autoFocus={autoFocus}
            onClick={this.handleClick}
            onKeyDown={this.handleKeyDown}
          />
        </div>
      </ClickAwayListener>
    );
  }
}

const styles = {
  root: {
    width: '300px',
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    background: 'white',
    borderRadius: '0.5em',
    boxSizing: 'border-box',
    padding: '16px',
    boxShadow: '0 4px 16px 0 rgba(102, 139, 195, 0.23)',
    '&.readOnly': {
      pointerEvents: 'none',
    },
    '& textarea': {
      width: '100%',
      resize: 'none',
      border: '0',
      background: '#fff',
    },
  },
  title: {
    fontSize: '14px',
    color: 'rgb(51, 51, 51)',
  },
  hr: {
    borderTop: '1px solid rgb(230, 230, 230)',
    margin: '14px 0',
  },
};
export default withStyles(styles)(RemarkComponent);
