/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { InnerLink } from 'components';
import { makeStyles } from '@material-ui/styles';

const stopPropagation = event => event.stopPropagation();

export default class LinkItemPreview extends PureComponent {
  static propTypes = {
    link: PropTypes.object,
    readOnly: PropTypes.bool,
    linkPreivewRef: PropTypes.func,
    toggleEditLink: PropTypes.func,
    submitLink: PropTypes.func,
    removeLink: PropTypes.func,
    openLink: PropTypes.func,
    getEditStatus: PropTypes.func,
    onMouseLeave: PropTypes.func,
  };
  constructor(props) {
    super(props);
    this.innerLinkRef = React.createRef();
    this.state = {
      edit: false,
    };
  }

  componentDidMount() {
    this.props.getEditStatus(this.state.edit);
  }

  componentDidUpdate() {
    if (this.state.edit) {
      this.innerLinkRef.current.querySelector('input').focus();
    }
  }

  editLink = () => {
    this.setState(
      {
        edit: true,
      },
      () => this.props.toggleEditLink(true),
    );
  };

  submitLink = (event, link) => {
    stopPropagation(event);
    this.setState(
      {
        edit: false,
      },
      () => this.props.toggleEditLink(false),
    );
    // 如果在节点中的内链清空，删除内链
    if (!link.value) return this.removeLink();
    this.props.submitLink(link);
  };

  removeLink = () => {
    this.props.removeLink(this.props.link);
  };

  getStyle = type => {
    const arr = ['inherit', 'none'];
    const style = {
      display: this.state.edit ? arr[type] : arr.reverse()[type],
    };
    return style;
  };

  handleMouseLeave = e => {
    if (this.state.edit) {
      return;
    }
    this.props.onMouseLeave(e);
  };
  /* eslint-disable no-unused-vars */
  render() {
    const {
      link,
      openLink,
      readOnly,
      linkPreivewRef,
      submitLink,
      removeLink,
      getEditStatus,
      toggleEditLink,
      // eslint-disable-next-line react/prop-types
      onMouseLeave,
      ...rest
    } = this.props;
    return (
      <div ref={linkPreivewRef} {...rest} onMouseLeave={this.handleMouseLeave}>
        <div style={this.getStyle(0)}>
          <InnerLink ref={this.innerLinkRef} id={link.id} link={link} onClick={this.submitLink} />
        </div>
        <div style={this.getStyle(1)}>
          <LinkPreview
            editLink={this.editLink}
            removeLink={this.removeLink}
            link={link}
            openLink={openLink}
            readOnly={readOnly}
          />
        </div>
      </div>
    );
  }
}

const linkContainer = {
  padding: '16px 24px',
  background: '#fff',
  boxShadow: '0 1px 16px rgba(90,109,122,.4)',
};

const linkSource = {
  width: '234px',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
  whiteSpace: 'nowrap',
  color: '#4C94FF',
  lineHeight: '20px',
  cursor: 'pointer',
};
const linkForm = {
  display: 'flex',
  alignItems: 'center',
  fontSize: '14px',
};
const useStyles = makeStyles({
  root: {
    ...linkContainer,
    ...linkForm,
    padding: '13px 24px',
  },
  linkSource,
  linkAction: {
    marginLeft: '24px',
    '& span': {
      color: '#878787',
      cursor: 'pointer',
      '&:hover': {
        color: '#333333',
      },
    },
  },
  gap: {
    padding: ' 0 12px',
  },
});

let LinkPreview = props => {
  const style = {
    display: props.show ? 'inherit' : 'hidden',
  };
  const classes = useStyles();
  return (
    <div style={style} className={classes.root} onDoubleClick={stopPropagation}>
      <React.Fragment>
        <p className={classes.linkSource} onClick={props.openLink}>
          {props.link.value}
        </p>
        {!props.readOnly && (
          <div className={classes.linkAction}>
            <span onClick={props.editLink}>编辑</span>
            <span className={classes.gap}>|</span>
            <span onClick={props.removeLink}>移除</span>
          </div>
        )}
      </React.Fragment>
    </div>
  );
};
LinkPreview = React.memo(LinkPreview);

LinkPreview.propTypes = {
  removeLink: PropTypes.func,
  editLink: PropTypes.func,
  openLink: PropTypes.func,
  link: PropTypes.object,
  readOnly: PropTypes.bool,
  show: PropTypes.bool,
};
