import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import Popper from '../Popper';
import RootRef from '../RootRef';

const styles = {
  padding: '3px 8px',
  background: '#505256',
  borderRadius: '1px',
  color: '#fff',
  fontSize: '12px',
  lineHeight: '16px',
  marginTop: '10px',
};

export default class Tooltip extends PureComponent {
  constructor(props) {
    super();
    this.isControlled = props.open !== undefined;
    this.state = {
      open: null,
    };
    if (!this.isControlled) {
      // not controlled, use internal state
      this.state.open = false;
    }
  }

  componentDidMount() {
    if (this.props.open) {
      this.forceUpdate();
    }
  }

  componentWillUnmount() {
    this.clearMouseTimeout();
  }

  onRootRef = ref => {
    this.childrenRef = ref;
  };

  handleEnter = event => {
    const { children, enterDelay } = this.props;
    const childrenProps = children.props;
    if (event.type === 'mouseover' && childrenProps.onMouseOver) {
      childrenProps.onMouseOver(event);
    }

    this.childrenRef.setAttribute('title', '');
    this.clearMouseTimeout();
    if (enterDelay) {
      event.persist();
      this.enterTimer = setTimeout(() => {
        this.handleOpen(event);
      }, enterDelay);
    } else {
      this.handleOpen(event);
    }
  };

  handleOpen = event => {
    if (!this.isControlled && !this.state.open) {
      this.setState({ open: true });
    }

    if (this.props.onOpen) {
      this.props.onOpen(event);
    }
  };

  handleLeave = event => {
    const { children, leaveDelay } = this.props;
    const childrenProps = children.props;
    if (event.type === 'mouseleave' && children.onMouseLeave) {
      childrenProps.onMouseLeave(event);
    }
    this.clearMouseTimeout();
    if (leaveDelay) {
      event.persist();
      this.leaveTimer = setTimeout(() => {
        this.handleClose(event);
      }, leaveDelay);
    } else {
      this.handleClose(event);
    }
  };

  handleClose = event => {
    if (!this.isControlled) {
      this.setState({ open: false });
    }

    if (this.props.onClose) {
      this.props.onClose(event);
    }
  };

  clearMouseTimeout = () => {
    clearTimeout(this.enterTimer);
    clearTimeout(this.leaveTimer);
  };

  render() {
    const {
      title,
      open: openProp,
      disableHoverListener,
      enterDelay,
      leaveDelay,
      placement,
      children,
      ...others
    } = this.props;
    let open = this.isControlled ? openProp : this.state.open;
    if (title === '') {
      open = false;
    }
    const childrenProps = {
      title: !open && typeof title === 'string' ? title : null,
      ...others,
    };
    if (!disableHoverListener) {
      childrenProps.onMouseOver = this.handleEnter;
      childrenProps.onMouseLeave = this.handleLeave;
    }
    return (
      <React.Fragment>
        <RootRef rootRef={this.onRootRef}>{React.cloneElement(children, childrenProps)}</RootRef>
        <Popper
          anchorEl={this.childrenRef}
          open={open}
          placement={placement}
          style={{ zIndex: 1000 }}
        >
          <div style={styles}>{title}</div>
        </Popper>
      </React.Fragment>
    );
  }
}
Tooltip.defaultProps = {
  disableHoverListener: false,
  enterDelay: 0,
  leaveDelay: 0,
  placement: 'bottom',
};

Tooltip.propTypes = {
  title: PropTypes.string.isRequired,
  disableHoverListener: PropTypes.bool,
  leaveDelay: PropTypes.number,
  enterDelay: PropTypes.number,
  children: PropTypes.object.isRequired,
  placement: PropTypes.string,
  onClose: PropTypes.func,
  onOpen: PropTypes.func,
  open: PropTypes.bool,
};
