import React from 'react';
import styled from 'styled-components';
import { Check as CheckIcon } from '../Icons';

const BaseInput = styled.input`
  cursor: inherit;
  position: absolute;
  opacity: 0;
  width: 16px;
  height: 16px;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  z-index: 1;
`;

const NotChecked = styled.span`
  width: 16px;
  height: 16px;
  border-radius: 2px;
  border: 1px solid rgba(229, 229, 229, 1);
  margin-right: 6px;
`;

const Checked = styled.span`
  width: 16px;
  height: 16px;
  background: rgba(76, 148, 255, 1);
  border-radius: 2px;
  margin-right: 6px;
  cursor: inherit;
`;

const StyledLabel = styled.label`
  display: inline-flex;
  margin-bottom: 8px;
  cursor: inherit;
`;

export default props => {
  const { checked, label, ...others } = props;
  return (
    <StyledLabel htmlFor={label} {...others}>
      {checked ? (
        <Checked>
          <CheckIcon />
        </Checked>
      ) : (
        <NotChecked />
      )}
      <BaseInput type="checkbox" id={label} />
      {label}
    </StyledLabel>
  );
};
