import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import exact from 'prop-types-exact';
import NodeItem from '../NodeItem';
import { pcController } from 'controllers';
import client from 'common/client';
import { __DEV__, FORCE_UPDATE_NODES } from '../../../common/constants';

class NodeList extends PureComponent {
  componentDidUpdate() {
    this.updateMindmap();
  }

  updateMindmap = () => {
    if (mindmapEditor.markUpdateCoordinate) {
      mindmapEditor.emit(FORCE_UPDATE_NODES);
      mindmapEditor.markUpdateCoordinate = false;
    }
    //  else if (mindmapEditor.shouldScrollToCenter) {
    // mindmapEditor.scrollToCenter();
    // mindmapEditor.shouldScrollToCenter = false;
    // }
  };

  /**
   * return 表示是否允许出现右键菜单 如果点击了节点，则展示菜单
   */
  handleContextMenu = () => {
    if (client.isPC()) {
      pcController.emit('check-paste-status', (isClipboardAvailable, isNoteLinkInClipboard) => {
        __DEV__ && console.log('isClipboardAvailable', isClipboardAvailable);
        __DEV__ && console.log('isNoteLinkInClipboard', isNoteLinkInClipboard);
        if (isNoteLinkInClipboard) {
          // 允许粘贴内链
          pcController.emit('controller:paste:link', true);
        } else {
          pcController.emit('controller:paste:link', false);
        }
      });
      return true;
    }
    // 发送消息给云笔记
    mindmapEditor.emit('controller:paste:innerlink', (id, name) => {
      if (id && name) {
        mindmapEditor.emit('controller:paste:link', id, name);
      }
    });
    return true;
  };

  handleSelectMultNodes = nodeId => {
    this.props.nodeContainer.toggleSelectNodes(nodeId);
  };

  handleSelectSingleNode = nodeId => {
    this.props.nodeContainer.selectNodes(nodeId);
    mindmapEditor.emit('controller:focus:editor');
  };

  handleNodeClick = node => event => {
    if (event.metaKey || event.ctrlKey) {
      this.handleSelectMultNodes(node.id);
    } else {
      this.handleSelectSingleNode(node.id);
    }
    event.stopPropagation();
  };

  handleNodeDoubleClick = event => {
    if (this.props.toolbarContainer.state.readOnly) return;
    if (!mindmapEditor.isEditing) {
      mindmapEditor.emit('controller:edit:toggle', 'show');
    }
    event.stopPropagation();
  };

  handleNodeContextMenu = nodeId => event => {
    this.handleContextMenu();
    this.handleSelectSingleNode(nodeId);
    // 避免右键+ctrl 时出现菜单选项
    if (event.ctrlKey || event.metaKey) {
      event.stopPropagation();
    }
    mindmapEditor.emit('controller:contextmenu:trigger', nodeId);
    event.preventDefault();
  };

  render() {
    const {
      nodeContainer: {
        state: { nodes },
      },
    } = this.props;
    const readOnly = this.props.toolbarContainer.state.readOnly;
    return (
      <div style={{ zIndex: 100 }} id="map-nodes">
        {nodes.map(node => {
          return (
            <NodeItem
              key={node.id}
              node={node}
              readOnly={readOnly}
              onClick={this.handleNodeClick}
              onContextMenu={this.handleNodeContextMenu}
              onDoubleClick={this.handleNodeDoubleClick}
            />
          );
        })}
      </div>
    );
  }
}

NodeList.propTypes = exact({
  nodeContainer: PropTypes.object,
  toolbarContainer: PropTypes.object,
});
export default NodeList;
