import React, { Component } from 'react';
import PropTypes from 'prop-types';
import exact from 'prop-types-exact';
import NodeList from '../NodeList';
import NodeGlobalHover from '../NodeGlobalHover';
import DragNode from './DragNode';
import SvgLine from '../SvgLine';
import DragSelection from './DragSelection';
import NodeSelect from '../NodeSelect';
import _ from 'lodash';
import ShortCutKey from '../ShortCutKey';
import NodeEditor from '../NodeEditor';

export default class CoreCanvas extends Component {
  /**
   * 鼠标划选区多选
   */
  handleSelection = domArr => {
    if (mindmapEditor.isEditing || !domArr || domArr.length === 0) return;
    const result = domArr.map(dom => dom.id);
    if (result.length) {
      this.props.nodeContainer.selectNodes(result);
      if (result.length === 1) {
        mindmapEditor.emit('controller:focus:editor');
      }
    }
  };

  handleDrop = (dragId, targetId) => {
    if (!dragId || !targetId) return;
    mindmapEditor.moveNodes(dragId, targetId);
  };

  handleBlur = (event, node) => {
    if (_.isEmpty(node)) return;
    if (node.topic === event.target.textContent) {
      return;
    }
    mindmapEditor.saveNodeTopic(event.target.textContent, node);
  };

  render() {
    const { nodeContainer, toolbarContainer } = this.props;
    const nodes = nodeContainer.state.nodes;
    const lines = nodeContainer.state.lines;
    const { zoom } = toolbarContainer.state.toolbar;
    const { selectedNodes, ...restState } = nodeContainer.state;
    const mindMapInnerStyle = {
      transform: `scale(${zoom})`,
      position: 'relative',
      transformOrigin: 'left top',
    };
    let currentNode = {};
    let currentNodeStyle = {};
    const disableEdit = selectedNodes.size > 1;
    if (selectedNodes.size >= 1) {
      currentNode = nodes.find(item => item.id === [...selectedNodes][0]) || {};
      if (currentNode && currentNode.style) {
        // eslint-disable-next-line no-unused-vars
        const { visibility, ...rest } = currentNode.style;
        currentNodeStyle = {
          ...rest,
        };
      }
    }
    const listProps = {
      selectNodes: nodeContainer.selectNodes.bind(nodeContainer),
      toggleSelectNodes: nodeContainer.toggleSelectNodes.bind(nodeContainer),
      state: { ...restState },
    };
    const updateNodeToolbar = toolbarContainer.updateNodeToolbar.bind(toolbarContainer);
    return (
      <div className="mindmap-design">
        <div id="mindmap-inner" style={mindMapInnerStyle}>
          <DragSelection
            zoom={zoom}
            handleSelection={this.handleSelection}
            disabled={toolbarContainer.state.readOnly}
          >
            {<SvgLine lines={lines} />}
            <NodeGlobalHover>
              <NodeList toolbarContainer={toolbarContainer} nodeContainer={listProps} />
            </NodeGlobalHover>
            {!mindmapEditor.readOnly && (
              <React.Fragment>
                {/** 用于鼠标划过多选 */}
                <NodeSelect
                  selectedNodes={selectedNodes}
                  nodes={nodeContainer.state.nodes}
                  // eslint-disable-next-line react/jsx-no-bind
                  updateNodeToolbar={updateNodeToolbar}
                />
                {/** 高亮选中的节点 */}

                {/** 用于节点的拖拽 */}
                <DragNode nodes={nodes} onDrop={this.handleDrop} />
                <ShortCutKey selectedNodes={nodeContainer.state.selectedNodes} nodes={nodes} />
                {!toolbarContainer.state.readOnly && (
                  // 当用户选中一个节点时，该编辑节点位置随选中节点调整
                  // 当用户双击时，该编辑节点显示
                  // 当用户直接输入键盘事件时，check 键盘事件后再决定是否显示
                  <NodeEditor
                    node={currentNode}
                    onBlur={this.handleBlur}
                    style={currentNodeStyle}
                    disableEdit={disableEdit}
                    zoom={zoom}
                  />
                )}
              </React.Fragment>
            )}
          </DragSelection>
        </div>
      </div>
    );
  }
}

CoreCanvas.propTypes = exact({
  nodeContainer: PropTypes.object,
  toolbarContainer: PropTypes.object,
});
