import {
  getFirstLayerTop,
  getFirstLayerCenterY,
  getFirstLayerBottom,
  getFirstLayerLeft,
  getFirstLayerCenterX,
  getFirstLayerRight,
} from './getFirstLayerParam';
// 左右布局
export const setFirstLayerTop2ParentBottom = (node, baseType = 'bottom') => {
  const firstLayerTop = getFirstLayerTop(node, baseType);
  node.childrenArea.start.y = 0 - (firstLayerTop - node.selfArea.height);
};

export const setFirstLayerTop2ParentCenter = (node, baseType = 'bottom') => {
  const firstLayerTop = getFirstLayerTop(node, baseType);
  node.childrenArea.start.y = 0 - (firstLayerTop - node.selfArea.height / 2);
};

export const setFirstLayerTop2ParentTop = (node, baseType = 'bottom') => {
  const firstLayerTop = getFirstLayerTop(node, baseType);
  node.childrenArea.start.y = 0 - firstLayerTop;
};

