import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { DoubleClickTime, __DEV__ } from 'common/constants';
import escapeHtml from 'escape-html';
import { KEYCODE_ENTER, KEYCODE_SPACE, KEYCODE_TAB } from '../../../common/constants';
import { placeCaretAtEnd, selectionAll, checkInputValid } from '../dom';
import { isShortcutKey } from '../../../common/util';
import styled, { css } from 'styled-components';

const StyledEdit = styled.div`
  position: absolute;
  background: white;
  color: black;
  max-width: 388px;
  z-index: -1;
  opacity: 0;
  box-sizing: border-box;
  word-wrap: break-word;
  white-space: normal;
  word-break: break-all;
  min-width: 16px;
  min-height: 16px;
  outline: none;
  user-select: text;
  ${props =>
    props.editing &&
    css`
      z-index: 1000;
      visibility: visible !important;
      opacity: 1;
    `};
`;
let touchTime = Date.now();
const addPx = num => `${num}px`;
/**
 * 待完善部分
 * 应该是两个节点
 * 父节点对应整体显示的宽高
 * 子节点对应内容区域宽高
 */

class NodeEditor extends Component {
  static propTypes = {
    onBlur: PropTypes.func,
    node: PropTypes.object,
    disableEdit: PropTypes.bool,
    zoom: PropTypes.number,
    style: PropTypes.object,
  };

  state = {
    editing: false,
  };
  shouldSelectAll = true;
  containerRef = React.createRef();

  componentDidMount() {
    mindmapEditor.on('controller:edit:toggle', this.handleEditToggle);
    mindmapEditor.on('controller:focus:editor', this.setFocus);
  }

  componentDidUpdate() {
    if (mindmapEditor.shouldDispatchSave) {
      setTimeout(() => {
        if (
          document.activeElement &&
          !['textarea', 'input'].includes(document.activeElement.tagName.toLowerCase()) &&
          document.hasFocus()
        ) {
          this.setFocus();
        }
      });
    }
  }

  componentWillUnmount() {
    mindmapEditor.removeListener('controller:edit:toggle', this.handleEditToggle);
    mindmapEditor.removeListener('controller:focus:editor', this.setFocus);
  }

  handleBlur = event => {
    __DEV__ && console.log('set Blur');
    this._hidden();
    this.props.onBlur && this.props.onBlur(event, this.props.node);
  };

  handleKeyDown = event => {
    __DEV__ && console.log('handleKeyDown', event.keyCode);
    // 如果处于编辑态的节点输入 enter 时收起节点
    if (mindmapEditor.isEditing && [KEYCODE_ENTER, KEYCODE_TAB].includes(event.keyCode)) {
      this.handleBlur(event);
      event.preventDefault();
      return;
    }

    // 如果多选的时候，对所有特殊的操作都先取消默认操作
    if (this.props.disableEdit) {
      event.preventDefault();
    }

    // 注入快捷键，当 match 时阻止默认事件。前提是需要判断该状态下快捷键是否生效
    const map = mindmapEditor.shortCutKeyMap;
    const has = 'hasOwnProperty';
    for (let key in map) {
      if (!map[has](key)) continue;
      if (isShortcutKey(event, key)) {
        let fn = map[key];
        // 除了进入到快捷键之后，还要判断此时状态下是否允许快捷键操作 具体的快捷键见 ShortCutKey
        if (fn.__status__ && fn.__status__()) {
          fn(event);
          event.preventDefault();
        }
        return;
      }
    }

    if (
      !this.props.disableEdit &&
      !mindmapEditor.isEditing && // 如果之前不是编辑态
      !event.metaKey && // 没有多重按键
      !event.ctrlKey && //
      checkInputValid(event.keyCode) && // 有效按键
      this.props.node && // 有 id
      this.props.node.id
    ) {
      this._show();
    }
  };

  setFocus = () => {
    // 避免当删除一个节点后 下次选中的 event.target.value 为空。所以加定时器
    if (this.containerRef.current === document.activeElement && mindmapEditor.isEditing) return;
    setTimeout(() => {
      if (!this.containerRef.current) {
        return;
      }
      this._calculateStyle();
      selectionAll(this.containerRef.current);
    });
  };

  _show = () => {
    mindmapEditor.isEditing = true;
    mindmapEditor.emit('controller:update:toolbar');
    this.setState(
      {
        editing: true,
      },
      () => {
        this._calculateStyle();
        this.setFocus();
      },
    );
  };

  _hidden = () => {
    this.setState({
      editing: false,
    });
    mindmapEditor.isEditing = false;
  };

  handleEditToggle = (type = 'close') => {
    if (type === 'show') {
      this._show();
    } else {
      this._hidden();
    }
  };

  _calculateStyle = () => {
    // 针对 root 样式特别定制
    const { style: nodePosition } = this.props;
    const id = this.props.node.id;
    const trueNode = document.getElementById(id);
    if (!trueNode) return;
    // 如果用户放大缩小，还得对应处理下
    let { width, height } = trueNode.getBoundingClientRect();
    // 覆盖底部宽度
    width = Math.floor(parseInt(width, 10) / this.props.zoom) + 1;
    height = Math.floor(parseInt(height, 10) / this.props.zoom);
    let style = this.containerRef.current.style;
    // // min: 避免用户删除文字后显示被覆盖的节点
    const pendingStyle = {
      minWidth: addPx(width),
      minHeight: addPx(height),
      ...nodePosition,
    };

    trueNode.firstChild.classList.forEach(item => {
      this.containerRef.current.classList.add(item);
    });
    Object.assign(style, {
      ...pendingStyle,
    });
  };

  handleTouchEnd = node => e => {
    if (Date.now() - touchTime < DoubleClickTime) {
      // 双击事件
      this.handleDoubleClick(node)(e);
    } else {
      touchTime = Date.now();
      this._selectNode(node);
    }
  };

  // 编辑态时节点的的纯文本处理
  handlePaste = event => {
    event.stopPropagation();
    event.preventDefault();
    if (!mindmapEditor.isEditing) {
      mindmapEditor.handlePaste(event);
      return;
    }
    // fix TIM QQ
    if (event.clipboardData.types.includes('Files')) {
      return;
    }
    let text = event.clipboardData.getData('text/plain');
    if (text) {
      text = text.replace(/\n/gi, '');
    }
    document.execCommand('insertHTML', false, text);
  };

  render() {
    const { node } = this.props;
    const { editing } = this.state;
    return (
      <StyledEdit
        tabIndex={-1}
        contentEditable={true}
        id="editor"
        spellCheck={false}
        onBlur={this.handleBlur}
        onKeyDown={this.handleKeyDown}
        onPaste={this.handlePaste}
        ref={this.containerRef}
        editing={editing}
        // eslint-disable-next-line react/no-danger
        dangerouslySetInnerHTML={{ __html: escapeHtml(node.topic || ' ') }}
        // eslint-disable-next-line jsx-a11y/no-autofocus
        autoFocus={true}
      />
    );
  }
}

export default NodeEditor;
