import _ from 'lodash';

// todo: The current Stack has no length limit.
class UndoStack {
  constructor() {
    this.reset();
  }

  canUndo() {
    return this.undoStack.length > 0;
  }

  pushToUndoStack(type, state) {
    if (_.isEqual(state, this.undoStack[0])) return;
    this.undoStack.unshift([type, state]);
  }

  undo() {
    return this.undoStack.shift();
  }

  reset() {
    this.undoStack = [];
  }
}

export const undoStack = new UndoStack();
window._undoStack = undoStack;
