import React from 'react';
import PropTypes from 'prop-types';
import client from '../../../common/client';
import { pcController } from 'controllers';
import DocumentEvents from 'react-document-events';
import { pcBridge } from '../../../bridges/pc';

export default class PCEventListener extends React.Component {
  static propTypes = {
    children: PropTypes.object,
    toolbarContainer: PropTypes.object,
  };

  componentDidMount() {
    if (client.isPC()) {
      pcController.on('controller:set:zoom', this.handleZoomPrecent);
      pcController.on('controller:focus:editor', this.focusEditor);
      pcController.on('controller:set:content', mindmapEditor.setValue);
      pcController.on('controller:insert:notelink', mindmapEditor.insertNoteLink);
    }
  }
  componentWillUnmount() {
    if (client.isPC()) {
      pcController.removeListener('controller:set:zoom', this.handleZoomPrecent);
      pcController.removeListener('controller:focus:editor', this.focusEditor);
      pcController.removeListener('controller:set:content', mindmapEditor.setValue);
      pcController.removeListener('controller:insert:notelink', mindmapEditor.insertNoteLink);
    }
  }

  handleZoomPrecent = zoom => {
    if (zoom > 0) {
      mindmapEditor.handleZoomIn();
    } else {
      mindmapEditor.handleZoomOut();
    }
  };

  handleContextMenu = event => {
    event.stopPropagation();
    event.preventDefault();
  };

  handleDocumentClick = event => {
    if (client.isPC() && !mindmapEditor.readOnly) {
      pcBridge.OnAreaClick();
    }
  };

  focusEditor = () => {
    setTimeout(() => {
      document.getElementById('editor') && document.getElementById('editor').focus();
    }, 1000);
  };

  render() {
    const { children = null } = this.props;
    return <DocumentEvents onClick={this.handleDocumentClick}>{children}</DocumentEvents>;
  }
}
