import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { __DEV__ } from 'common/constants';
const inputStyle = {
  color: 'transparent',
};
const hiddenStyle = {
  display: 'none',
};
export default class FileUpload extends Component {
  static propTypes = {
    hidden: PropTypes.bool,
    getResource: PropTypes.func,
  };
  handleChange = ({ target }) => {
    __DEV__ && console.log(target.files[0]);
    const file = target.files[0];
    if (!file.type.match('image')) {
      return __DEV__ && console.error('只能上传图片');
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = arg => {
      const result = arg.target.result;
      this.props.getResource && this.props.getResource(result);
      // const image = '<img class="preview" src="' + arg.target.result + '" alt="preview"/>';
      // const div = document.createElement('div');
      // div.innerHTML = image;
      // document.body.appendChild(div);
    };
  };
  render() {
    return (
      <input
        id="upload-image"
        type="file"
        style={this.props.hidden ? hiddenStyle : inputStyle}
        accept="image/*"
        onChange={this.handleChange}
      />
    );
  }
}
