import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import LinkItemPreview from '../LinkItemPreview';
import { Popper } from 'components';
import DocumentEvents from 'react-document-events';
import { HyperLink as HyperLinkIcon, InnerLink as InnerLinkIcon } from 'components/Icons';
import { __DEV__ } from '../../../../common/constants';

const iconNodeGap = {
  marginLeft: '6px',
  display: 'inline-block',
};

class LinkItem extends PureComponent {
  state = {
    open: false,
  };

  elementRef = React.createRef();

  componentDidMount() {
    this.elementRef.current.addEventListener('mouseleave', this.handleMouseLeave);
    this.elementRef.current.addEventListener('mouseenter', this.handleMouseEnter);
  }

  openLink = () => {
    mindmapEditor.openLink(this.props.link);
  };

  toggleShow = event => {
    this.leaveTimeout = null;
    this.enterTimeout = null;
    if (event && this.shouldShow) {
      this.setState({
        open: true,
      });
      this.triggerRender(event.target);
      return;
    }
    this.setState({
      open: false,
    });
    this.triggerUnMount();
  };

  handleRemoveLink = link => {
    mindmapEditor.removeLink(link);
    this.setHidden();
  };

  triggerRender = anchorEl => {
    const { link, readOnly } = this.props;
    mindmapEditor.emit('controller:render:component', {
      component: (
        <Popper
          open={Boolean(!link.name)}
          anchorEl={anchorEl}
          placement="bottom"
          style={{ zIndex: 1000 }}
        >
          <LinkItemPreview
            link={link}
            openLink={this.openLink}
            readOnly={readOnly}
            linkPreivewRef={this.getLinkItemRef}
            toggleEditLink={this.toggleEditLink}
            getEditStatus={this.getEditStatusFromChild}
            removeLink={this.handleRemoveLink}
            submitLink={mindmapEditor.insertLink}
            onMouseLeave={this.linkMouseLeave}
            onMouseEnter={this.linkMouseEnter}
            style={{ marginTop: -20, paddingTop: 20 }}
          />
          <DocumentEvents onMouseDown={this.handleOutSideClick} />
        </Popper>
      ),
      triggerElement: anchorEl,
    });
  };

  triggerUnMount = () => {
    mindmapEditor.emit('controller:unmount:component');
  };

  toggleEditLink = edit => {
    if (edit === false) {
      this.setHidden();
    } else {
      mindmapEditor.__uistatus__.focus = 'link';
    }
  };

  handleOutSideClick = event => {
    // 如果点击的是 icon 或者是元素内部，那么不关闭弹窗
    if (
      this.elementRef.current.contains(event.target) ||
      (this.linkItemRef && this.linkItemRef.contains(event.target))
    ) {
      return;
    }
    this.setHidden();
  };

  setHidden = () => {
    mindmapEditor.__uistatus__.focus = '';
    this.shouldShow = false;
    this.toggleShow();
  };

  handleMouseLeave = () => {
    this.shouldShow = false;
    __DEV__ && console.log('mouseleave', this.shouldShow);
    if (mindmapEditor.__uistatus__.focus) return;
    if (this.getEditStatusFromChild()) return;
    if (this.enterTimeout) return;
    this.leaveTimeout = setTimeout(() => this.toggleShow(), 500);
  };

  handleMouseEnter = event => {
    this.shouldShow = true;
    // prettier-ignore
    if (this.getEditStatusFromChild() || this.state.open || mindmapEditor.__uistatus__.focus) return;
    if (this.leaveTimeout) return;
    this.enterTimeout = setTimeout(() => this.toggleShow(event), 500);
  };

  linkMouseEnter = () => {
    this.shouldShow = true;
    mindmapEditor.__uistatus__.focus = 'link';
  };

  linkMouseLeave = () => {
    this.shouldShow = false;
    if (this.getEditStatusFromChild()) return;
    this.leaveTimeout = setTimeout(() => this.toggleShow(), 500);
  };

  linkIconClick = event => {
    this.toggleShow(event);
    this.openLink();
  };
  // eslint-disable-next-line no-return-assign
  getLinkItemRef = ref => (this.linkItemRef = ref);

  getEditStatusFromChild = edit => {
    return edit;
  };

  render() {
    const { link, style } = this.props;

    if (!link || !link.id) return null;
    return (
      <div ref={this.elementRef} style={{ ...style, ...iconNodeGap }}>
        <LinkIcon onClick={this.linkIconClick} isNoteLink={!!link.name} />
      </div>
    );
  }
}

LinkItem.propTypes = {
  link: PropTypes.object,
  readOnly: PropTypes.bool,
  style: PropTypes.object,
};

const LinkIcon = ({ onClick, isNoteLink }) => (
  <EnterLeave
    render={({ hover }) =>
      !isNoteLink ? (
        <HyperLinkIcon hover={hover} onClick={onClick} />
      ) : (
        <InnerLinkIcon hover={hover} onClick={onClick} />
      )
    }
  />
);
LinkIcon.propTypes = {
  onClick: PropTypes.func,
  isNoteLink: PropTypes.bool,
};

class EnterLeave extends PureComponent {
  state = {
    hover: false,
  };
  onMouseEnter = event => {
    this.setState({
      hover: true,
    });
    event.stopPropagation();
  };

  onMouseLeave = event => {
    this.setState({
      hover: false,
    });
    event.stopPropagation();
  };

  render() {
    const defaultEvents = {
      onMouseEnter: this.onMouseEnter,
      onMouseLeave: this.onMouseLeave,
    };
    return (
      <div style={{ height: '100%' }} {...defaultEvents}>
        {this.props.render(this.state)}
      </div>
    );
  }
}
EnterLeave.propTypes = {
  render: PropTypes.func,
};

export default LinkItem;
