import { hs_lr, rhs_lr, vs_lr, hs_tb, vs_tb, rvs_tb } from '../constant';
// childrenArea的布局策略
// 即计算子节点ruleArea的起点

function columnLeft(node) {
  // 纵向排列左对齐
  const children = node.children;
  if (!children.length) return;
  let baseX = 0,
    baseY = 0;
  for (let i = 0; i < children.length; i++) {
    let child = children[i];
    // 计算每个孩子ruleArea的宽高
    child.doLayout();
    // 计算每个孩子ruleArea的起点
    child.ruleArea.start.x = 0;
    child.ruleArea.start.y = baseY;

    baseY += child.ruleArea.height + vs_lr;
  }
}

function columnRight(node) {
  // 纵向排列右对齐
  const children = node.children;
  if (!children.length) return;
  let baseX = 0,
    baseY = 0;

  let wChildrenArea = 0;
  for (let i = 0; i < children.length; i++) {
    let child = children[i];
    child.doLayout();
    wChildrenArea = Math.max(child.ruleArea.width, wChildrenArea);
  }

  for (let i = 0; i < children.length; i++) {
    let child = children[i];
    child.ruleArea.start.x = wChildrenArea - child.ruleArea.width;
    child.ruleArea.start.y = baseY;
    baseY += child.ruleArea.height + vs_lr;
  }
}

function rowTop(node) {
  // 横向排列上对齐
  const children = node.children;
  if (!children.length) return;
  let baseX = 0,
    baseY = 0;
  for (let i = 0; i < children.length; i++) {
    let child = children[i];
    // 计算每个孩子ruleArea的宽高
    child.doLayout();
    // 计算每个孩子ruleArea的起点
    child.ruleArea.start.x = baseX;
    child.ruleArea.start.y = 0;

    baseX += child.ruleArea.width + hs_tb;
  }
}

function rowBottom(node) {
  // 横向排列下对齐
  const children = node.children;
  if (!children.length) return;
  let baseX = 0,
    baseY = 0;

  let hChildrenAre = 0;
  for (let i = 0; i < children.length; i++) {
    let child = children[i];
    child.doLayout();
    hChildrenAre = Math.max(hChildrenAre, child.ruleArea.height);
  }

  for (let i = 0; i < children.length; i++) {
    let child = children[i];
    child.ruleArea.start.x = baseX;
    child.ruleArea.start.y = hChildrenAre - child.ruleArea.height;
    baseX += child.ruleArea.width + hs_tb;
  }
}

function both_sides_h(node) {
  // 横向两侧排列
  const children = node.children;
  if (!children.length) return;
  let leftNodes = [],
    rightNodes = [],
    len = children.length,
    midIdx = Math.ceil(len / 2);
  for (let i = 0; i < len; i++) {
    let child = children[i];
    child.doLayout();
    if (i >= midIdx) {
      leftNodes.push(child);
    } else {
      rightNodes.push(child);
    }
  }
  // 计算左右两侧宽高及childrenArea宽高
  let wl = 0,
    hl = 0,
    wr = 0,
    hr = 0;

  for (let i = 0; i < leftNodes.length; i++) {
    let node = leftNodes[i];
    wl = wl > node.ruleArea.width ? wl : node.ruleArea.width;
    hl += node.ruleArea.height;
  }

  for (let i = 0; i < rightNodes.length; i++) {
    let node = rightNodes[i];
    wr = wr > node.ruleArea.width ? wr : node.ruleArea.width;
    hr += node.ruleArea.height;
  }

  let wChildrenArea = wl + wr + 2 * rhs_lr + node.selfArea.width,
    baseYl = 0,
    baseYr = 0;

  for (let i = 0; i < leftNodes.length; i++) {
    let child = leftNodes[i];
    child.ruleArea.start.x = wl - child.ruleArea.width;
    child.ruleArea.start.y = baseYl;
    baseYl += child.ruleArea.height + vs_lr;
  }

  for (let i = 0; i < rightNodes.length; i++) {
    let child = rightNodes[i];
    child.ruleArea.start.x = wChildrenArea - wr;
    child.ruleArea.start.y = baseYr;
    baseYr += child.ruleArea.height + vs_lr;
  }

  const selfAreaCenterY = (nodes, idx) => {
    return (
      nodes[idx].ruleArea.start.y + nodes[idx].selfArea.start.y + nodes[idx].selfArea.height / 2
    );
  };

  const getBaseLineY = nodes => {
    const { length } = nodes;
    return (selfAreaCenterY(nodes, 0) + selfAreaCenterY(nodes, length - 1)) / 2;
  };

  const rightBaseLineY = getBaseLineY(rightNodes);
  const leftBaseLineY = leftNodes.length ? getBaseLineY(leftNodes) : 0;

  const delta = rightBaseLineY - leftBaseLineY;

  if (delta > 0) {
    for (let i = 0; i < leftNodes.length; i++) {
      let child = leftNodes[i];
      child.ruleArea.start.y += delta;
    }
  } else {
    for (let i = 0; i < rightNodes.length; i++) {
      let child = rightNodes[i];
      // delta此时小于0，是 -= 不是 +=
      child.ruleArea.start.y -= delta;
    }
  }

  node.childrenArea.partArea.left = {
    width: wl,
    height: hl,
    YMNodesArray: leftNodes,
  };

  node.childrenArea.partArea.right = {
    width: wr,
    height: hr,
    YMNodesArray: rightNodes,
  };

  node.childrenArea.partArea.baseLine = delta > 0 ? rightBaseLineY : leftBaseLineY;
}

function both_sides_v(node) {
  // 纵向两侧排列
  const children = node.children;
  if (!children.length) return;
  let topNodes = [],
    bottomNodes = [],
    len = children.length,
    midIdx = Math.ceil(len / 2);
  for (let i = 0; i < len; i++) {
    let child = children[i];
    child.doLayout();
    if (i >= midIdx) {
      topNodes.push(child);
    } else {
      bottomNodes.push(child);
    }
  }
  // 计算上下两侧宽高及childrenArea宽高
  let ht = 0,
    wt = 0,
    hb = 0,
    wb = 0;

  for (let i = 0; i < topNodes.length; i++) {
    let node = topNodes[i];
    wt += node.ruleArea.width;
    ht = ht > node.ruleArea.height ? ht : node.ruleArea.height;
  }

  for (let i = 0; i < bottomNodes.length; i++) {
    let node = bottomNodes[i];
    wb += node.ruleArea.width;
    hb = hb > node.ruleArea.height ? hb : node.ruleArea.height;
  }

  let wChildrenArea = Math.max(wt, wb),
    hChildrenArea = ht + hb + 2 * vs_lr + node.selfArea.height,
    baseXt = 0,
    baseXb = 0;

  for (let i = 0; i < topNodes.length; i++) {
    let child = topNodes[i];
    child.ruleArea.start.x = (wChildrenArea - wt) / 2 + baseXt;
    child.ruleArea.start.y = ht - child.ruleArea.height;
    baseXt += child.ruleArea.width + hs_lr;
  }

  for (let i = 0; i < bottomNodes.length; i++) {
    let child = bottomNodes[i];
    child.ruleArea.start.x = (wChildrenArea - wb) / 2 + baseXb;
    child.ruleArea.start.y = hChildrenArea - hb;
    baseXb += child.ruleArea.width + hs_lr;
  }

  node.childrenArea.partArea.top = {
    width: wt,
    height: ht,
    YMNodesArray: topNodes,
  };

  node.childrenArea.partArea.bottom = {
    width: wb,
    height: hb,
    YMNodesArray: bottomNodes,
  };
}

function fishbone_right(node) {
  const children = node.children;
  if (!children.length) return;
  let bottomNodes = [],
    topNodes = [],
    len = children.length,
    midIdx = Math.ceil(len / 2);
  for (let i = 0; i < len; i++) {
    let child = children[i];
    child.doLayout();
    if (i >= midIdx) {
      bottomNodes.push(child);
    } else {
      topNodes.push(child);
    }
  }

  let fishGap = hs_lr + 5,
    baseXt = fishGap,
    baseXb = fishGap + 5,
    ht = 0,
    hb = 0;
  for (let i = 0; i < topNodes.length; i++) {
    let child = topNodes[i];
    ht = ht > child.ruleArea.height ? ht : child.ruleArea.height;
    child.ruleArea.start.x = baseXt;
    child.ruleArea.start.y = -1 * fishGap - child.ruleArea.height;
    baseXt += child.ruleArea.width + hs_lr;
  }
  for (let i = 0; i < bottomNodes.length; i++) {
    let child = bottomNodes[i];
    hb = hb > child.ruleArea.height ? hb : child.ruleArea.height;
    child.ruleArea.start.x = baseXb;
    child.ruleArea.start.y = fishGap;
    baseXb += child.ruleArea.width + hs_lr;
  }

  children.forEach(child => (child.ruleArea.start.y += fishGap + ht));
  node.childrenArea.partArea.top.height = fishGap + ht;
}

function rootRender(root) {
  // 针对root的特殊方法，node只能是root
  // 计算root的ruleArea的起点
  root.ruleArea.start.x = root.selfArea.start.x;
  root.ruleArea.start.y = root.selfArea.start.y;
}

const cs = {
  columnLeft,
  columnRight,
  rowTop,
  rowBottom,
  both_sides_h,
  // both_sides_v,
  // fishbone_right,
  rootRender,
};

export default cs;
