import defaultTheme from './default';
import simple from './simple';

const getRole = index => {
  switch (index) {
    case 0:
      return 'root';
    case 1:
      return 'main';
    case 2:
      return 'sub';
    case 3:
      return 'third';
    default:
      return 'default';
  }
};

export const getCustomStyleWithTheme = (role, theme) => {
  if (theme.normal[role]) {
    return theme.normal[role] || theme.normal.default;
  }
};

const getSelectedStyleWithTheme = (index, theme) => {
  const role = getRole(index);
  return theme.selected[role] || theme.selected.default;
};

export const getTheme = configTheme => {
  switch (configTheme) {
    case 'simple': {
      return simple;
    }
    default:
      return defaultTheme;
  }
};

export const getThemeStyle = (index, configTheme = 'default') => {
  const role = getRole(index);
  const theme = getTheme(configTheme);
  return getCustomStyleWithTheme(role, theme);
};

export const getStroke = (index, configTheme = 'default') => {
  const theme = getTheme(configTheme);
  const role = getRole(index - 1); // 线条的 index 需要 +1 才能和节点 index 匹配
  return {
    strokeWidth: theme.strokeStyle.strokeWidth[role],
  };
};

export const getSelectedStyle = (index, configTheme = 'default') => {
  const theme = getTheme(configTheme);
  return getSelectedStyleWithTheme(index, theme);
};

const getFontTheme = (index, theme) => {
  const role = getRole(index);
  const style = theme.normal[role] || theme.normal.default;
  return {
    fontSize: style.fontSize,
    fontFamily: style.fontFamily,
  };
};

export const getThemeFontFamily = configTheme => {
  const theme = getTheme(configTheme);
  return theme.fontFamily;
};

export const getFontStyle = (index, configTheme = 'default') => {
  const theme = getTheme(configTheme);
  return getFontTheme(index, theme);
};
