import React, { useCallback } from 'react';
import { KEYCODE_ENTER } from 'common/constants';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles({
  root: {
    padding: '16px 24px',
    background: '#fff',
    boxShadow: '0 1px 16px rgba(90,109,122,.4)',
  },
  form: {
    display: 'flex',
    alignItems: 'center',
    fontSize: '14px',
  },
  label: {
    fontSize: '1em',
    color: '#797979',
  },
  button: {
    padding: '6px 22px',
    fontSize: '1em',
    backgroundColor: '#5576BD',
    border: 'none',
    borderRadius: '0px  2px  2px  0px',
    color: '#fff',
    outline: 'none',
    cursor: 'pointer',
    height: '32px',
    '&:hover': {
      backgroundColor: '#4966A3',
    },
  },
  input: {
    width: '176px',
    height: '32px',
    boxSizing: 'border-box',
    border: '1px solid #e5e5e5',
    padding: '0',
    outline: 'none',
    textIndent: '1em',
    marginLeft: '24px',
    '&:hover': {
      borderColor: '#999999',
    },
    '&:focus': {
      borderColor: '#4D94FF',
    },
  },
});

const stopPropagation = event => {
  event.stopPropagation();
};
const InnerLink = React.forwardRef((props, ref) => {
  const inputRef = React.createRef();
  const getLink = () => ({
    value: inputRef && inputRef.current.value.trim(),
    id: props.id,
  });
  const onClick = e => {
    const link = getLink();
    if (link.value && !link.value.startsWith('http')) {
      link.value = `http://${link.value}`;
    }
    if (props.onClick) props.onClick(e, link);
  };

  const selfClick = useCallback(() => {
    mindmapEditor.__uistatus__.focus = 'link';
  });

  const handleKeydown = useCallback(e => {
    if (e.keyCode === KEYCODE_ENTER) {
      onClick(e);
      e.preventDefault();
      stopPropagation(e);
    }
  });
  const classes = useStyles();
  return (
    <div className={classes.root} ref={ref}>
      {/* 本来用 form，但是点击之后会触发窗口重新刷新，需要禁止掉 form 中 submit 默认事件，所以改用 div */}
      <div className={classes.form}>
        <span className={classes.label}>链接</span>
        <input
          placeholder="输入链接"
          className={classes.input}
          ref={inputRef}
          // eslint-disable-next-line jsx-a11y/no-autofocus
          autoFocus={true}
          onKeyDown={handleKeydown}
          onDoubleClick={stopPropagation}
          onClick={selfClick}
          defaultValue={props.link ? props.link.value : props.value || ''}
        />
        <button className={classes.button} onClick={onClick} type="button">
          确定
        </button>
      </div>
    </div>
  );
});
InnerLink.propTypes = {
  onClick: PropTypes.func,
  id: PropTypes.string,
  link: PropTypes.object,
  value: PropTypes.string,
};
export default React.memo(InnerLink);
