import { isMacOS, __DEV__ } from './constants';
import KEYMAP from './keymap';
import _ from 'lodash';

function shallowEqualArguments(prevArguments, nextArguments) {
  if (!prevArguments || prevArguments.length !== nextArguments.length) return false;
  for (let i = 0, length = prevArguments.length; i < length; i++) {
    if (prevArguments[i] !== nextArguments[i]) return false;
  }
  return true;
}

// it's only for array
export function immutableTransFromCache(transformFunc) {
  let cacheResult;
  let cacheArguments;
  return function () {
    if (!shallowEqualArguments(cacheArguments, arguments)) {
      cacheResult = transformFunc(...arguments);
      cacheArguments = arguments;
    }
    return cacheResult;
  };
}

export function getUnhandleProps(Component, props, blackList) {
  const { handleProps = [] } = Component;
  return Object.keys(props).reduce((acc, prop) => {
    if (prop === 'childKey' || _.has(blackList, prop)) return acc;
    if (!handleProps.includes(prop)) acc[prop] = props[prop];
    return acc;
  }, {});
}

/**
 * 不能只提取必要的数据，这会引起老版本兼容性问题：
 * 比如A版本客户端没有 link 的数据支持，但是B版本支持 link 数据。
 * 如果此时A版本客户端拿到了B版本客户端分享的脑图数据，但是那么A版本没有 link数据，在A版本保存数据的时候会丢失 link 字段。
 */
export function extractDataFromNodes(nodes) {
  return nodes.map(node => {
    // 这几个属性是可以不要的，所以就过滤掉了
    // eslint-disable-next-line no-unused-vars
    const { index, offset, direction, style, expanderStyle, view, ...rest } = node;
    return {
      ...rest,
      style: {
        visibility: style ? style.visibility : 'visible',
      },
    };
  });
}

export function deepCopy(target) {
  try {
    return JSON.parse(JSON.stringify(target));
  } catch (e) {
    __DEV__ && console.error(e);
  }
}

export const muteEvent = callback => event => {
  event.stopPropagation();
  event.preventDefault();
  callback && callback(event);
};

export const shortKeyHandle = callback => event => {
  event.stopPropagation();
  event.preventDefault();
  if (isMacOS && !event.metaKey) return;
  if (!isMacOS && !event.ctrlKey) return;
  callback && callback(event);
};

export function getParameter(paramName) {
  const searchString = window.location.search.substring(1);
  let i;
  let val;
  const params = searchString.split('&');

  for (i = 0; i < params.length; i++) {
    val = params[i].split('=');
    if (val[0].toLowerCase() === paramName.toLowerCase()) {
      return val[1].split('?')[0];
    }
  }
  return null;
}

export function compareVersion(v1, v2) {
  let i = 0;

  let t1;

  let t2;

  let a1 = v1.split('.');

  let a2 = v2.split('.');
  const TrueValue = true;
  while (TrueValue) {
    if (i === a1.length || i === a2.length) {
      return a1.length >= a2.length;
    }
    t1 = Number(a1[i]);
    t2 = Number(a2[i]);

    if (t1 > t2) {
      return TrueValue;
    }
    if (t1 < t2) {
      return !TrueValue;
    }
    i++;
  }
}

const hasOwn = Object.prototype.hasOwnProperty;

export function shallowEqual(objA, objB) {
  // 首先对基本数据类型的比较
  if (Object.is(objA, objB)) return true;
  // 由于Obejct.is()可以对基本数据类型做一个精确的比较， 所以如果不等
  // 只有一种情况是误判的，那就是object,所以在判断两个对象都不是object
  // 之后，就可以返回false了
  if (typeof objA !== 'object' || objA === null || typeof objB !== 'object' || objB === null) {
    return false;
  }

  // 过滤掉基本数据类型之后，就是对对象的比较了
  // 首先拿出key值，对key的长度进行对比

  const keysA = Object.keys(objA);
  const keysB = Object.keys(objB);

  // 长度不等直接返回false
  if (keysA.length !== keysB.length) return false;
  // key相等的情况下，在去循环比较
  for (let i = 0; i < keysA.length; i++) {
    // key值相等的时候
    // 借用原型链上真正的 hasOwnProperty 方法，判断ObjB里面是否有A的key的key值
    // 属性的顺序不影响结果也就是{name:'daisy', age:'24'} 跟{age:'24'，name:'daisy' }是一样的
    // 最后，对对象的value进行一个基本数据类型的比较，返回结果
    if (!hasOwn.call(objB, keysA[i]) || !Object.is(objA[keysA[i]], objB[keysA[i]])) {
      return false;
    }
  }

  return true;
}

export function splitFileName(text) {
  let pattern = /\.{1}[a-z]{1,}$/;
  if (pattern.exec(text) !== null) {
    return text.slice(0, pattern.exec(text).index);
  } else {
    return text;
  }
}

/**
 * 计算包含 meta 键的 keycode
 *  @param  {String|KeyEvent}
 */
function getMetaKeyCode(e) {
  let CTRL_MASK = 0x1000;
  let ALT_MASK = 0x2000;
  let SHIFT_MASK = 0x4000;
  let metaKeyCode = 0;

  if (typeof e === 'string') {
    // e as string
    e.toLowerCase()
      .split(/\+\s*/)
      .forEach(name => {
        switch (name) {
          case 'ctrl':
          case 'cmd':
            metaKeyCode |= CTRL_MASK;
            break;
          case 'alt':
            metaKeyCode |= ALT_MASK;
            break;
          case 'shift':
            metaKeyCode |= SHIFT_MASK;
            break;
          default:
            metaKeyCode |= KEYMAP[name];
        }
      });
  } else {
    // e as key event
    if (e.ctrlKey || e.metaKey) {
      metaKeyCode |= CTRL_MASK;
    }
    if (e.altKey) {
      metaKeyCode |= ALT_MASK;
    }
    if (e.shiftKey) {
      metaKeyCode |= SHIFT_MASK;
    }
    metaKeyCode |= e.keyCode;
  }

  return metaKeyCode;
}

export function isShortcutKey(e, keyCombine) {
  if (!e) return false;
  return getMetaKeyCode(keyCombine) === getMetaKeyCode(e);
}

export function getOuterWidth(el) {
  let width = el.offsetWidth;
  let style = getComputedStyle(el);

  width += parseInt(style.marginLeft, 10) + parseInt(style.marginRight, 10);
  return width;
}

export function getMaxCanvasSize(id) {
  // 16384 * 16384
  const MaxSize = 16384;
  const { width, height } = document.getElementById(id);
  const ifOuterCanvas = (Math.min(width, MaxSize) && Math.min(height, MaxSize)) === MaxSize;
  if (ifOuterCanvas) return false;
  const twoScale = (Math.max(width, MaxSize / 2) && Math.max(height, MaxSize / 2)) === MaxSize / 2;
  if (twoScale) return 2;
  const threeScale =
    (Math.max(width, MaxSize / 3) && Math.max(height, MaxSize / 3)) === MaxSize / 3;
  if (threeScale) return 3;
  return 1;
}

export function removeStylePosition(nodes) {
  return nodes.map(node => {
    const {
      // eslint-disable-next-line no-unused-vars
      style: { left, top, visibility },
      ...rest
    } = node;
    return { ...rest, style: { visibility } };
  });
}

export function wrapMindMapData(nodes, format) {
  return {
    data: nodes,
    format: format || 'node_array',
  };
}

function mapRemarks(nodes) {
  let remarks = {};
  nodes.forEach(item => {
    if (item.remark) {
      remarks[item.id] = item.remark;
    }
  });
  return remarks;
}

/**
 * nodes // 节点数据
 * readOnly  // 是否可读
 * toolbar // 工具栏数据  zoom strategy theme
 * resourceList // 资源列表
 * remarks // 兼容旧版本
 */
export function getPureMapData({ nodes, status }) {
  const {
    toolbar: { color, fontSize, fontStyle, fontWeight, textDecoration, fontFamily, ...rest },
  } = status;
  return {
    nodes: extractDataFromNodes(nodes),
    readOnly: status.readOnly,
    toolbar: {
      ...rest,
    },
    remarks: mapRemarks(nodes),
    version: '2.0',
  };
}

export const has = (target, key) => Object.prototype.hasOwnProperty.call(target, key);

export function eqSet(as, bs) {
  if (as.size !== bs.size) return false;
  for (let a of as) if (!bs.has(a)) return false;
  return true;
}
