import { Container } from 'unstated';
import { FONT_FAMILY, UNDERLINE, LINE_THROUGH, __DEV__ } from '../common/constants';
import storage from 'common/storage';
import { pcBridge } from '../bridges';
import client from '../common/client';
import { updateCanvasSize } from '../containers/App/dom';

const initialNodes = [
  {
    id: 'root',
    isroot: true,
    topic: '中心主题',
    customStyle: {
      fontFamily: FONT_FAMILY['微软雅黑'],
    },
    link: null,
  },
];
export const getInitNodes = isInital => {
  if (isInital) {
    return initialNodes;
  } else {
    return storage.get('data') || initialNodes;
  }
};

const getLines = () => {
  if (__DEV__) {
    return storage.get('lines') || [];
  }
  return [];
};
const defaultSelectSet = new Set(['root']);
export default class NodeContainer extends Container {
  state = {
    nodes: getInitNodes(),
    lines: getLines(),
    selectedNodes: defaultSelectSet,
  };

  deleteLink({ link }) {
    this.updateNodes(mindmapEditor._deleteLink(link));
  }

  insertNoteLink({ link }) {
    this.updateNodes(mindmapEditor._insertNoteLink({ id: link.id, link }));
  }

  insertLink({ link }) {
    this.updateNodes(mindmapEditor._insertLink(link));
  }

  saveNodeTopic({ id, topic }) {
    this.updateNodes(mindmapEditor._saveNodeTopic(id, topic));
  }

  updateRemark(id, value) {
    const nodesRenderArray = mindmapEditor._updateRemark(id, value);
    this.updateNodes(nodesRenderArray);
  }

  setFontColor({ selectedNodes, color }) {
    this.updateNodes(mindmapEditor._toggleCustomStyle(selectedNodes, color, 'color'));
  }

  setFontSize({ selectedNodes, fontSize }) {
    this.updateNodes(mindmapEditor._toggleCustomStyle(selectedNodes, fontSize, 'fontSize'));
  }

  setFontFamily({ selectedNodes, fontFamily }) {
    const newNodes = mindmapEditor._toggleCustomStyle(
      selectedNodes,
      FONT_FAMILY[fontFamily],
      'fontFamily',
    );
    this.updateNodes(newNodes);
  }
  toggleBold({ selectedNodes, fontWeight }) {
    const newNodes = mindmapEditor._toggleCustomStyle(selectedNodes, fontWeight, 'fontWeight');
    this.updateNodes(newNodes);
  }

  toggleItalic({ selectedNodes, fontStyle }) {
    const newNodes = mindmapEditor._toggleCustomStyle(selectedNodes, fontStyle, 'fontStyle');
    this.updateNodes(newNodes);
  }

  toggleDeleteline({ selectedNodes, textDecoration }) {
    // return handleCommonTextDecoration(state, selectedNodes, textDecoration, LINE_THROUGH);
    const newNodes = mindmapEditor._toggleCustomStyle(
      selectedNodes,
      {
        textDecoration,
        currentLine: LINE_THROUGH,
      },
      'textDecoration',
    );
    this.updateNodes(newNodes);
  }

  toggleUnderline({ selectedNodes, textDecoration }) {
    const newNodes = mindmapEditor._toggleCustomStyle(
      selectedNodes,
      {
        textDecoration,
        currentLine: UNDERLINE,
      },
      'textDecoration',
    );
    this.updateNodes(newNodes);
  }

  _handleSelectNode() {
    const selectedNode = mindmapEditor.getSelectedNode();
    if (!selectedNode) {
      return;
    }
    this.selectNodes(selectedNode.id);
  }
  deleteNodes(selectedNodes) {
    this.updateNodes(mindmapEditor._deleteNode(selectedNodes));
    this._handleSelectNode();
  }

  moveNodes(id, targetId) {
    this.updateNodes(mindmapEditor._moveNodes(id, targetId));
    this.selectNodes(id);
  }

  insertParentNode(selectedNodes) {
    this.updateNodes(mindmapEditor._insertParentNode([...selectedNodes][0]));
    this._handleSelectNode();
  }

  insertChildNode(selectedNodes) {
    this.updateNodes(mindmapEditor._insertChildNode([...selectedNodes][0]));
    this._handleSelectNode();
  }

  insertBrotherNode(selectedNodes) {
    this.updateNodes(mindmapEditor._insertBrotherNode([...selectedNodes][0]));
    this._handleSelectNode();
  }

  pasteMultNodes(selectedNodes, text) {
    this.updateNodes(mindmapEditor._pasteMultNodes([...selectedNodes][0], text));
  }

  toggleNode(id) {
    const [newNodes, newLines] = mindmapEditor._toggleNode(id);
    this.updateNodes(newNodes);
    this.updateLines(newLines);
  }

  // 应该提供 callback 接口由用户负责保存 而不是硬写在逻辑里
  updateNodes(nodes) {
    this.setState({
      nodes,
    });
    __DEV__ && console.log('mindmapEditor.shouldDispatchSave', mindmapEditor.shouldDispatchSave);
    if (mindmapEditor.shouldDispatchSave && client.isPC() && !mindmapEditor.readOnly) {
      pcBridge.OnContentChange();
    }
  }

  updateLines(lines) {
    this.setState({
      lines,
    });
  }

  selectNodes(payload) {
    if (typeof payload === 'string') {
      if (this.state.selectedNodes.has(payload)) return;
      const newSet = new Set();
      this.setState({
        selectedNodes: newSet.add(payload),
      });
      return;
    }
    this.setState({
      selectedNodes: new Set(payload),
    });
  }

  toggleSelectNodes(payload) {
    if (this.state.selectedNodes.has(payload)) {
      this.setState(prevState => {
        const newState = new Set(prevState.selectNodes);
        newState.delete(payload);
        return { selectedNodes: newState };
      });
      return;
    }

    this.setState(prevState => {
      const newState = new Set(prevState.selectedNodes).add(payload);
      return { selectedNodes: newState };
    });
  }

  handleLayoutStrategy(strategy, lineType) {
    mindmapEditor._handleLayoutStrategy(strategy, lineType);
    updateCanvasSize(mindmapEditor.manager.root.ruleArea);
    this.updateNodes(mindmapEditor.manager.nodesRenderArray);
    this.updateLines(mindmapEditor.manager.linesArray);
  }

  selectAllNodes() {
    const nodesArr = Object.keys(mindmapEditor.manager.YMNodes);
    this.setState({
      selectedNodes: new Set(nodesArr),
    });
  }

  clearSelect() {
    this.setState({
      selectedNodes: new Set(),
    });
  }
}
