import emitter from 'common/events';
import client from 'common/client';
import pcController from './pcController';
import { getSize, getHtml, updateScalePadding } from '../containers/App/dom';
import YMDelegate from '../common/ydmind-new/index';
import { __DEV__ } from '../common/constants';

// 标记所有有副作用的事件，即主动修改节点数据
// 用于通知其他端校验是否有操作导致数据变更
const MARK_NODE_EFFECT_COMMANDS = [
  'insertBrotherNode',
  'insertChildNode',
  'insertParentNode',
  'toggleNode',
  'insertLink',
  'insertNoteLink',
  'removeLink',
  'saveRemark',
  'deleteNode',
  'moveNodes',
  'saveNodeTopic',
  'handleUndo',
  'handleRedo',
  'pasteMultNodes',
  'handleFontFamily',
  'handleFontSize',
  'handleLayoutStrategy',
  'handlePaste',
  'toggleDeleteline',
  'toggleBold',
  'toggleItalic',
  'toggleUnderline',
  'handleColor',
];

// 脑图的客户端主动调用的方法，而非用户
const CLIENT_COMMANDS = ['setValue'];

// 以下操作不需要再触发二次 re-render
const NOT_RENDER_COMMANDS = [
  'getValue',
  'handleZoomIn',
  'handleZoomOut',
  'scrollToCenter',
  'arrowUp',
  'arrowDown',
  'arrowRight',
  'arrowLeft',
];

// 标记需要二次更新的数据
const MARK_UPDATE_COMMANDS = [...MARK_NODE_EFFECT_COMMANDS, ...CLIENT_COMMANDS];

const COMMANDS = [...NOT_RENDER_COMMANDS, ...MARK_UPDATE_COMMANDS];

class MindmapEditor extends YMDelegate {
  constructor(props) {
    super(props);
    this.reset();
    this._shortCutKeyMap = {};
    this.shouldDispatchSave = false;
    if (!MindmapEditor.instance) {
      MindmapEditor.instance = this;
    }
    return MindmapEditor.instance;
  }

  reset() {
    this._shiftKeyStartOrder = null;
    this._shiftKeyEndOrder = null;
    this._isEditng = false;
    this._resourceList = [];
    // 标记是否需要重新 parse
    this.markUpdateCoordinate = true;
    this.readOnly = Boolean(!__DEV__);
    this.__uistatus__ = {
      focus: '',
    };
    this.emit('controller:unmount:component');
    this.emit('controller:contextmenu:trigger');
  }

  set isEditing(isEdit) {
    this._isEditng = isEdit;
  }

  get isEditing() {
    return this._isEditng;
  }

  set shortCutKeyMap(map) {
    this._shortCutKeyMap = map;
  }

  get shortCutKeyMap() {
    return this._shortCutKeyMap;
  }

  /**
   *
   * @param {object} link {id,value}
   * @param {bool} isInnerLink 是否为内链
   */
  openLink(link) {
    const isInnerLink = link.isInnerLink;
    // 外部来源后续通过setValue 方法去判断
    if (client.isPC() && !client.fromShare()) {
      pcController.openLink(link.value, isInnerLink);
    } else if (isInnerLink) {
      // 这个消息在云笔记响应
      this.emit('controller:open:innerlink', link.value, link.name);
    } else {
      window.open(link.value, false);
    }
    return this;
  }

  emit(type, ...args) {
    emitter.emit(type, ...args);
    return this;
  }

  on(type, ...args) {
    emitter.on(type, ...args);
    return this;
  }

  removeListener(type, ...args) {
    emitter.removeListener(type, ...args);
    return this;
  }

  export2image(fileName) {
    this.emit('controller:export:image', fileName);
  }

  // 注册 API
  registerAPI(props) {
    Object.entries(props).forEach(([key, func]) => {
      if (COMMANDS.includes(key)) {
        if (MARK_UPDATE_COMMANDS.includes(key)) {
          // 将标记改成 message 来表示由哪次操作引起的数据变更
          this[key] = (...args) => {
            if (MARK_NODE_EFFECT_COMMANDS.includes(key)) {
              this.shouldDispatchSave = key;
            }
            this.markUpdateCoordinate = key;
            func.apply(props, args);
          };
        } else {
          this[key] = func.bind(props);
        }
      }
    });
  }

  getResources() {
    const resourceArr = this.resourceList;
    if (!Array.isArray(resourceArr)) return '';
    return resourceArr.join(';');
  }
}

// DOM API
MindmapEditor.prototype.getPureHtml = getHtml;
MindmapEditor.prototype.updateScalePadding = updateScalePadding;
MindmapEditor.prototype.getSize = getSize;

export default MindmapEditor;
