/* eslint-disable no-param-reassign */
import _ from 'lodash';
import YMNode from './YMNode';
import { uuid } from 'common/constants';

const removeChildrenFromParent = (parentNode, id) =>
  // eslint-disable-next-line implicit-arrow-linebreak
  _.remove(parentNode.children, child => child.id === id);

const replaceChildrenFromParent = (parentNode, id, newNode) => {
  const index = _.findIndex(parentNode.children, node => node.id === id);
  parentNode.children.splice(index, 1, newNode);
};

const createNewNode = ({ type, node }, attr) => {
  let baseIndex = 0;
  let baseNode;
  if (type === 'child') {
    baseIndex = 1;
    baseNode = node;
  } else if (type === 'brother') {
    baseNode = node.parent;
  } else if (type === 'parent') {
    baseNode = node;
  }
  const id = uuid();
  const selfIndex = node.index + baseIndex;
  let topic;
  const getChildCount = () => {
    return baseNode.children ? baseNode.children.length + 1 : 1;
  };

  const setTopic = baseTopic => {
    if (type === 'parent') {
      topic = baseTopic + node.parent.children.length;
    } else {
      topic = baseTopic + getChildCount();
    }
    return topic;
  };
  if (selfIndex === 1) {
    // fontSize = 18;
    setTopic('分支主题');
  } else if (selfIndex === 2) {
    // fontSize = 16;
    setTopic('子主题');
  } else if (selfIndex >= 3) {
    // fontSize = 14;
    setTopic('子主题');
  }
  let customStyle = {};
  if (attr && attr.customStyle) {
    customStyle = attr.customStyle;
  }
  if (attr && attr.topic) {
    topic = attr.topic;
  }
  // const fontFamily = FONT_FAMILY['微软雅黑'];
  // customStyle = Object.assign(customStyle, { fontFamily });
  return {
    id,
    topic,
    customStyle,
  };
};

const deepTraverseChildren = (node, action) => {
  if (!node || !node.children || !node.children.length) return;
  node.children.forEach(child => {
    action(child);
    deepTraverseChildren(child, action);
  });
};

const setVisibility = node => {
  // 子节点可见条件 父节点展开并且父节点可见
  if (
    _.has(node.parent, 'expanded') &&
    node.parent.expanded &&
    node.parent.style.visibility !== 'hidden'
  ) {
    node.style = {
      visibility: 'visible',
    };
    if (node.children && node.children.length) {
      node.expanderStyle = {
        visibility: 'visible',
      };
    }
  } else {
    node.style = { visibility: 'hidden' };
    node.expanderStyle = { visibility: 'hidden' };
  }
};

const handleTextDecoration = ({ textDecoration, currentLine }) => {
  if (!textDecoration) {
    textDecoration = [];
  } else if (!Array.isArray(textDecoration)) {
    textDecoration = textDecoration.split(' ').filter(item => !!item);
  }
  if (!currentLine) {
    return;
  }
  if (textDecoration.includes(currentLine)) {
    _.remove(textDecoration, item => item === currentLine);
  } else {
    if (!Array.isArray(textDecoration)) textDecoration = [textDecoration];
    textDecoration.push(currentLine);
  }
  textDecoration = textDecoration.join(' ');
  return textDecoration;
};

export default class Core {
  constructor() {
    this.selectedNode = null;
    this.waitForRemoveDOM = [];
  }

  deleteNode(idSets) {
    const parentNode = this.findNestedParentNode([...idSets][0]);
    if (idSets.size === 1) {
      this.updateSelectedNode(parentNode);
    }
    this._deleteNestedNode(idSets);
    const updateOrder = () => {
      if (parentNode.id === 'root') {
        parentNode.children.forEach((item, i) => {
          item.order = i;
        });
      }
    };
    updateOrder();
  }

  findNestedParentNodeIdPath(id) {
    let path = [];
    let target = 'root';
    if (!this.YMNodes[id]) return path;
    let parent = this.YMNodes[id].parent;
    while (parent) {
      if (parent.id === target) {
        parent = null;
        if (path.length) {
          return path;
        }
        path = ['root'];
        return path;
      }
      path.push(parent.id);
      parent = this.YMNodes[parent.id].parent;
    }
    return path;
  }

  /**
   * 找到该嵌套节点的 parentNode
   * 比如删除这个节点的时候，需要获取 parent
   * 或者上下移位也需要获取 parent
   * @param {*} id
   * @param {*} type  parent | curr
   * @param {*} nodes
   */
  findNestedParentNode(id) {
    const path = this.findNestedParentNodeIdPath(id);
    if (path.length === 1 && path[0] === 'root') return this.YMNodes.root;
    let node = this.YMNodes.root.children;
    let result = {};
    if (!path.length || !node) return null;
    while (path.length > 0) {
      if (!path.length) {
        return result;
      }
      const childPath = path.pop();
      result = _.find(node, child => child.id === childPath);
      if (result) {
        node = result.children;
      } else {
        return null;
      }
    }
    return result;
  }

  findNestedNodeIdPath(id) {
    let path = [id];
    let target = 'root';
    const node = this.YMNodes[id];
    if (!node) return [];
    let parent = node.parent;
    while (parent) {
      if (parent.id === target) {
        parent = null;
        return path;
      }
      path.push(parent.id);
      parent = this.YMNodes[parent.id].parent;
    }
    return path;
  }

  findNestedNode(id) {
    if (id === 'root') {
      return this.YMNodes.root;
    }
    const path = this.findNestedNodeIdPath(id);
    let node = this.YMNodes.root.children;
    if (!path.length || !node) return null;
    let result = {};
    while (path.length > 0) {
      if (!path.length) {
        return result;
      }
      const childPath = path.pop();
      result = _.find(node, child => child.id === childPath);
      node = result.children;
    }
    return result;
  }

  _deleteNestedNode(idSets) {
    const ids = [...idSets];
    ids.forEach(id => {
      const parentNode = this.findNestedParentNode(id);
      if (!parentNode) return;
      removeChildrenFromParent(parentNode, id);
    });
  }

  /**
   * 无法移动的情况
   * 1. 目标节点是当前节点的孩子节点
   * 2. 目标节点是当前节点的父节点
   * @param {*} id
   * @param {*} targetNodeId
   */
  canMoveNodes(id, targetNodeId) {
    const targetPathArr = this.findNestedNodeIdPath(targetNodeId);
    const idPathArr = this.findNestedNodeIdPath(id);
    const targetNodePathString = targetPathArr.reverse().join(';');
    const idPath = idPathArr.reverse().join(';');

    if (targetNodePathString.includes(idPath)) return false;
    const targetNode = this.YMNodes[targetNodeId];
    const currNode = this.YMNodes[id];
    if (currNode.parent === targetNode) return false;
    return true;
  }

  moveNodes(id, targetNodeId) {
    const parentNode = this.findNestedParentNode(id);
    const node = this.findNestedNode(id);
    const targetNode = this.findNestedNode(targetNodeId);

    const updatePointer = () => {
      // 修改该节点的父节点的引用指向
      node.parent = targetNode;
      // 插入到目标节点的孩子后面
      if (!targetNode.children.length) {
        targetNode.children = [node];
      } else {
        targetNode.children.push(node);
      }
      // 删除该父节点的children
      removeChildrenFromParent(parentNode, id);
    };
    const updateIndex = () => {
      node.index = targetNode.index + 1;
    };
    const updateOrder = () => {
      // 更新选中的父亲节点所有子节点的 order
      if (parentNode.id === 'root') {
        parentNode.children.forEach((item, i) => {
          item.order = i;
          // 更新后的父节点会影响子节点，遍历更新子节点
          deepTraverseChildren(item, _node => {
            _node.order = i;
          });
        });
      }

      // 更新自身节点的 order
      if (targetNode.id === 'root') {
        node.order = targetNode.children.length;
      } else {
        node.order = targetNode.order;
      }

      // 更新自身子节点所有 order
      deepTraverseChildren(node, item => {
        item.order = node.order;
      });
    };

    const updateExpand = () => {
      if (!targetNode.expanded) {
        targetNode.expanded = true;
        // 遍历子节点可见
        deepTraverseChildren(targetNode, setVisibility);
      }
    };
    updatePointer();
    updateIndex();
    updateExpand();
    updateOrder();
    this.updateSelectedNode(targetNode);
  }

  insertChildNode(id, topic) {
    const currentNode = this.findNestedNode(id);
    if (!currentNode) return null;
    const { fontFamily } = currentNode.source.customStyle;
    const customStyle = fontFamily ? { fontFamily } : {};
    // 如果该节点不是展开的，设置展开，并且让所有子节点显示
    const newNode = createNewNode({ type: 'child', node: currentNode }, { topic, customStyle });
    const newYMNode = new YMNode(newNode);

    const updateIndex = () => {
      if (typeof currentNode.index === 'number') {
        newYMNode.index = currentNode.index + 1;
      }
    };
    const updateExpand = () => {
      if (!currentNode.expanded) {
        currentNode.expanded = true;
        // 遍历子节点可见
        deepTraverseChildren(currentNode, setVisibility);
      }
    };
    const updatePointer = () => {
      newYMNode.parent = currentNode;
      if (currentNode.children) {
        currentNode.children.push(newYMNode);
      } else {
        currentNode.children = [newNode];
      }
    };

    const updateOrder = () => {
      if (currentNode.id === 'root') {
        newYMNode.order = currentNode.children.length;
      } else {
        newYMNode.order = currentNode.order;
      }
    };

    updateOrder();
    updateExpand();
    updateIndex();
    updatePointer();
    this.updateSelectedNode(newNode);
    return newNode;
  }

  insertParentNode(id) {
    const currentNode = this.findNestedNode(id);
    if (!currentNode) return null;
    const { fontFamily } = currentNode.source.customStyle;
    const customStyle = fontFamily ? { fontFamily } : {};
    const parentNode = currentNode.parent;
    const newNode = createNewNode({ type: 'parent', node: currentNode }, { customStyle });
    const newYMNode = new YMNode(newNode);
    const updatePointer = () => {
      newYMNode.index = currentNode.index;
      // 父节点的孩子节点替换成新的节点
      replaceChildrenFromParent(parentNode, id, newYMNode);

      // 子节点表头
      newYMNode.parent = parentNode;
      if (newYMNode.children) {
        newYMNode.children.push(currentNode);
      } else {
        newYMNode.children = [currentNode];
      }
      currentNode.parent = newYMNode;
    };

    const updateIndex = () => {
      currentNode.index += 1;
    };

    newYMNode.order = currentNode.order; // update order
    updatePointer();
    updateIndex();
    this.updateSelectedNode(newNode);
  }

  insertBrotherNode(id) {
    const currentParentNode = this.findNestedParentNode(id);
    const currentNode = this.findNestedNode(id);
    if (!currentNode || !currentParentNode) return null;
    const { fontFamily } = currentNode.source.customStyle;
    const customStyle = fontFamily ? { fontFamily } : {};
    const newNode = createNewNode({ type: 'brother', node: currentNode }, { customStyle });
    const newYMNode = new YMNode(newNode);

    const updateIndex = () => {
      newYMNode.index = currentParentNode.index + 1;
    };
    const updatePointer = () => {
      // 子节点表头
      const currentNodeIndex = _.findIndex(currentParentNode.children, child => child.id === id);
      // 父节点表尾
      currentParentNode.children.splice(currentNodeIndex + 1, 0, newYMNode);
      newYMNode.parent = currentParentNode;
    };

    const updateOrder = () => {
      // 更新选中的父亲节点所有子节点的 order
      if (currentParentNode.id === 'root') {
        currentParentNode.children.forEach((item, i) => {
          item.order = i;
          // 更新后的父节点会影响子节点，遍历更新子节点
          deepTraverseChildren(item, _node => {
            _node.order = i;
          });
        });
      } else {
        newYMNode.order = currentNode.order;
      }
    };

    updateIndex();
    updatePointer();
    updateOrder();
    // this.insertDOM(newYMNode);
    this.updateSelectedNode(newNode);
  }

  saveNodeTopic(id, topic) {
    const node = this.findNestedNode(id);
    node.source = {
      ...node.source,
      topic,
    };
  }

  // 设置该节点的子节点是否可见
  toggleNode(id) {
    const currentNode = this.findNestedNode(id);
    currentNode.expanded = !currentNode.expanded;
    // 如果当前节点未展开，遍历设置子节点是隐藏状态
    deepTraverseChildren(currentNode, setVisibility);
    return currentNode;
  }

  updateSelectedNode(selectedNode) {
    this.selectedNode = selectedNode;
  }

  pasteMultNodes(id, text) {
    const currentNode = this.findNestedNode(id);
    const { fontFamily } = currentNode.source.customStyle;
    const customStyle = fontFamily ? { fontFamily } : {};

    _.compact(text.split('\n')).forEach(topic => {
      const newNode = createNewNode({ type: 'child', node: currentNode }, { topic, customStyle });
      const newYMNode = new YMNode(newNode);

      const updateIndex = () => {
        if (typeof currentNode.index === 'number') {
          newYMNode.index = currentNode.index + 1;
        }
      };
      const updateExpand = () => {
        if (!currentNode.expanded) {
          currentNode.expanded = true;
          // 遍历子节点可见
          deepTraverseChildren(currentNode, setVisibility);
        }
      };
      const updatePointer = () => {
        newYMNode.parent = currentNode;
        if (currentNode.children) {
          currentNode.children.push(newYMNode);
        } else {
          currentNode.children = [newNode];
        }
      };
      const updateOrder = () => {
        if (currentNode.id === 'root') {
          newYMNode.order = currentNode.children.length;
        } else {
          newYMNode.order = currentNode.order;
        }
      };
      updateExpand();
      updateIndex();
      updatePointer();
      updateOrder();
    });
  }

  insertLink(link) {
    const currentNode = this.findNestedNode(link.id);
    if (!currentNode) return;
    currentNode.source = {
      ...currentNode.source,
      link,
    };
  }

  deleteLink(deleteLink) {
    const currentNode = this.findNestedNode(deleteLink.id);
    if (!currentNode) return;
    // eslint-disable-next-line no-redeclare
    const { link, ...rest } = currentNode.source;
    currentNode.source = {
      ...rest,
    };
    // delete currentNode.source.link;
  }

  insertNoteLink({ link, id }) {
    const currentNode = this.findNestedNode(id);
    if (!currentNode) return;
    const newNode = this.insertChildNode(currentNode.id, link.name);
    newNode.link = link;
  }

  toggleCustomStyle(id, data, type) {
    const currentNode = this.findNestedNode(id);
    if (!currentNode) return;
    let newCustomStyle;
    if (type !== 'textDecoration') {
      newCustomStyle = { ...currentNode.source.customStyle, [`${type}`]: data };
    } else {
      const textDecoration = handleTextDecoration({
        textDecoration: currentNode.source.customStyle.textDecoration,
        currentLine: data.currentLine,
      });
      newCustomStyle = { ...currentNode.source.customStyle, [`${type}`]: textDecoration };
    }

    currentNode.source = {
      ...currentNode.source,
      customStyle: newCustomStyle,
    };

    // currentNode.source.customStyle = newCustomStyle;
  }

  updateRemark(id, remark) {
    const currentNode = this.findNestedNode(id);
    if (!currentNode) return;
    if (!remark) {
      this.deleteRemark(id);
    } else {
      currentNode.source = {
        ...currentNode.source,
        remark,
      };
    }
  }

  deleteRemark(id) {
    const currentNode = this.findNestedNode(id);
    if (!currentNode) return;

    // eslint-disable-next-line no-redeclare
    const { remark, ...rest } = currentNode.source;
    currentNode.source = {
      ...rest,
    };
  }

  // insertDOM(node) {
  //   this.waitForRemoveDOM.push(node);
  //   const dom = document.createElement('div');
  //   dom.id = node.id;
  //   const {
  //     customStyle: { fontSize, fontFamily },
  //     topic,
  //   } = node.source;
  //   const content = document.createElement('div');
  //   content.classList.add('content');
  //   content.innerText = escapeHtml(topic);
  //   dom.appendChild(content);
  //   dom.classList.add('node');
  //   Object.assign(dom.style, {
  //     fontSize: parseInt(fontSize) + 'px',
  //     fontFamily,
  //   });
  //   document.getElementById('map-nodes').appendChild(dom);
  //   if (node.index === 1) {
  //     dom.classList.add('first-child');
  //   }
  // }
}
