import React, { useLayoutEffect } from 'react';
import PropTypes from 'prop-types';
import { Tooltip } from '../../../components';
import { makeStyles } from '@material-ui/styles';
import styled, { css } from 'styled-components';

const BaseIcon = styled.span`
  width: 18px;
  height: 18px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  ${props =>
    props.disabled &&
    css`
      color: rgb(82, 92, 111);
      opacity: 0.4;
    `}
`;

const styles = {
  root: {
    position: 'fixed',
    top: '70px',
    left: '20px',
    display: 'flex',
    zIndex: '1000',
    background: '#fff',
  },
  control: {
    width: '87px',
    height: '28px',
    display: 'flex',
    justifyContent: 'space-evenly',
    alignItems: 'center',
    boxShadow: '0px 2px 8px 0px rgba(114, 153, 213, 0.23)',
  },
  rate: {
    fontSize: '12px',
    color: '#666666',
    width: '40px',
    textAlign: 'center',
  },
};

const useStyles = makeStyles(styles);

function FixedIcon(props) {
  const classes = useStyles(props);
  useLayoutEffect(() => {
    mindmapEditor.updateScalePadding(props.zoom);
  }, [props.zoom]);
  return (
    <div className={classes.root} id="zoom-area">
      <div className={classes.control}>
        <Tooltip title="缩小">
          <BaseIcon
            disabled={props.zoom <= 0.5}
            onClick={props.zoom <= 0.5 ? null : mindmapEditor.handleZoomOut}
          >
            -
          </BaseIcon>
        </Tooltip>
        <span className={classes.rate}>{`${props.zoom * 100}%`}</span>
        <Tooltip title="放大">
          <BaseIcon
            disabled={props.zoom >= 3}
            onClick={props.zoom >= 3 ? null : mindmapEditor.handleZoomIn}
          >
            +
          </BaseIcon>
        </Tooltip>
      </div>
    </div>
  );
}

export default React.memo(FixedIcon);
FixedIcon.propTypes = {
  zoom: PropTypes.number,
};
FixedIcon.defaultProps = {
  zoom: 1,
};
