// eslint-disable-next-line import/prefer-default-export
export const getXmindData = root => {
  const { children, id, source } = root;
  const { isroot, parentid, style, expanderStyle, customStyle, index, expanded, ...rest } = source;
  let tempChild = [];
  if (children.length) {
    children.forEach(node => {
      tempChild.push(getXmindData(node));
    });
  }
  return {
    children: tempChild,
    id,
    ...rest,
  };
};
