import * as fl from './firstLayerbase';
import { hs_lr, rhs_lr, vs_lr, hs_tb, vs_tb, rvs_tb, arcRadius, vs_tb_base } from '../../constant';

// 左右布局

export const setXRight = node => {
  node.childrenArea.start.x =
    node.index > 1 ? node.selfArea.width + hs_lr : node.selfArea.width + rhs_lr;
};

export const setXLeft = node => {
  node.childrenArea.start.x =
    node.index > 1
      ? -1 * (node.childrenArea.width + hs_lr)
      : -1 * (node.childrenArea.width + rhs_lr);
};

// 简洁的布局方式： 所有节点都是垂直居中的
const renderSimplePosition = node => {
  fl.setFirstLayerCenter2ParentCenterHorizon(node, 'center');
};

const renderDefaultPosition = node => {
  if (node.index === 0) {
    fl.setFirstLayerCenter2ParentCenterHorizon(node, 'center');
  } else if (node.index === 1) {
    node.children.length > 1
      ? fl.setFirstLayerCenter2ParentCenterHorizon(node)
      : fl.setFirstLayerBottom2ParentCenter(node);
  } else {
    node.children.length > 1
      ? fl.setFirstLayerCenter2ParentBottom(node)
      : fl.setFirstLayerBottom2ParentBottom(node);
  }
};
export const setYCenter = node => {
  const lineType = mindmapEditor.manager.lineType;
  if (lineType === 'simple') {
    renderSimplePosition(node);
    return;
  }
  renderDefaultPosition(node);
};

export const setRootYForCenterLR = root => {
  root.childrenArea.start.y = 0 - root.childrenArea.partArea.baseLine + root.selfArea.height / 2;
};

export const setRootXForCenterLR = root => {
  root.childrenArea.start.x = 0 - root.childrenArea.partArea.left.width - rhs_lr;
};

// 上下布局

export const setYBottom = node => {
  node.childrenArea.start.y =
    node.index > 1 ? node.selfArea.height + vs_tb : node.selfArea.height + rvs_tb;
};

export const setYTop = node => {
  node.childrenArea.start.y =
    node.index > 1
      ? -1 * (node.childrenArea.height + vs_tb)
      : -1 * (node.childrenArea.height + rvs_tb);
};

export const setXCenter = node => {
  fl.setFirstLayerCenter2ParentCenter_Virtical(node);
};

export const setXHS = node => {
  node.childrenArea.start.x = hs_tb + arcRadius;
};

export const setYVS = node => {
  node.childrenArea.start.y = node.isRoot()
    ? node.selfArea.height + rvs_tb
    : node.selfArea.height + vs_tb_base;
};
