import { Container } from 'unstated';
import { remove, findKey, isEqual } from 'lodash';
import { FONT_FAMILY } from 'common/constants';
import { getFontStyle } from '../theme/getTheme';
import { __DEV__ } from '../common/constants';

const defaultTextStyle = {
  textDecoration: [],
  fontWeight: 'normal',
  fontStyle: 'normal',
};

const initialState = {
  color: 'black',
  fontSize: '24',
  fontFamily: '微软雅黑',
  ...defaultTextStyle,
};

export default class ToolbarContainer extends Container {
  constructor(props) {
    super(props);
    this.handleReadOnly = this.handleReadOnly.bind(this);
  }
  state = {
    toolbar: { ...initialState, lineType: 'default', strategy: 'logic_right', zoom: 1 },
    readOnly: false,
    loading: true,
  };

  toggleUnderline() {
    let state = this.state.toolbar;

    let textDecoration = [...state.textDecoration] || [];
    if (textDecoration.includes('underline')) {
      remove(textDecoration, item => item === 'underline');
    } else {
      if (!Array.isArray(textDecoration)) {
        textDecoration = [textDecoration];
      }
      textDecoration.push('underline');
    }

    this.setState({
      toolbar: { ...state, textDecoration },
    });
  }

  toggleDeleteline() {
    let state = this.state.toolbar;
    let textDecoration = [...state.textDecoration] || [];
    if (textDecoration.includes('line-through')) {
      remove(textDecoration, item => item === 'line-through');
    } else {
      if (!Array.isArray(textDecoration)) {
        textDecoration = [textDecoration];
      }
      textDecoration.push('line-through');
    }
    this.setState({
      toolbar: { ...state, textDecoration },
    });
  }

  handleLayoutStrategy(strategy, lineType) {
    let state = this.state.toolbar;
    this.setState({
      toolbar: {
        ...state,
        strategy,
        lineType,
      },
    });
  }

  setDefaultTextStyle() {
    let state = this.state.toolbar;
    this.setState({
      toolbar: {
        ...state,
        ...defaultTextStyle,
      },
    });
  }

  updateToolbarItem(payload) {
    __DEV__ && console.log('updateToolbarItem', payload);
    this.setState(prevState => ({
      toolbar: {
        ...prevState.toolbar,
        ...payload,
      },
    }));
  }

  // 传入一个 node 的 style， 需要进行特殊处理
  updateNodeToolbar(node) {
    __DEV__ && console.log('updateNodeToolbar', node);

    const lineType = this.state.toolbar.lineType;
    const payload = { ...getFontStyle(node.index, lineType), ...node.customStyle };
    let { fontFamily: fontarr, textDecoration, ...rest } = payload;
    if (!textDecoration) {
      textDecoration = [];
    } else if (!Array.isArray(textDecoration)) {
      textDecoration = textDecoration.split(' ').filter(item => !!item);
    }
    const fontFamily =
      findKey(FONT_FAMILY, font => isEqual(font, fontarr)) || initialState.fontFamily;

    this.setState(prevState => ({
      toolbar: {
        ...prevState.toolbar,
        ...defaultTextStyle,
        ...rest,
        textDecoration,
        fontFamily,
      },
    }));
  }

  handleReadOnly(readOnly) {
    if (readOnly === undefined) {
      this.setState(prevState => ({
        readOnly: !prevState.readOnly,
      }));
      return;
    }
    this.setState({
      readOnly,
    });
  }

  setLoading(loading) {
    this.setState({
      loading,
    });
  }
}
