import React from 'react';
import PropTypes from 'prop-types';
import client from '../../common/client';
import MoveableCanvas from './MoveableCanvas';
import CoreCanvas from './CoreCanvas';
import MindContextMenu from './MindContextMenu';
import FixedIcon from './FixedIcon';
import { ThemeProvider } from '@material-ui/styles';
import { Subscribe } from 'unstated';
import NodeContainer from '../../model/NodeContainer';
import ToolbarContainer from '../../model/ToolbarContainer';
import { WEBCSSBase, PCCSSBase } from './CSSBase';
import dynamic from 'next/dynamic';
import PCEventListener from './PCEventListener';

const ToolbarComponent = dynamic(() => import('../ToolbarContainer'));

// const NodesCount = React.memo(({ count }) => (
//   <div style={{ position: 'fixed', left: 50, bottom: 50 }}>主题数{count}</div>
// ));
// NodesCount.displayName = NodesCount;
// NodesCount.propTypes = {
//   count: PropTypes.number,
// };

// const ToolbarComponent = React.lazy(() => import(/* webpackChunkName: "toolbar" */ '../ToolbarContainer'));

function App() {
  const renderCSSBase = () => (client.isPC() ? <PCCSSBase /> : <WEBCSSBase />);
  return (
    <Subscribe to={[NodeContainer, ToolbarContainer]}>
      {(nodeContainer, toolbarContainer) => (
        <ThemeProvider theme={toolbarContainer.state.toolbar.lineType}>
          <MoveableCanvas nodeContainer={nodeContainer} toolbarContainer={toolbarContainer}>
            <CoreCanvas nodeContainer={nodeContainer} toolbarContainer={toolbarContainer} />
          </MoveableCanvas>
          {!mindmapEditor.readOnly && (
            <React.Fragment>
              <ToolbarComponent toolbarContainer={toolbarContainer} nodeContainer={nodeContainer} />
              <FixedIcon zoom={toolbarContainer.state.toolbar.zoom} />
              <MindContextMenu
                selectedNodes={nodeContainer.state.selectedNodes}
                mindmapEditor={mindmapEditor}
              />
              {/* <NodesCount count={nodeContainer.state.nodes.length} /> */}
            </React.Fragment>
          )}
          {renderCSSBase()}
          {client.isPC() && <PCEventListener toolbarContainer={toolbarContainer} />}
        </ThemeProvider>
      )}
    </Subscribe>
  );
}

export default React.memo(App);
