import '@babel/polyfill';
import React from 'react';
import { Provider, Subscribe } from 'unstated';
// import { render } from 'react-dom';
import App from './containers/App/App';
import WrapperIndex from './containers/App/WrapperIndex';
import { __DEV__ } from 'common/constants';
import detectUpdate from './common/updater';
import NodeContainer from './model/NodeContainer';
import ToolbarContainer from './model/ToolbarContainer';
import { isIE } from './common/constants';

if (__DEV__ && !isIE) {
  import('unstated-debug').then(UNSTATED => {
    UNSTATED.default.logStateChanges = true;
    UNSTATED.default.isEnabled = true;
  });
}

detectUpdate();

// const rootElement = document.getElementById('mindmap');
const options = {
  // container: 'mindmap-container',
};
const Mindmap = () => (
  <Provider>
    <Subscribe to={[NodeContainer, ToolbarContainer]}>
      {(nodeContainer, toolbarContainer) => (
        <WrapperIndex
          nodeContainer={nodeContainer}
          toolbarContainer={toolbarContainer}
          options={options}
        >
          <App />
        </WrapperIndex>
      )}
    </Subscribe>
  </Provider>
);
// Mindmap.propTypes = {
//   options: PropTypes.object,
// };

export default Mindmap;
