// 左右布局水平间距基准值
const hs_lr_base = 52;
// 左右布局非根节点之间的水平间距
const hs_lr = hs_lr_base * 1.1;
// 左右布局根节点与一级节点的水平间距
const rhs_lr = 70;
// 左右布局垂直间距
const vs_lr = 30;

// 上下布局垂直间距基准值
const vs_tb_base = 18;
// 上下布局非根节点之间的垂直间距
const vs_tb = vs_tb_base * 2;
// 上下布局根节点与一级节点的垂直间距
const rvs_tb = vs_tb_base * 2.618;
// 上下布局水平间距
const hs_tb = 20;

const expanderDiameter = 15;
const arcRadius = 8;
export const baseExpandGap = 7;
// 从节点到展开节点的距离 用于绘制连线
const nodeFromExpandSize = baseExpandGap + expanderDiameter / 2; // 距离加圆半径，下一个连接线条的绘制起点
const expandPostion = baseExpandGap + expanderDiameter;
const LineTypeMap = {
  default: {
    line: 'Bezier', // 曲线类型
    shouldLineThrough: true, // 线条是否穿过节点
  },
  simple: {
    line: 'Bezier', // 曲线类型
    shouldLineThrough: false, // 线条是否穿过节点
    main: {
      selected: {
        boxShadow: '0 0 0 2px #, 0 0 0 4px #4C94FF',
      },
    },
    second: {
      selected: {
        border: '2px solid #4C94FF',
        borderRadius: '8px',
      },
    },
    default: {
      selected: {
        border: '2px solid #4C94FF',
        borderRadius: '8px',
      },
    },
  },
};
export {
  // 左右布局
  hs_lr_base,
  hs_lr,
  rhs_lr,
  vs_lr,
  // 上下布局
  vs_tb_base,
  vs_tb,
  rvs_tb,
  hs_tb,
  // 连线圆角半径
  arcRadius,
  // toggle直径
  expandPostion,
  expanderDiameter,
  LineTypeMap,
  nodeFromExpandSize,
};
