const isNode = typeof window === 'undefined';
export default function detectUpdate() {
  if (isNode) {
    return;
  }
  const appCache = window.applicationCache;
  if (!appCache) {
    return;
  }

  bindEvents();

  function bindEvents() {
    appCache.addEventListener('cached', noNeedReload);
    appCache.addEventListener('error', noNeedReload);
    appCache.addEventListener('obsolete', noNeedReload);
    appCache.addEventListener('noupdate', noNeedReload);
    appCache.addEventListener('updateready', needReload);

    function needReload() {
      removeEvents();
      if (appCache.status == appCache.UPDATEREADY) {
        // Browser downloaded a new app cache.
        // Swap it in and reload the page to get the new hotness.
        appCache.swapCache();
        location.reload();
      }
    }

    function noNeedReload() {
      removeEvents();
    }

    function removeEvents() {
      appCache.removeEventListener('cached', noNeedReload);
      appCache.removeEventListener('error', noNeedReload);
      appCache.removeEventListener('noupdate', noNeedReload);
      appCache.removeEventListener('obsolete', noNeedReload);
      appCache.removeEventListener('updateready', needReload);
    }
  }
}
