import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';

export default class NodeGlobalHover extends PureComponent {
  constructor(props) {
    super(props);
    this.dynamicComponent = null;
    this.triggerElement = null;
    this.remarkRef = React.createRef();
  }

  componentDidMount() {
    mindmapEditor.on('controller:render:component', this.handleComponentRender);
    mindmapEditor.on('controller:unmount:component', this.handleComponentUnmount);
  }

  componentWillUnmount() {
    mindmapEditor.removeListener('controller:render:component', this.handleComponentRender);
    mindmapEditor.removeListener('controller:unmount:component', this.handleComponentUnmount);
  }

  createReactElementInDom = component => {
    this.dynamicComponent = component;
    this.container = document.createElement('div');
    document.body.append(this.container);
    ReactDOM.render(this.dynamicComponent, this.container);
  };

  handleComponentRender = ({ component, triggerElement }) => {
    if (mindmapEditor.__uistatus__.focus) return;
    // 如果后续传递的组件和当前的组件，或者挂载的DOM元素不一样 那么先卸载，后新建
    if (
      (triggerElement !== null && triggerElement !== this.triggerElement) ||
      (this.dynamicComponent !== null && this.dynamicComponent !== component)
    ) {
      this.handleComponentUnmount();
    }
    this.triggerElement = triggerElement;
    this.createReactElementInDom(component);
  };

  handleComponentUnmount = () => {
    if (this.container) {
      ReactDOM.unmountComponentAtNode(this.container);
      this.container.remove();
      this.dynamicComponent = null;
      this.triggerElement = null;
      mindmapEditor.__uistatus__.focus = '';
    }
  };

  render() {
    return this.props.children;
  }
}
NodeGlobalHover.propTypes = {
  children: PropTypes.object,
};
