import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { MenuItem } from 'components/ContextMenu';
import { pcController } from 'controllers';
import { Popper, ClickAwayListener } from 'components';
import client from 'common/client';
import setType from 'es6-set-proptypes';
import { __DEV__ } from '../../../common/constants';
import { withStyles } from '@material-ui/styles';

const CONTEXT_MENU_DEL = '删除';
const CONTEXT_MENU_INS_CHILD = '插入下级主题';
const CONTEXT_MENU_INS_BROTHER = '插入同级主题';
const CONTEXT_MENU_INS_PARENT = '插入上级主题';
const CONTEXT_MENU_INS_INNERLINK = '粘贴引用链接';
const CONTEXT_MENU_LISTS = [
  CONTEXT_MENU_DEL,
  CONTEXT_MENU_INS_CHILD,
  CONTEXT_MENU_INS_BROTHER,
  CONTEXT_MENU_INS_PARENT,
];

/**
 * 用户判断何种情况下右键菜单中的功能无法使用
 * @param {string} item 右键功能Action
 * @param {Set} selectedNodes 已经选中的节点
 */
function disabledHandle(item, selectedNodes) {
  switch (item) {
    case CONTEXT_MENU_INS_BROTHER:
    case CONTEXT_MENU_INS_PARENT:
      return selectedNodes.has('root') || selectedNodes.size > 1;
    case CONTEXT_MENU_DEL:
      return selectedNodes.has('root');
    case CONTEXT_MENU_INS_CHILD:
      return selectedNodes.size > 1;
    default:
      return false;
  }
}

class MindContextMenu extends PureComponent {
  constructor(props) {
    super(props);
    if (client.isPC()) {
      pcController.on('controller:paste:link', this.handlePastLinkSuccess);
    } else {
      mindmapEditor.on('controller:paste:link', this.handlePastLinkSuccess);
    }
    mindmapEditor.on('controller:contextmenu:trigger', this.handleOpen);
    this.state = {
      menuList: CONTEXT_MENU_LISTS,
      open: false,
    };
    this.menuActions = {
      [`${CONTEXT_MENU_DEL}`]: mindmapEditor.deleteNode,
      [`${CONTEXT_MENU_INS_BROTHER}`]: mindmapEditor.insertBrotherNode,
      [`${CONTEXT_MENU_INS_CHILD}`]: mindmapEditor.insertChildNode,
      [`${CONTEXT_MENU_INS_PARENT}`]: mindmapEditor.insertParentNode,
      [`${CONTEXT_MENU_INS_INNERLINK}`]: () => {},
    };
  }

  componentWillUnmount() {
    if (client.isPC()) {
      pcController.removeListener('controller:paste:link', this.handlePastLinkSuccess);
    } else {
      mindmapEditor.removeListener('controller:paste:link', this.handlePastLinkSuccess);
    }
    mindmapEditor.removeListener('controller:contextmenu:trigger', this.handleOpen);
  }

  handleOpen = (id = null) => {
    this.setState({
      open: Boolean(id),
      anchorEl: document.getElementById(id),
    });
  };

  /**
   * 针对粘贴引用链接的情况，将对应的方法注入到菜单中
   * 如果是 pc 调用 pc 的粘贴方法
   * value: 笔记 id
   * name: 笔记 标题
   */
  handlePastLinkSuccess = (id, name) => {
    // eslint-disable-next-line no-unused-expressions
    __DEV__ && console.log('handlePasteLinkSuccess', id, name);
    if (!id) {
      this.setState(prevState => {
        let newShdadowMenu = [...prevState.menuList];
        const index = newShdadowMenu.indexOf(CONTEXT_MENU_INS_INNERLINK);
        if (index > -1) {
          newShdadowMenu.splice(index, 1);
        }
        return {
          menuList: newShdadowMenu,
        };
      });
      return;
    }
    if (!this.state.menuList.includes(CONTEXT_MENU_INS_INNERLINK)) {
      this.setState(prevState => {
        // 插入引用链接显示在删除下方
        const shdadowMenu = [...prevState.menuList];
        shdadowMenu.splice(1, 0, CONTEXT_MENU_INS_INNERLINK);
        return {
          menuList: shdadowMenu,
        };
      });
    }

    let pasteFun = () => {};
    if (client.isPC()) {
      pasteFun = () => pcController.pasteNoteLink();
    } else {
      pasteFun = () => {
        // 拿到具体的 id 和 name ，可以做内部的粘贴工作了
        mindmapEditor.insertNoteLink(id, name);
      };
    }
    this.menuActions[`${CONTEXT_MENU_INS_INNERLINK}`] = pasteFun;
  };

  handleClick = (_, { action }) => {
    this.handleClose();
    this.menuActions[action]();
  };

  handleClose = () => {
    this.setState({
      open: false,
    });
  };

  render() {
    const { selectedNodes, classes } = this.props;
    const { open, anchorEl } = this.state;

    return (
      <Popper open={open} anchorEl={anchorEl}>
        <ClickAwayListener onClickAway={this.handleClose}>
          <div className={classes.root}>
            {this.state.menuList.map(item => {
              const disabled = disabledHandle(item, selectedNodes);
              const contextMenuAttributes = {
                className: disabled
                  ? [classes.menuItem, classes.disabled].join(' ')
                  : classes.menuItem,
              };
              return (
                <MenuItem
                  key={item}
                  onClick={this.handleClick}
                  data={{ action: item }}
                  attributes={contextMenuAttributes}
                  disabled={disabled}
                >
                  {item}
                </MenuItem>
              );
            })}
          </div>
        </ClickAwayListener>
      </Popper>
    );
  }
}

const styles = {
  root: {
    width: '150px',
    background: '#fff',
    borderRadius: '0.2em',
    boxShadow: '0px 4px 20px 0px rgba(0, 0, 0, 0.2)',
    cursor: 'pointer',
    color: '#333333',
    fontSize: '12px',
  },
  menuItem: {
    lineHeight: '2.5',
    textIndent: '1.67em',
    '&:hover': {
      background: 'rgba(242,243,247,1)',
    },
  },
  disabled: {
    color: '#b8b8b8',
    background: '#fff',
  },
};
export default withStyles(styles)(MindContextMenu);
MindContextMenu.propTypes = {
  selectedNodes: setType,
  classes: PropTypes.object,
};
