const getBaseX = (node, baseType) => {
  switch (baseType) {
    case 'left':
      return node.ruleArea.start.x + node.selfArea.start.x;
    case 'center':
      return node.ruleArea.start.x + node.selfArea.start.x + node.selfArea.width / 2;
    case 'right':
      return node.ruleArea.start.x + node.selfArea.end.x;
  }
};

const getBaseY = (node, baseType) => {
  switch (baseType) {
    case 'top':
      return node.ruleArea.start.y + node.selfArea.start.y;
    case 'center':
      return node.ruleArea.start.y + node.selfArea.start.y + node.selfArea.height / 2;
    case 'bottom':
      return node.ruleArea.start.y + node.selfArea.end.y;
  }
};

export const getFirstLayerTop = (node, baseType) => {
  const firstChild = node.children[0];
  return getBaseY(firstChild, baseType);
};

export const getFirstLayerCenterY = (node, baseType) => {
  const firstChild = node.children[0];
  const lastChild = node.children[node.children.length - 1];
  return (getBaseY(firstChild, baseType) + getBaseY(lastChild, baseType)) / 2;
};

export const getFirstLayerBottom = (node, baseType) => {
  const lastChild = node.children[node.children.length - 1];
  return getBaseY(lastChild, baseType);
};

export const getFirstLayerLeft = (node, baseType) => {
  const firstChild = node.children[0];
  return getBaseX(firstChild, baseType);
};

export const getFirstLayerCenterX = (node, baseType) => {
  const firstChild = node.children[0];
  const lastChild = node.children[node.children.length - 1];
  return (getBaseX(firstChild, baseType) + getBaseX(lastChild, baseType)) / 2;
};

export const getFirstLayerRight = (node, baseType) => {
  const lastChild = node.children[node.children.length - 1];
  return getBaseX(lastChild, baseType);
};
