import _ from 'lodash';
import { expanderDiameter as ed, expandPostion } from '../constant';

function getProcessedNum(YMNode, pos) {
  const prop = pos === 'top' || pos === 'bottom' ? 'height' : 'width';
  const n = YMNode.selfArea[prop] / 2 - ed / 2;
  return YMNode.index > 1 ? n : n - 2;
}

function putRight(lineType, YMNode) {
  delete YMNode._expanderStyle.left;
  delete YMNode._expanderStyle.top;
  YMNode._expanderStyle.right = -1 * expandPostion;
  if (lineType === 'default' && YMNode.index > 1) {
    // 基线为节点底线 - 自身半径
    YMNode._expanderStyle.bottom = 0 - ed / 2;
    return;
  }
  if (lineType === 'simple') {
    YMNode._expanderStyle.top = getProcessedNum(YMNode, 'top');
    return;
  }
  YMNode._expanderStyle.top = getProcessedNum(YMNode, 'top');
}

function putLeft(lineType, YMNode) {
  delete YMNode._expanderStyle.right;
  YMNode._expanderStyle.left = -1 * expandPostion;
  if (lineType === 'default' && YMNode.index > 1) {
    YMNode._expanderStyle.top = YMNode.selfArea.height - ed / 2;
    return;
  }
  if (lineType === 'simple') {
    YMNode._expanderStyle.top = getProcessedNum(YMNode, 'top');
    return;
  }
  YMNode._expanderStyle.top = getProcessedNum(YMNode, 'top');
}

function putBottom(YMNode) {
  delete YMNode._expanderStyle.top;
  delete YMNode._expanderStyle.right;
  YMNode._expanderStyle.bottom = -1 * (ed + 1);
  YMNode._expanderStyle.left =
    YMNode.index > 1 ? (YMNode.selfArea.width - ed) / 2 : (YMNode.selfArea.width - ed - 4) / 2;
}

function putTop(YMNode) {
  delete YMNode._expanderStyle.top;
  delete YMNode._expanderStyle.right;
  YMNode._expanderStyle.top = -1 * (ed + 1);
  YMNode._expanderStyle.left =
    YMNode.index > 1 ? (YMNode.selfArea.width - ed) / 2 : (YMNode.selfArea.width - ed - 4) / 2;
}

function logic_right(manager) {
  const { root, lineType } = manager;
  root.children.forEach(n => {
    manager._doAll(n, YMNode => putRight(lineType, YMNode));
  });
}

function logic_left(manager) {
  const { root, lineType } = manager;
  root.children.forEach(n => {
    manager._doAll(n, YMNode => putLeft(lineType, YMNode));
  });
}

function logic_left_right(manager) {
  const { root, lineType } = manager;
  if (!_.isEmpty(root.childrenArea.partArea.left)) {
    root.childrenArea.partArea.left.YMNodesArray.forEach(n => {
      manager._doAll(n, YMNode => putLeft(lineType, YMNode));
    });
  }

  if (!_.isEmpty(root.childrenArea.partArea.right)) {
    root.childrenArea.partArea.right.YMNodesArray.forEach(n => {
      manager._doAll(n, YMNode => putRight(lineType, YMNode));
    });
  }
}

function logic_bottom(manager) {
  const { root } = manager;
  root.children.forEach(n => {
    manager._doAll(n, YMNode => putBottom(YMNode));
  });
}

function logic_bottom_catalog(manager) {
  const { root } = manager;
  root.children.forEach(n => {
    manager._doAll(n, YMNode => {
      YMNode.index > 1 ? putLeft(YMNode) : putTop(YMNode);
    });
  });
}

function logic_top(manager) {
  const { root } = manager;
  root.children.forEach(n => {
    manager._doAll(n, YMNode => putTop(YMNode));
  });
}

function logic_top_bottom(manager) {
  const { root } = manager;
  if (!_.isEmpty(root.childrenArea.partArea.top)) {
    root.childrenArea.partArea.top.YMNodesArray.forEach(n => {
      manager._doAll(n, YMNode => putTop(YMNode));
    });
  }

  if (!_.isEmpty(root.childrenArea.partArea.bottom)) {
    root.childrenArea.partArea.bottom.YMNodesArray.forEach(n => {
      manager._doAll(n, YMNode => putBottom(YMNode));
    });
  }
}

// const fish_bone_right = logic_right

const expanderLS = {
  logic_right,
  logic_left,
  logic_left_right,
  logic_bottom,
  logic_bottom_catalog,
  logic_top,
  logic_top_bottom,
  // fish_bone_right,
};

export default expanderLS;
